					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.jump_url" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.page_param" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
