<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block" v-if="searchType==1">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
					<div class="block" v-else-if="searchType==0">
						<span class="demonstration"></span>
						<el-date-picker v-model="formOne.month" type="month" placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item style="margin-left: 100px;">
					<span></span>
					<el-select style="width: 150px;" v-model="searchType">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="0"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="time" label=""></el-table-column>
				<el-table-column prop="totalrandcall" label=""></el-table-column>
				<el-table-column prop="totalrandtime" label=""></el-table-column>
				<el-table-column prop="totalrandmoney" label=""></el-table-column>
				<el-table-column prop="totalfriendcall" label=""></el-table-column>
				<el-table-column prop="totalfriendtime" label="" sortable></el-table-column>
				<el-table-column prop="totalfriendmoney" label=""></el-table-column>
				<el-table-column prop="totalredcall" label=""></el-table-column>
				<el-table-column prop="totalredtime" label=""></el-table-column>
				<el-table-column prop="totalredmoney" label=""></el-table-column>
				<el-table-column prop="totallistencall" label=""></el-table-column>
				<el-table-column prop="totallistentime" label=""></el-table-column>
				<el-table-column prop="totallistenmoney" label=""></el-table-column>
				<el-table-column prop="totalcall" label=""></el-table-column>
				<el-table-column prop="totaltime" label=""></el-table-column>
				<el-table-column prop="totalmoney" label=""></el-table-column>
			</el-table>
			<!--  -->
			<!-- <el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" :page-size="20" :total=1000 style="float:right; ">
				</el-pagination>
			</el-col> -->
		</template>
	</section>
</template>

<script>
import { allget } from "../../../api/api";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
				startDate: [new Date() - 7 * 24 * 60 * 60 * 1000, new Date()], // ,180
				month: ""
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            searchType: "1"
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/Record/getCallData";
            let param = _this.condition();
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        //    
                        for (var i = 0; i < res.data.data.length; i++) {
                            res.data.data[i].totalrandtime = _this.time(
                                res.data.data[i].totalrandtime
                            );
                            res.data.data[i].totalredtime = _this.time(
                                res.data.data[i].totalredtime
                            );
                            res.data.data[i].totalfriendtime = _this.time(
                                res.data.data[i].totalfriendtime
                            );
                            res.data.data[i].totaltime = _this.time(
                                res.data.data[i].totaltime
                            );
                            res.data.data[i].totallistentime = _this.time(
                                res.data.data[i].totallistentime
                            );
                        }
                        this.listData = res.data.data;
                    } else {
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    console.log(err);
                    baseConfig.errorTipMsg(this, "error");
                });
		},
		// 
		condition() {
			if(this.searchType==1){
				return {
                	date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                	date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                	date: this.searchType
            	};
			}else {
				return{
					date_ms: baseConfig.changeDateTime(this.formOne.month, 2),
					date_me: baseConfig.changeDateTime(this.formOne.month, 2),
					date: this.searchType
				}
			}
		},
        // 
        time(time) {
            var dd = parseInt(time / 60 / 60 / 24);
            var hh = parseInt((time / 60 / 60) % 24);
            var mi = parseInt((time / 60) % 60);
            var ss = parseInt(time % 60);
            return dd + "" + hh + "" + mi + "" + ss + "";
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = pageHeight;
        _this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



