<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select v-model="channelId" multiple filterable collapse-tags style="width: 150px;" placeholder="">
						<el-option v-for="(item, key) of channelData" :key="item" :label="item" :value="key">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select style="width: 100px;" v-model="content">
						<el-option label="" value=""></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
						<el-option label="" value="3"></el-option>
						<el-option label="" value="4"></el-option>
						<el-option label="" value="5"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<span>UID</span>
					<el-input style="width:100px;" placeholder="" v-model="find" clearable>
					</el-input>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select style="width: 100px;" v-model="status">
						<el-option label="" value=""></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
						<el-option label="" value="3"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="id" label="ID"></el-table-column>
				<el-table-column prop="time" label=""></el-table-column>
				<el-table-column prop="uid" label=""></el-table-column>
				<el-table-column prop="complain_uid" label=""></el-table-column>
				<el-table-column prop="long_time" label=""></el-table-column>
				<el-table-column prop="channel" label=""></el-table-column>
				<el-table-column prop="num" label=""></el-table-column>
				<el-table-column prop="warn_num" label=""></el-table-column>
				<el-table-column prop="content" label=""></el-table-column>
				<el-table-column prop="explains" label=""></el-table-column>
				<el-table-column prop="evidences" label="">
					<template slot-scope="scope">
						<el-popover trigger="hover" placement="left">
							<img :src="scope.row.evidences" alt="" style="width:300px;height:300px;">
							<div slot="reference" class="name-wrapper">
								<img :src="scope.row.evidences" alt="" style="width:100px;height:100px;">
							</div>
						</el-popover>
					</template>
				</el-table-column>
				<el-table-column label="" min-width="120">
					<template slot-scope="scope">
						<div v-if="scope.row.status==1"></div>
						<div v-else-if="scope.row.status==2"></div>
						<div v-else-if="scope.row.status==3"></div>
						<el-row v-else-if="scope.row.status==0">
							<el-col :span="8">
								<el-button size="mini" type="success" @click="ignore(scope.$index, scope.row)"></el-button>
							</el-col>
							<el-col :span="8">
								<el-button size="mini" type="warning" @click="warning(scope.$index, scope.row)"></el-button>
							</el-col>
							<el-col :span="8">
								<el-button size="mini" type="info" @click="dialogFormVisible = true, getter(scope.$index, scope.row)"></el-button>
							</el-col>
						</el-row>
					</template>
				</el-table-column>
			</el-table>
		</template>
		<el-dialog title="" :visible.sync="dialogFormVisible">
			<el-form :model="form">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="form.time" placeholder="">
						<el-option label="1" value="1"></el-option>
						<el-option label="3" value="3"></el-option>
						<el-option label="5" value="5"></el-option>
						<el-option label="7" value="7"></el-option>
						<el-option label="15" value="15"></el-option>
						<el-option label="30" value="30"></el-option>
						<el-option label="" value="0"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="form.reason" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false"> </el-button>
				<el-button type="primary" @click="title()"> </el-button>
			</div>
		</el-dialog>
		<el-dialog title="" :visible.sync="dialogtitle" width="30%">
			<span></span>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false"> </el-button>
				<el-button type="primary" @click="dialogtitle = false"> </el-button>
			</span>
		</el-dialog>
	</section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from "axios";
import store from "../../../vuex/store";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 100 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            recordStatus: "",
            listData: [],
            totalpage: null,
            formLabelWidth: "120px",
            listLoading: false,
            channelData: {},
            channelId: null,
            find: null,
            status: null,
            content: null,
            dialogFormVisible: false,
            formLabelWidth: "120px",
            form: {
                time: null,
                reason: null
            },
            totle: {
                complain: "",
                time: "",
                id: "",
                uid: ""
            },
			dialogtitle: false,
			operate_user: null
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/NewUser/getComplain";
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                content: this.content,
                find: this.find,
                status: this.status,
                channel: this.channelId.join(","),
                operate_user: this.operate_user
            };
            allget(param, url)
                .then(res => {
                    this.listData = res.data.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        // 
        ignore(index, row) {
            let url = "/NewUser/ignoreUserComplain";
            let param = {
                id: row.id
            };
            allget(param, url)
                .then(res => {
                    baseConfig.successTipMsg(_this, res.data.msg);
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, res.data.msg);
                });
        },
        // 
        warning(index, row) {
            let url = "/NewUser/warnComplain";
            let param = {
                id: row.id,
                uid: row.complain_uid
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        },
        //  
        getter(index, row) {
            this.totle.complain = row.uid;
            this.totle.time = row.time;
            this.totle.id = row.id;
            this.totle.uid = row.complain_uid;
        },
        // 
        title(index, row) {
            var _this = this;
            let url = "/User/kickUser";
            let param = {
                complain: this.totle.complain,
                operate_user: this.operate_user,
                time: this.totle.time,
                id: this.totle.id,
                reason: this.form.reason,
                uid: this.totle.uid,
                day: this.form.time
            };
            if (param.day == "" || param.day == null) {
                _this.dialogtitle = true;
                return;
            }
            _this.dialogFormVisible = false;
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = searchPageHeight;
        _this.getData();
        var id = store.state.user.channelid.split(",");
        var name = store.state.user.channelname.split(",");
        for (var i = 0; i < id.length; i++) {
            _this.channelData[id[i]] = name[i];
        }
        _this.operate_user = store.state.user.name;
    },
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



