<template>
<!-- dom -->
	<section>
		<!--  -->
		<el-dialog title="" :visible.sync="dialogVisible" size="large">
			<el-row  v-loading="isgetuser" element-loading-text="" :gutter="20">
				<el-col :span="24" style="height: auto; background: red;">
					
					<div class="grid-content" style="overflow: auto;height: auto;">
						<el-col :span="4" v-for="(o, index) in allimg" :key="o" style="float: left; margin:2px 0;">
							<el-card :body-style="{ padding: '0px' }">
							<img :src="o" class="image" style="width: 110px; height: 110px;">
							<div style="padding: 0px;">							
								<el-button type="text" class="button" @click="deleteAll(1,userinfo.uid,index)"></el-button>
							</div>
							</el-card>
						</el-col>
					</div>
				</el-col>
				<el-col :span="24" style="height: auto; background: pink;">
					
					<div class="grid-content" style="overflow: auto;height: auto;">
						<!--  -->
						<el-col :span="12" style="float: left; margin:2px 0;">
							<el-card :body-style="{ padding: '0px' }">
							<div class="grid-content" style="height: 50px; float: left;">
								<audio :src="userinfo.voice_signature" controls="controls" preload="none"></audio>
							</div>
							<el-button type="primary" size="mini" icon="close" @click="deleteAll(3,userinfo.uid, index)"></el-button>
							</el-card>
						</el-col>
						<!--  -->
						<el-col :span="6" v-for="(item, index) in allVoiceData" :key="index" style="float: left; margin:2px 0;">
							<el-card :body-style="{ padding: '0px' }">
							<div class="grid-content" style="height: 50px; float: left;">
								<audio :src="item.voice_url" controls="controls" preload="none"></audio>
							</div>
							<el-button type="primary" size="mini" icon="close" @click="deleteAll(3,userinfo.uid, index)"></el-button>
							</el-card>
						</el-col>
					</div>
				</el-col>
				<el-col :span="12" style="height: auto; background: blue;">
					
					<div class="bg-purple" ></div>
					<div style="width: 220px; height: 220px; margin: auto;">
						<span class="photo_close" @click="deleteAll(2,userinfo.uid)">x</span>					
						<img style="width: 200px; height: 200px;" :src="userinfo.icon" alt="" >
					</div> 
				</el-col>
				<el-col :span="6"><div class="grid-content bg-purple">uid{{userinfo.uid}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.nickname}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.unionid}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.phone}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.birthday}}</div></el-col>
				<el-col :span="6">
					<div class="grid-content bg-purple" v-if="userinfo.sex==1"></div>
					<div class="grid-content bg-purple" v-else-if="userinfo.sex==2"></div>
				</el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.city}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.annotation}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">vip{{userinfo.vip_level}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.addtime}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.lasttime}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">IP{{userinfo.ip}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.accumulate_time}}</div></el-col>
				<el-col :span="6">
					<div class="grid-content bg-purple">{{userinfo.status}}</div>
				</el-col>
				<el-col :span="6">
					<div class="grid-content bg-purple">{{userinfo.is_online}}</div>
				</el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.remarks}}</div></el-col>				
				<el-col :span="6">
					<div class="grid-content bg-purple">{{userinfo.realname_aut_status}}</div>
				</el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.evaluate_score}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.charm_score}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.signature}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.labels}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.occupation}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.version_name}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.chat_gold}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.chat_ticket}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.pay_num}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.price}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.out_chat_gold}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.out_chat_ticket}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.name}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.identity_card}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.pay_account}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.family_name}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.follow}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.fans}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.friend}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.gift_num}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.cash}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totalrandcall}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totalrandtime}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totalfriendcall}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totalfriendtime}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totalredcall}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totalredtime}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totallistencall}}</div></el-col>
				<el-col :span="6"><div class="grid-content bg-purple">{{userinfo.totallistentime}}</div></el-col>
			</el-row>
		</el-dialog>
	</section>
</template>

<script>
/* js */
import Event from '../../public_js/event.js';
import { allget } from '../../api/api';
import store from '../../vuex/store';
export default {
	data() {
		return {
			userinfo: [], //
			dialogVisible: false, //
			isgetuser: false, //
		}
    },
	computed:{
		allimg() {
			var _this = this;
			return _this.userinfo.photo_wall;
		},
		allVoiceData() {
			var _this = this;
			return _this.userinfo.voice_data;
		},
	},
	methods: {
		//  
		getuserinfo(uid){
			let _this =this;
			_this.listLoading = true;
			_this.isgetuser = true;
			let url = '/NewUser/getUserInfo';// /User/getUserInfo
			let data ={ uid: uid, };
			allget(data, url).then(data => {
                console.log(data.data);
				if (data.data.ret) {
					_this.userinfo = data.data.data[0];
					_this.dialogVisible = true;
					_this.isgetuser = false;
				} else {
					baseConfig.normalTipMsg(_this, data.data.msg);
					_this.isgetuser=false;
				}
			}).catch(function(err){
				console.log(err);
			});
			
		},
    },
	mounted() {
        var _this = this;
        // mounted
        Event.$on('show-one-user', function(obj){
            // console.log(''+obj.uid);   
            // true
            // _this.dialogVisible = true;
            _this.getuserinfo(obj.uid);
        });
	},
};
</script>

<style>
/* css */
	
</style>



