<template>
    <!--  -->
    <!-- dom -->
    <section>

        <!-- Form -->
        <div class="container">
            <div class="setForm">
                <el-form :model="form">
                    <p class="bindingTitle" style="color: red;"><br>1(+-)<br> 2
                    </p>
                    <el-form-item class="input_uid" label="ID" >
                        <el-input style="width:400px;" v-model="form.uid" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item class="input_uid" label="" >
                        <el-input style="width:400px;" v-model="form.level" placeholder="" auto-complete="off"></el-input>
                        <p class="title">3 3</p>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="cancelFamily()"> </el-button>
                    <el-button type="primary" @click="gradeFamily()"> </el-button>
                </div>
            </div>
            <div class="familyRule">
                <template>
                    <el-table border :data="tableData" style="width: 100%">
                        <el-table-column prop="date" label="">
                        </el-table-column>
                        <el-table-column prop="name" label="" width="180">
                        </el-table-column>
                        <el-table-column prop="address" label="">
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </div>

    </section>
</template>

<script>
import { allget, officialAllet } from "../../../api/api.js";
export default {
    data() {
        return {
            tableHeight: null, // table
            form: {
                uid: null,
                level: null,
            },
            tableData: [
                {
                    date: "1",
                    name: "2000",
                    address: "30"
                },
                {
                    date: "2",
                    name: "200010000",
                    address: "40"
                },
                {
                    date: "3",
                    name: "1000050000",
                    address: "50"
                },
                {
                    date: "4",
                    name: "50000200000",
                    address: "100"
                },
                {
                    date: "5",
                    name: "2000001000000",
                    address: "150"
                },
                {
                    date: "6",
                    name: "1000000",
                    address: "200"
                },
            ]
        };
    },
    methods: {
        //
        gradeFamily() {
            var _this = this;
            let url = "/NewFamily/upFamilyLevel";
            let param = {
                family_id: this.form.uid,
                level: this.form.level,
            };
            allget(param, url)
                .then(res => {
                    console.log(res.data)
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this,res.data.msg);
                    }else{
                        baseConfig.warningTipMsg(_this,res.data.msg);
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        cancelFamily() {
            // alert(1)
            this.param.uid = "";
            this.param.level = "";
        }
    },
    mounted() {
        this.tableHeight = leftNavHeight;
    }
};
</script>

<style lang="css" scoped>
.container {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    margin-top: 20px;
    /* text-align: center; */
    /* border: 1px solid orangered; */
    overflow: hidden;
}
.setForm {
    float: left;
    width: 500px;
    height: 400px;
    padding-left: 20px;
    margin-left: 100px;
    border: 1px solid #999;
}
.familyRule {
    float: left;
    width: 400px;
    height: 400px;
    margin-left: 100px;
    border: 1px solid #aaa;
}
.input_uid {
    text-align: center;
    margin-top: 30px;
}
.dialog-footer {
    text-align: center;
    margin-top: 40px;
}
.title{
    color: #999;
    text-align: left;
    margin-left: 90px;
}
</style>



