<template>
	<!-- ->tabletable -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-tabs v-model="tabActiveName" type="border-card" @tab-click="handleClick">
			<!--  -->
			<el-tab-pane label="" name="first" :style="{ height: tabSearchHeight+'px' }">
				<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
					<el-form :inline="true" :model="formOne">
						<el-form-item>
							<div class="block">
								<span class="registerTime"></span>
								<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
							</div>
						</el-form-item>
						<el-form-item label="ID">
							<el-input v-model="formOne.family_id" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="getTableFind"></el-button>
						</el-form-item>
					</el-form>
				</el-col>
				<!---->
				<template>
					<el-table ref="tableHeight" :data="onePageData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="start_time" label="" width="200"></el-table-column>
						<el-table-column prop="end_time" label="" width="200"></el-table-column>
						<el-table-column prop="family_id" label="ID" width="200"></el-table-column>
						<el-table-column prop="theme" label="" width="200"></el-table-column>
						<el-table-column prop="money" label="" min-width="200"></el-table-column>
						<el-table-column prop="num" label="" width="200"></el-table-column>
					</el-table>
					<!---->
					<el-col :span="24" class="toolbar">
						<el-pagination layout="total,prev,pager,next,jumper" @current-change="oneHandleCurrentChange" :page-size="20" :total="formOne.TotalPage" style="float:right;"></el-pagination>
					</el-col>
				</template>
			</el-tab-pane>
			<!--  -->
			<el-tab-pane label="" name="second" :style="{ height: tabSearchHeight+'px' }">
				<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
					<el-form :inline="true" :model="formTwo">
						<el-form-item>
							<div class="block">
								<span class="registerTime"></span>
								<el-date-picker v-model="formTwo.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
							</div>
						</el-form-item>
						<el-form-item label="ID">
							<el-input v-model="formTwo.family_id" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="bannerNewloading.dialogShow = true;"></el-button>							
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="getTableManage"></el-button>
						</el-form-item>
					</el-form>
				</el-col>
				<!---->
				<template>
					<el-table ref="tableHeight" :data="twoPageData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="id" label="ID" width="80"></el-table-column>
						<el-table-column prop="start_time" label="" width="80"></el-table-column>
						<el-table-column prop="end_time" label="" width="80"></el-table-column>
						<el-table-column prop="family_name" label="" width="80"></el-table-column>
						<el-table-column prop="family_id" label="ID" width="80"></el-table-column>
						<el-table-column label="" width="150">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<img :src="scope.row.banner" style="width: 100px; height: auto;">
								</div>
							</template>
						</el-table-column>
						<el-table-column label="" width="150">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<img :src="scope.row.pop_banner" style="width: 100px; height: auto;">
								</div>
							</template>
						</el-table-column>
						<el-table-column label="banner" width="150">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<img :src="scope.row.message_banner" style="width: 100px; height: auto;">
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="theme" label="" width="50"></el-table-column>
						<el-table-column prop="content" label="" min-width="200"></el-table-column>
						<el-table-column prop="status" label="" width="100">
							<template slot-scope="scope">
								<p v-if="scope.row.status=='0'"></p>
								<p v-else-if="scope.row.status=='1'"></p>
							</template>
						</el-table-column>
						<el-table-column label="" width="150">
							<template slot-scope="scope">
								<el-button v-if="scope.row.status=='0'" type="primary" @click.native.prevent="grounding(scope.$index, formTwo.TabData)" size="small"></el-button>								
								<el-button v-else-if="scope.row.status=='1'" type="warning" @click.native.prevent="undercarriage(scope.$index, formTwo.TabData)" size="small"></el-button>								
							</template>
						</el-table-column>
					</el-table>
					<!---->
					<el-col :span="24" class="toolbar">
						<el-pagination layout="total,prev,pager,next,jumper" @current-change="twoHandleCurrentChange" :page-size="20" :total="formTwo.TotalPage" style="float:right;"></el-pagination>
					</el-col>
				</template>
			</el-tab-pane>
			<!-- banner -->
			<el-dialog title="" :visible.sync="bannerNewloading.dialogShow">
				<el-form :model="bannerNewloading.params">
					<el-form-item label="" :label-width="formLabelWidth">
						<div class="block">
							<el-date-picker v-model="bannerNewloading.params.choiceDate" type="datetimerange" range-separator="  " start-placeholder="" end-placeholder=""></el-date-picker>
						</div>
					</el-form-item>
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.family_id" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.theme" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.pop_banner" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="banner" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.message_banner" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.banner" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="bannerNewloading.params.content[0]" auto-complete="off" placeholder="1"></el-input>
						<el-input v-model="bannerNewloading.params.content[1]" auto-complete="off" placeholder="2"></el-input>
						<el-input v-model="bannerNewloading.params.content[2]" auto-complete="off" placeholder="3"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click.native.prevent="addBannerSure(0)"> </el-button>
					<el-button type="primary" @click.native.prevent="addBannerSure(1)"> </el-button>
				</div>
			</el-dialog>
		</el-tabs>
	</section>
</template>

<script>
/* js */
import Event from './../../public_js/event.js';
import { allget } from '../../api/api';
import store from '../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			tabSearchHeight: null,
			// tab
			formOne: {
				choiceDate: [new Date('2018/03/25'), new Date('2018/04/25')], // ,180
				family_id: '',
				TabData: [], //
				TotalPage: null, // 
				Page: 1, //1
				star: '0', //
				end: '20', //
			},
			formTwo: {
				choiceDate: [new Date('2018/03/25'), new Date('2018/04/25')], // ,180
				family_id: '',
				TabData: [], //
				TotalPage: null, // 
				Page: 1, //1
				star: '0', //
				end: '20', //
			},
			bannerNewloading: {
				dialogShow: false,
				params: {
					choiceDate: '',
					family_id: '',
					theme: '',
					content: ['', '', ''],
					banner: '',
					message_banner: '',
					pop_banner: '',
				},
			},
			listLoading: false, //
			tabActiveName: 'second', // tab(firstsecond)
			formLabelWidth: '130px', // dialog
		};
	},
	computed:{
		// 20
		onePageData() {
			var _this = this;
			return _this.formOne.TabData.slice(_this.formOne.star, _this.formOne.end);
		},
		twoPageData() {
			var _this = this;
			return _this.formTwo.TabData.slice(_this.formTwo.star, _this.formTwo.end);
		},
	},
	methods: {
		// onePageData
		oneHandleCurrentChange(val) {
			// val
			var _this = this;
			_this.formOne.Page = val;
			_this.formOne.star = (_this.formOne.Page-1)*20;
			_this.formOne.end = _this.formOne.star+20;
		},
		twoHandleCurrentChange(val) {
			// val
			var _this = this;
			_this.formTwo.Page = val;
			_this.formTwo.star = (_this.formTwo.Page-1)*20;
			_this.formTwo.end = _this.formTwo.star+20;
		},
		// 
		searchConditionFind() {
			var _this = this;
			var obj = {};
			obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0);
			obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0);
			obj.family_id = _this.formOne.family_id;
			for(var key in obj) { // 
				if(key=='family_id') {
					// 
				} else {
					if(obj[key]=='') {
						baseConfig.warningTipMsg(_this, '');
						return null;
					}
				}
			}
			return obj; // return
		},
		// 
		getTableFind() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Activity/getFamilyThemeActivityData';
			var params = _this.searchConditionFind();
			if(params==null) { // null
				_this.listLoading = false; // ,
			} else {
				axios.get(baseConfig.server+baseConfig.requestUrl+url, { params: params })
				.then((res) => {
					// console.log(res.data);
					_this.listLoading = false;	
					if(res.data.ret) {
						_this.formOne.TotalPage = res.data.data.length; // 
						_this.formOne.TabData = res.data.data;
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
					}
				}).catch((error) => {
					console.log(error);
				});
			}
		},
		// 
		searchConditionManage() {
			var _this = this;
			var obj = {};
			obj.date_s = baseConfig.changeDateTime(_this.formTwo.choiceDate[0], 0);
			obj.date_e = baseConfig.changeDateTime(_this.formTwo.choiceDate[1], 0);
			obj.family_id = _this.formTwo.family_id;
			for(var key in obj) { // 
				if(key=='family_id') {
					// 
				} else {
					if(obj[key]=='') {
						baseConfig.warningTipMsg(_this, '');
						return null;
					}
				}
			}
			return obj; // return
		},
		// banner
		getTableManage() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Activity/getFamilyThemeActivityInfoList';
			var params = _this.searchConditionManage();
			if(params==null) { // null
				_this.listLoading = false; // ,
			} else {
				axios.get(baseConfig.server+baseConfig.requestUrl+url, { params: params })
				.then((res) => {
					_this.listLoading = false;	
					if(res.data.ret) {
						for(var i=0; i<res.data.data.length; i++) {
							res.data.data[i].content = JSON.parse(res.data.data[i].content);
							res.data.data[i].content = JSON.parse(res.data.data[i].content);
							for(var j=0; j<res.data.data[i].content.length; j++) {
								res.data.data[i].content[j] = (j+1)+''+decodeURI(res.data.data[i].content[j]);
							}
						}
						_this.formTwo.TotalPage = res.data.data.length; // 
						_this.formTwo.TabData = res.data.data;
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
					}
				}).catch((error) => {
					console.log(error);
				});
			}
		},
		// banner
		addBannerSure(type) {
			var _this = this;
			if(type==0) {
				// console.log('');
				_this.bannerNewloading.dialogShow = false;
			} else if(type==1) {
				// console.log('');
				_this.listLoading = true;
				// 
				let formData = new FormData();
				var start_time = baseConfig.changeDateTime(_this.bannerNewloading.params.choiceDate[0], 1);
				var end_time = baseConfig.changeDateTime(_this.bannerNewloading.params.choiceDate[1], 1);
				for(var i=0; i<_this.bannerNewloading.params.content.length; i++) {
					_this.bannerNewloading.params.content[i] = encodeURI(_this.bannerNewloading.params.content[i]);
				}
				var content = JSON.stringify(_this.bannerNewloading.params.content);
				formData.append('start_time', start_time);
				formData.append('end_time', end_time);
				formData.append('theme', _this.bannerNewloading.params.theme);
				formData.append('family_id', _this.bannerNewloading.params.family_id);
				formData.append('content', content);
				formData.append('banner', _this.bannerNewloading.params.banner);
				formData.append('pop_banner', _this.bannerNewloading.params.pop_banner);
				formData.append('message_banner', _this.bannerNewloading.params.message_banner);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};	
				axios.post(baseConfig.server+baseConfig.requestUrl+'/Activity/addFamilyThemeActivity', formData, config)
				.then((res) => {
					console.log(res.data);
					_this.listLoading = false;	
					_this.bannerNewloading.dialogShow = false;								
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableFind();
						_this.getTableManage();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			}
		},
		// 
		grounding(index, rows) {
			var _this = this;
			index = index + (_this.formTwo.Page-1)*20;
			var id = rows[index].id;
			_this.listLoading = true;
			var url = 'Activity/upDownFamilyThemeActivity';
			var params = {
				id: id,
				status: 1,
			};
			axios.get(baseConfig.server+baseConfig.requestUrl+url, { params: params })
			.then((res) => {
				_this.listLoading = false;	
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, '');
					_this.getTableFind();
					_this.getTableManage();
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
				}
			}).catch((error) => {
				console.log(error);
			});
		},
		// 
		undercarriage(index, rows) {
			var _this = this;
			index = index + (_this.formTwo.Page-1)*20;
			var id = rows[index].id;
			_this.listLoading = true;
			var url = 'Activity/upDownFamilyThemeActivity';
			var params = {
				id: id,
				status: 0,
			};
			axios.get(baseConfig.server+baseConfig.requestUrl+url, { params: params })
			.then((res) => {
				_this.listLoading = false;	
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, '');
					_this.getTableFind();
					_this.getTableManage();
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
				}
			}).catch((error) => {
				console.log(error);
			});
		},
		// tab
		handleClick(tab, event) {
			var _this = this;
			// console.log(tab.label);
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = tabSearchPageHeight;
			_this.tabSearchHeight = tabSearchHeight;			
			_this.getTableFind();
			_this.getTableManage();
		})
	}
};
</script>

<style lang="css" scoped>
/*  */
.fileinput{
	float: left;
	margin-top: 8px;
}
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
.demo-ruleForm{
	width: 500px;
}
.el-tab-pane{
	height: 800px;
}
</style>



