<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="time" label=""></el-table-column>
				<el-table-column prop="manredclick" label=""></el-table-column>
				<el-table-column prop="man_num" label=""></el-table-column>
				<el-table-column prop="man_rate" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{(scope.row.woman_rate*100).toFixed(2)+"%"}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="man_time" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.man_time)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="womanredclick" label=""></el-table-column>
				<el-table-column prop="woman_num" label=""></el-table-column>
				<el-table-column prop="woman_rate" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{(scope.row.woman_rate*100).toFixed(2)+"%"}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="woman_time" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.woman_time)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="freeredclick" label=""></el-table-column>
				<el-table-column prop="free_num" label=""></el-table-column>
				<el-table-column prop="free_rate" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{(scope.row.free_rate*100).toFixed(2)+"%"}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="free_time" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.free_time)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="moneyredclick" label=""></el-table-column>
				<el-table-column prop="money_num" label=""></el-table-column>
				<el-table-column prop="money_rate" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{(scope.row.money_rate*100).toFixed(2)+"%"}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="money_time" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.money_time)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="totalredclick" label=""></el-table-column>
				<el-table-column prop="total_num" label=""></el-table-column>
				<el-table-column prop="total_rate" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{(scope.row.total_rate*100).toFixed(2)+"%"}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="total_time" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.total_time)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="call_money" label=""></el-table-column>
				<el-table-column prop="listen_valid_times" label=""></el-table-column>
				<el-table-column prop="listen_valid_long" label=""></el-table-column>
				<el-table-column prop="avg" label=""></el-table-column>
				<el-table-column prop="gift_money" label=""></el-table-column>
				<el-table-column prop="one_num" label="0~1"></el-table-column>
				<el-table-column prop="three_num" label="1~3"></el-table-column>
				<el-table-column prop="five_num" label="3~5"></el-table-column>
				<el-table-column prop="ten_num" label="5~10"></el-table-column>
				<el-table-column prop="thirty_num" label="10~30"></el-table-column>
				<el-table-column prop="sixty_num" label="30~60"></el-table-column>
				<el-table-column prop="sixtymore_num" label="60"></el-table-column>
			</el-table>
		</template>
	</section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from 'axios';
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
				startDate: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()], // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
			let param = _this.condition();
            let url = "/Record/getRedData";
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                            for(var i = 0;i<res.data.data.length;i++){
                                res.data.data[i].avg = baseConfig.changeTime(res.data.data[i].total_time/res.data.data[i].total_num);
                            }
                        this.listData = res.data.data;
                    } else {
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(this, "error"); 
                });
		},
		// 
		condition() {
            return {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
            };
        },
        // 
        timeTransform(oldValue){
            return baseConfig.changeTime(oldValue);
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = pageHeight;
        _this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



