<template>
	<!-- ->tabletable -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-tabs v-model="tabActiveName" type="border-card" @tab-click="handleClick">
			<!-- Banner -->
			<el-tab-pane label="" name="first" :style="{ height: tabSearchHeight+'px' }">
				<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
					<el-form :inline="true" :model="formOne">
						<el-form-item>
							<div class="block">
								<span class="registerTime"></span>
								<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
							</div>
						</el-form-item>
						<el-form-item>
							<el-input placeholder="uid" v-model="formOne.uid"></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="getTableOne"></el-button>
						</el-form-item>
					</el-form>
				</el-col>
				<!---->
				<template>
					<el-table ref="tableHeight" :data="formOne.tabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="id" label="ID" width="200"></el-table-column>
						<el-table-column prop="uid" label="uid" width="200"></el-table-column>
						<el-table-column label="" width="200">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="scope.row.vip_level==1"></p>
									<p v-else-if="scope.row.vip_level==2"></p>
									<p v-else-if="scope.row.vip_level==3"></p>
									<p v-else-if="scope.row.vip_level==4"></p>
									<p v-else-if="scope.row.vip_level==5"></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="create_time" label="" width="80"></el-table-column>
                        <el-table-column label="" width="200">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="scope.row.status==0"></p>
									<p v-else-if="scope.row.status==1"></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="reward_uid_list" label="" min-width="300"></el-table-column>
					</el-table>
					<!---->
					<el-col :span="24" class="toolbar">
						<el-pagination layout="total,prev,pager,next,jumper" @current-change="oneHandleCurrentChange" :page-size="10" :total="formOne.totalPage" style="float:right;"></el-pagination>
					</el-col>
				</template>
			</el-tab-pane>
			<!-- Banner -->
			<el-tab-pane label="" name="second" :style="{ height: tabSearchHeight+'px' }">
				<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
					<el-form :inline="true" :model="formTwo">
						<el-form-item>
							<div class="block">
								<span class="registerTime"></span>
								<el-date-picker v-model="formTwo.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
							</div>
						</el-form-item>
						<el-form-item>
							<el-input placeholder="uid" v-model="formTwo.uid"></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="getTableTwo"></el-button>
						</el-form-item>
					</el-form>
				</el-col>
				<!---->
				<template>
					<el-table ref="tableHeight" :data="formTwo.tabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="id" label="ID" width="200"></el-table-column>
						<el-table-column prop="uid" label="uid" width="200"></el-table-column>
						<el-table-column label="" width="200">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="create_time" label="" width="80"></el-table-column>
                        <el-table-column label="" width="200">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="scope.row.status==0"></p>
									<p v-else-if="scope.row.status==1"></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="reward_uid_list" label="" min-width="300"></el-table-column>
					</el-table>
					<!---->
					<el-col :span="24" class="toolbar">
						<el-pagination layout="total,prev,pager,next,jumper" @current-change="twoHandleCurrentChange" :page-size="10" :total="formTwo.totalPage" style="float:right;"></el-pagination>
					</el-col>
				</template>
			</el-tab-pane>
		</el-tabs>
	</section>
</template>

<script>
/* js */
import Event from './../../public_js/event.js';
import { allget } from '../../api/api';
import store from '../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // plane
			tabSearchHeight: null, // table
			// tab
			formOne: {
				choiceDate: [new Date()-180*24*60*60*1000, new Date()], // ,180
				uid: '',
				tabData: [], //
				totalPage: 1000, // 
				page: 0, //1
			},
			formTwo: {
                choiceDate: [new Date()-180*24*60*60*1000, new Date()], // ,180
                uid: '',
				tabData: [], //
				totalPage: 1000, // 
				page: 0, //1
			},
			listLoading: false, //
			tabActiveName: 'first', // tab(firstsecond)
			formLabelWidth: '130px', // dialog
		};
	},
	methods: {
		// onePageData
		oneHandleCurrentChange(val) {
			var _this = this;
            _this.formOne.page = val-1;
            _this.getTableOne();
		},
		twoHandleCurrentChange(val) {
			var _this = this;
            _this.formTwo.page = val-1;
            _this.getTableTwo();
		},
		searchConditionOne() {
			var _this = this;
			var obj = {};
			obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0);
			obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0);
            obj.uid = _this.formOne.uid;
            obj.page = _this.formOne.page;
			return obj; // return
        },
        searchConditionTwo() {
			var _this = this;
			var obj = {};
			obj.date_s = baseConfig.changeDateTime(_this.formTwo.choiceDate[0], 0);
			obj.date_e = baseConfig.changeDateTime(_this.formTwo.choiceDate[1], 0);
            obj.uid = _this.formTwo.uid;
            obj.page = _this.formTwo.page;
			return obj; // return
        },
		// banner
		getTableOne() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Activity/getCrowdFundingVipNormalData';
			var params = _this.searchConditionOne();
            allget(params, url).then(res => {
                _this.listLoading = false;
                if(res.data.ret) {
                    _this.formOne.tabData = res.data.data;
                } else {
                    baseConfig.errorTipMsg(_this, res.data.msg);
                }
            }).catch(function(error){
                baseConfig.errorTipMsg(_this, error);
            })
		},
		// banner
		getTableTwo() {
			var _this = this ;
			_this.listLoading = true;
            var url = '/Activity/getCrowdFundingVipSuperData';
            var params = _this.searchConditionTwo();
			allget(params, url).then(res => { 
				_this.listLoading = false;
				if(res.data.ret) {
					_this.formTwo.tabData = res.data.data;
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg);
				}
			}).catch(function(error){
				baseConfig.errorTipMsg(_this, error);
			})
		},
		// plane
		handleClick(tab, event) {
			var _this = this;
		},
	},
	mounted() {
		var _this = this;
		_this.$nextTick(function() {
			_this.tableHeight = tabSearchPageHeight;
			_this.tabSearchHeight = tabSearchHeight;			
			_this.getTableOne();
			_this.getTableTwo();
		})
	}
};
</script>

<style lang="css" scoped>
/*  */
.fileinput{
	float: left;
	margin-top: 8px;
}
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
.demo-ruleForm{
	width: 500px;
}
.el-tab-pane{
	height: 800px;
}
</style>



