<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select v-model="formOne.send_status">
						<el-option label="" value=""></el-option>
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<el-button type="primary" @click="formCondition.dialogFormVisible=true;"></el-button>
					<el-button type="primary" @click="formPhone.dialogFormVisible=true;">UID</el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="create_time" label="" width="150" sortable ></el-table-column>
				<el-table-column prop="send_time" label="" width="150" sortable ></el-table-column>
				<el-table-column label="" width="50" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.condition_sex==0"></p>
							<p v-else-if="scope.row.condition_sex==1"></p>
							<p v-else></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="" width="80" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.address==0"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="phone_list" label="" min-width="150" sortable ></el-table-column>
				<el-table-column prop="template_id" label="id" width="100" sortable ></el-table-column>
				<el-table-column prop="parameter" label="" width="150" sortable ></el-table-column>
				<el-table-column label="" width="100" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.is_timing==0"></p>
							<p v-else-if="scope.row.is_timing==1"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.send_status==0"></p>
							<p v-else-if="scope.row.send_status==1"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="operation_name" label="" width="150" sortable ></el-table-column>				
				<el-table-column label="" width="100">
					<template slot-scope="scope">
						<el-button type="primary" @click.native.prevent="lookOneUserData(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
		</template>
		<!--  -->
		<el-dialog title="" :visible.sync="formFind.dialogFormVisible">
			<el-table :data="formFind.tabData" style="width: 100%">
				<el-table-column prop="mobile" label="" width="180"></el-table-column>
				<el-table-column prop="updatetime" label="" width="180"></el-table-column>
				<el-table-column prop="status" label="">
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.status==0"></p>
							<p v-else-if="scope.row.status==1"></p>
							<p v-else-if="scope.row.status==2"></p>
							<p v-else-if="scope.row.status==3"></p>
						</div>
					</template>
				</el-table-column>
			</el-table>
		</el-dialog>
		<!-- --dialog -->
		<el-dialog title="" :visible.sync="formCondition.dialogFormVisible">
			<el-form :model="formCondition">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formCondition.condition_sex" label="0"></el-radio>
					<el-radio v-model="formCondition.condition_sex" label="1"></el-radio>
					<el-radio v-model="formCondition.condition_sex" label="2"></el-radio>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formCondition.registerTime" type="daterange" range-separator="  " start-placeholder="" end-placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formCondition.loginTime" type="daterange" range-separator="  " start-placeholder="" end-placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="formCondition.address">
						<el-option label="" value="0"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formCondition.channel" label="0"></el-radio>
					<el-radio v-model="formCondition.channel" label="1">ios</el-radio>
					<el-radio v-model="formCondition.channel" label="2">android</el-radio>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formCondition.version_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="ID" :label-width="formLabelWidth">
					<el-input v-model="formCondition.template_id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input type="textarea" :rows="2" :maxlength="50" v-model="formCondition.parameter" auto-complete="off"></el-input>
					<p style="font-weight: bold;">xxx,xxx,xxx,xxx</p>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formCondition.is_timing" label="0"></el-radio>
					<el-radio v-model="formCondition.is_timing" label="1"></el-radio>
				</el-form-item>
				<el-form-item v-if="formCondition.is_timing==1" label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formCondition.send_time" type="datetime" placeholder=""></el-date-picker>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="conditionSure(0)"> </el-button>
				<el-button type="primary" @click="conditionSure(1)"> </el-button>
			</div>
		</el-dialog>
		<!-- uid--dialog -->
		<el-dialog title="Phone" :visible.sync="formPhone.dialogFormVisible">
			<el-form :model="formPhone">
				<el-form-item label="Phone" :label-width="formLabelWidth">
					<el-input type="textarea" :rows="2" :maxlength="50" v-model="formPhone.phone_list" auto-complete="off"></el-input>
					<p style="font-weight: bold;">Phone(:13800000000,13800000001,13800000002)</p>
				</el-form-item>
				<el-form-item label="ID" :label-width="formLabelWidth">
					<el-input v-model="formPhone.template_id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input type="textarea" :rows="2" :maxlength="50" v-model="formPhone.parameter" auto-complete="off"></el-input>
					<p style="font-weight: bold;">xxx,xxx,xxx,xxx</p>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formPhone.is_timing" label="0"></el-radio>
					<el-radio v-model="formPhone.is_timing" label="1"></el-radio>
				</el-form-item>
				<el-form-item v-if="formPhone.is_timing==1" label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formPhone.send_time" type="datetime" placeholder=""></el-date-picker>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="phoneSure(0)"> </el-button>
				<el-button type="primary" @click="phoneSure(1)"> </el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			// 
			formOne: {
				choiceDate: [new Date()-180*24*60*60*1000, new Date()], // ,180
				send_status: '',
			},
			formFind: {
				dialogFormVisible: false,
				tabData: [],
			},
			formCondition: {
				dialogFormVisible: false, // dialog
				appoint_type: '2', //0
				// 
				condition_sex: '0', //
				registerTime: [new Date()-180*24*60*60*1000, new Date()], //
				loginTime: [new Date()-180*24*60*60*1000, new Date()], //
				addtime_start: '', //
				addtime_end: '', //
				lasttime_start: '', //
				lasttime_end: '', //
				address: '0', //
				channel: '0', //ios
				version_name: '', //
				// 
				// *****
				phone_list: ' ', 
				// *****
				template_id: '', 
				parameter: ' ', 
				is_timing: '0', //0->1->
				send_time: '', //
				operation_name: '', //
			},
			formPhone: {
				dialogFormVisible: false, // dialog
				appoint_type: '3', //uid1
				// *****
				condition_sex: '0', //
				addtime_start: '', //
				addtime_end: '', //
				lasttime_start: '', //
				lasttime_end: '', //
				address: '0', //
				channel: '0', //ios
				version_name: '', //
				// *****
				// 
				phone_list: ' ', 
				// 
				template_id: '', 
				parameter: ' ', 
				is_timing: '0', //0->1->
				send_time: '', //
				operation_name: '', //
			},
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0); // 
			obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0); // 
			obj.send_status = _this.formOne.send_status; // 
			// 
			for(var key in obj) {
				if(key=='send_status') {
					// 
				} else {
					if(obj[key]=='') {
						baseConfig.warningTipMsg(_this, '');
						return null;
					}
				}
			}
			return obj; // return
		},
		// 
		conditionSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.formCondition.dialogFormVisible = false; // dialog
				// console.log('');
			} else if(val==1) {
				// 
				// console.log('');
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('condition_sex', _this.formCondition.condition_sex);
				formData.append('addtime_start', baseConfig.changeDateTime(_this.formCondition.registerTime[0], 0));
				formData.append('addtime_end', baseConfig.changeDateTime(_this.formCondition.registerTime[1], 0));
				formData.append('lasttime_start', baseConfig.changeDateTime(_this.formCondition.loginTime[0], 0));
				formData.append('lasttime_end', baseConfig.changeDateTime(_this.formCondition.loginTime[1], 0));
				formData.append('address', _this.formCondition.address);
				formData.append('appoint_type', _this.formCondition.appoint_type);
				formData.append('phone_list', _this.formCondition.phone_list);
				formData.append('template_id', _this.formCondition.template_id);
				formData.append('parameter', _this.formCondition.parameter);
				formData.append('operation_name', _this.formCondition.operation_name);
				formData.append('version_name', _this.formCondition.version_name);
				formData.append('is_timing', _this.formCondition.is_timing);
				formData.append('send_time', baseConfig.changeDateTime(_this.formCondition.loginTime, 1));
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/sendMsmMess', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formCondition.dialogFormVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			} 
		},
		// uid
		phoneSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.formPhone.dialogFormVisible = false; // dialog
				// console.log('');
			} else if(val==1) {
				// 
				// console.log('');
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('condition_sex', _this.formPhone.condition_sex);
				formData.append('addtime_start', '');
				formData.append('addtime_end', '');
				formData.append('lasttime_start', '');
				formData.append('lasttime_end', '');
				formData.append('address', _this.formPhone.address);
				formData.append('appoint_type', _this.formPhone.appoint_type);
				formData.append('phone_list', _this.formPhone.phone_list);
				formData.append('template_id', _this.formPhone.template_id);
				formData.append('parameter', _this.formPhone.parameter);
				formData.append('operation_name', _this.formPhone.operation_name);
				formData.append('version_name', _this.formPhone.version_name);
				formData.append('is_timing', _this.formPhone.is_timing);
				formData.append('send_time', baseConfig.changeDateTime(_this.formPhone.loginTime, 1));
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/sendMsmMess', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formPhone.dialogFormVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			} 
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/GlobalSet/findMsmMess';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
					_this.listLoading = false;
					if(res.data.ret) {
						// 
						_this.totalpage = res.data.data.length;
						_this.tabData = res.data.data;
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}

				}).catch(function(error){
					console.log(error);
				})
			}
		},
		// 
		lookOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var send_id = rows[index].send_id; // value	
			// 
			_this.listLoading = true;
			var url = '/GlobalSet/getMsmMessStatus';
			var params = {
				send_id: send_id,
			};
			allget(params, url).then(res => {
				_this.listLoading = false;
				if(res.data.ret) {
					// 
					for(var i=0; i<res.data.obj.length; i++) {
						res.data.obj[i].updatetime = baseConfig.changeDateTime(new Date(res.data.obj[i].updatetime), 0);
					}
					_this.formFind.tabData = res.data.obj;
					_this.formFind.dialogFormVisible = true;
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg);
				}
			}).catch(function(error) {
				console.log(error);
			})
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getTableData();
			_this.formCondition.operation_name = store.state.user.name; // 
			_this.formPhone.operation_name = store.state.user.name; // 
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



