<template>
    <!-- IM -->
    <!-- dom -->
    <section>
        <el-tabs type="border-card" v-model="activeName">
            <el-tab-pane label="" name="first">
                <!--  -->
                <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                    <el-form :inline="true">
                        <el-form-item>
                            <span></span>
                            <el-select style="width: 200px;" v-model="sex">
                                <el-option label="" value=""></el-option>
                                <el-option label="" value="1"></el-option>
                                <el-option label="" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <span>UID</span>
                            <el-input style="width:100px;" placeholder="" v-model="uid" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item style="float:right;margin-right: 100px;">
                            <el-button type="primary" @click="getTbData"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <!---->
				<template>
					<el-table ref="tableHeight" :data="listData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="uid" label="uid"></el-table-column>
						<el-table-column prop="nickname" label=""></el-table-column>
						<el-table-column prop="sex" :formatter="judgeSex" label=""></el-table-column>
						<el-table-column prop="match_people" label=""></el-table-column>
						<el-table-column prop="read_people" label=""></el-table-column>
						<el-table-column prop="new_reply_people" label=""></el-table-column>
						<el-table-column prop="reply_people" label=""></el-table-column>
						<el-table-column prop="add_pond_time" label=""></el-table-column>
					</el-table>
				</template>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <!--  -->
                <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                    <el-form :inline="true">
                        <el-form-item>
                            <div class="block">
                                <span class="registerTime"></span>
                                <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                                </el-date-picker>
                            </div>
                        </el-form-item>
                        <el-form-item>
                            <span>UID</span>
                            <el-input style="width:100px;" placeholder="" v-model="uid1" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item style="float:right;margin-right: 100px;">
                            <el-button type="primary" @click="getTbData1"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <!---->
				<template>
					<el-table ref="tableHeight" :data="listData1" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="date" label=""></el-table-column>
						<el-table-column prop="uid" label="uid"></el-table-column>
						<el-table-column prop="match_num" label=""></el-table-column>
						<el-table-column prop="read_num" label=""></el-table-column>
						<el-table-column prop="new_reply_num" label=""></el-table-column>
						<el-table-column prop="reply_num" label=""></el-table-column>
					</el-table>
				</template>
            </el-tab-pane>
        </el-tabs>
    </section>
</template>
<script>
import {allget} from '../../../api/api';
export default {
    data() {
        return {
            sex: "",
            uid: "",
            uid1: "",
            listData: [],
            listData1: [],
            listLoading: false,
            tableHeight: null,
            activeName: "first",
            formOne: {
				startDate: [new Date() - 30 * 24 * 60 * 60 * 1000, new Date()], // ,180
            },
        };
    },
    methods: {
        getTbData() {
            var _this = this;
            let url = '/User/sayHelloData';
            let param = {
                sex: this.sex,
                uid: this.uid,
            }
            allget(param, url).then(res => {
                console.log(res)
                if(res.data.ret){
                    this.listData = res.data.data;
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);
                }
            }).catch(err => {
                console.log(err)
            })
        },
        getTbData1(){
            var _this = this;
            let url = '/User/sayHelloInfo';
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                uid: this.uid1,
            }
            this.uid1==null||this.uid1==""?delete param.uid1:param.uid1=this.uid1;
            allget(param, url).then(res => {
                console.log(res)
                if(res.data.ret){
                    this.listData1 = res.data.data;
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);
                }
            }).catch(err => {
                console.log(err)
            })
        },
        // 
        judgeSex(row){
            return row.sex==1?"":"";
        }
    },
    mounted() {
        this.tableHeight = tabSearchHeight;
        this.getTbData();        
        this.getTbData1();        
    }
};
</script>
<style lang="css" scoped>

</style>




