<template lang="html">
    <el-row class="container">
        <!--  -->
        <el-menu theme="dark" :default-active="indexPath" class="el-menu-demo" mode="horizontal" router @select="handleSelect">
            <!-- logo -->
            <div class="el_logo">
                <div class="logo"></div>
            </div>
            <!--  -->
            <!-- <el-menu-item index="/operationData" :route="{path: '/operationData'}"></el-menu-item>
            <el-menu-item index="/userManagement" :route="{path: '/userManagement'}"></el-menu-item>
            <el-menu-item index="/operationSupport" :route="{path: '/operationSupport'}"></el-menu-item>
            <el-menu-item index="/activities" :route="{path: '/activities'}"></el-menu-item>
            <el-menu-item index="/systemSetup" :route="{path: '/systemSetup'}"></el-menu-item> -->
            <el-menu-item v-for="(item, indexs) in dataView" :key="indexs" :index="item.path" :router="{path: item.path}">{{item.name}}</el-menu-item>
            <!-- uid -->
            <div class="uidFind">
                <input v-model="uidFind" placeholder="uid" />
                <div @click="goToShowUidContent()"></div>
            </div>
            <!--  -->
            <el-col :span="4" class="userinfo">
                <el-dropdown trigger="hover">
                    <span class="el-dropdown-link userinfo-inner"><img :src="sysUserAvatar" /> {{sysUserName}}</span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item></el-dropdown-item>
                        <el-dropdown-item></el-dropdown-item>
                        <el-dropdown-item divided @click.native="logout"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-col>
        </el-menu>
        <!--  -->
		<oneUserContent></oneUserContent>
        <!--  -->
        <!--  -->
        <el-col :span="24" class="content-all-wrapper">
            <transition name="el-fade-in-linear" mode="out-in" >
                <router-view  class="nav-view">

                </router-view>
            </transition>
        </el-col>
    </el-row>

</template>

<script>
import Event from './../../public_js/event.js';
import oneUserContent from './../rootGlobal/oneUserContent.vue'; // 
import store from '../../vuex/store';
export default {
     data() {
        return {
            sysUserName: '',
            sysUserAvatar: 'http://imgtu.5011.net/uploads/content/20170410/2880121491810236.jpg',
            indexPath: '', //  
            indexPathArr: ['/operationData', '/operationSupport', '/anchorManager', '/systemManagement', '/systemSetting'], // home
            // routes:"",
            uidFind: '', //uid
        };
    },
    computed: {
        dataView() {
            // store
            let thatDdta = store.getters.addRouters;
            // console.log(store.getters.addRouters);            
            return store.getters.addRouters;
        }
    },
    components: {
        oneUserContent,
	},
    mounted:function(){
        var _this = this;
        _this.$nextTick(function(){
            // 
            var strPath = location.href;
            var index = strPath.indexOf('#/');
            strPath = strPath.substring(index+1,strPath.length);
            for(var i=0; i<_this.indexPathArr.length; i++) {
                if(strPath.indexOf(_this.indexPathArr[i])!=-1) {
                    // 
                    _this.indexPath = _this.indexPathArr[i];
                    break;
                }
            }
        })
    },
    methods: {
        handleSelect(key, keyPath) {
            // console.log(key, keyPath);
        },
        logout: function () {
            var _this = this;
            _this.$confirm('?', '', {
                //type: 'warning'
            }).then(() => {
                _this.$router.push({ path: '/login', });
                //
                location.reload();
            }).catch((error) => {
                console.log(error);
            });
        },
        handleOpen(key, keyPath) {
            // console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            // console.log(key, keyPath);
        },
        // 
        goToShowUidContent() {
            var _this = this;
            if(baseConfig.server.indexOf('test')=='-1') {
                // 6
                if(/^[0-9]{6}$/.test(_this.uidFind)) {
                    Event.$emit('show-one-user', {
                        uid : _this.uidFind,
                    });
                } else {
                    baseConfig.normalTipMsg(_this, 'uiduid');
                }
            } else {
                // 5uid
                if(/^[0-9]{5}$/.test(_this.uidFind)) {
                    Event.$emit('show-one-user', {
                        uid : _this.uidFind,
                    });
                } else {
                    baseConfig.normalTipMsg(_this, 'uiduid');
                }
            }
         },
    }
}
</script>

<style lang="css">
.el-select__tags{
    max-width: auto !important;
    width: 1000px !important;
}
/* uid */
.uidFind{
    position: absolute;
    right: 15%; top: 10px;
    width: 220px; height: 40px;
}
.uidFind input{
    width: 150px; height: 40px;
    padding: 0; margin: 0;
    text-align: left; line-height: 40px;
    position: absolute; left: 0; top: 0;
    border: none; outline: none;
    text-indent: 20px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}
.uidFind div{
    width: 70px; height: 40px;
    text-align: center; line-height: 40px;
    position: absolute; left: 150px; top: 0;
    background: cadetblue; color: #fff;
    cursor: pointer;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
/*  */
.userinfo {
    height: 60px;
    text-align: right;
    padding-right: 35px;
    float: right !important;
}
.userinfo-inner {
    cursor: pointer;
    color:#fff;
}
.userinfo-inner img {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin: 10px 0px 10px 10px;
    float: right;
}
/* logo,13% */
.el_logo{
    width: 13%; 
    height: 60px;
    min-width: 150px;
    display: block;
    float: left;
}
.logo {
    display: block;
    float: left;
    width: 58px;
    height: 28px;
    background-image: url(http://dianliaotools.oss-cn-shenzhen.aliyuncs.com/common/dianliao-logo.png) ;
    background-size: cover;
    margin:15px 10px 0 80px;
}
.nav-view {
    /*height: 100%;*/
    width: 100%;
    /* min-width: 1080px; */
}
/* .nav-head {
    min-width: 1080px;
} */
.container{
    /* min-width: 1080px; */
    min-height: 660px;
}
/*  */
/* .el-breadcrumb__item{
    height: 60px !important; 
    background-color: #f1f7ff !important; 
    float: left !important; 
    line-height: 60px !important;
}
.el-breadcrumb__item:nth-of-type(1){
    margin-left: 200px;
    padding-left: 40px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}
.el-breadcrumb__item:nth-last-of-type(1){
    padding-right: 40px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
} */
@media screen and (min-height: 660px) {
    .left_active {
        width: 13%;
        min-width: 150px;
        /* min-height: 660px; */
        height: 100% ;
        background-color: #eef1f6;
        float: left;
    }
}
.left_active {
    width: 13%;
    min-width: 150px;
    /* min-height: 944px; */
    /* min-height: 914px; */
    height: 100% ;
    background-color: #324157;
    float: left;
}
.content-all-wrapper {
    /*margin-top: 2px;*/
    height: 100%;
    /* min-width: 1200px; */
}
.content-container {
    display: inline-block;
    overflow-y: scroll;
    padding: 20px;
}
.content-wrapper {
    width: 87% !important;
    height: 100%;
    background-color: #fff;
    float: left;
}
/*  */
.el-menu-item-group__title{
    padding-top: 0 !important;
}
.el-submenu__title{
    height: 40px !important;
    line-height: 40px !important;
}
/* item */
.el-submenu .el-menu-item{
    height: 40px !important;
    line-height: 40px !important;
}
/* item */
/* el-menu */
.left_active .el-menu-item{
    height: 40px !important;
    line-height: 40px !important;
}
.el-dialog__body{
    padding-top: 10px !important;
}
</style>



