<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!-- / -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="formOne">
                <el-form-item>
                    <div class="block">
                        <span class="registerTime"></span>
                        <el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item>
                    <span></span>
                    <el-select style="width: 150px;" v-model="searchType">
                        <el-option label="" value="0"></el-option>
                        <el-option label="" value="1"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <span></span>
                    <el-select v-model="channelId" multiple filterable collapse-tags style="margin-left: 20px;" placeholder="">
                        <el-option v-for="(item, key) of channelData" :key="item" :label="item" :value="key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item style="margin-left: 100px;">
                    <el-button type="primary" @click="chartLineShow"></el-button>
                    <el-button type="primary" @click="getTableData"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <template>
            <el-table ref="tableHeight" :data="tabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
                <el-table-column prop="date" label="/" sortable></el-table-column>
                <el-table-column  label="//">
                    <template slot-scope="scope">
                        <p>{{+scope.row.man_num+(+scope.row.woman_num)}}</p>   
                    </template>
                </el-table-column>
                <el-table-column prop="silver" label="//">
                    <template slot-scope="scope">
                        <p>{{+scope.row.man_people+(+scope.row.woman_people)}}</p>   
                    </template>
                </el-table-column>
                <el-table-column prop="total" label=""></el-table-column>
                <el-table-column prop="ten" label="10"></el-table-column>
                <el-table-column prop="thirty" label="30"></el-table-column>
                <el-table-column prop="hundred" label="100"></el-table-column>
                <el-table-column prop="two_hundred" label="200"></el-table-column>
                <el-table-column prop="five_hundred" label="500"></el-table-column>
                <el-table-column prop="thousand" label="1000"></el-table-column>
                <el-table-column prop="two_thousand" label="2000"></el-table-column>
                <el-table-column prop="three_thousand" label="3000"></el-table-column>
                <el-table-column prop="five_thousand" label="5000"></el-table-column>
                <el-table-column prop="ten_thousand" label="10000"></el-table-column>
            </el-table>
            <!--  -->
            <el-dialog title="" :width="dialogWidth" :visible.sync="dialogVisible" @open="show">
                <div class="chartLine" style="width: 100%;height: 500px;"></div>
            </el-dialog>
        </template>
    </section>
</template>

<script>
/* js */
import Event from "./../../../public_js/event.js";
import { allget } from "../../../api/api";
import store from "../../../vuex/store";
import axios from "axios";
import echarts from "echarts";
export default {
    data() {
        return {
            tableHeight: null, // table
            // 
            formOne: {
                choiceDate: [new Date() - 180 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            searchType: "0",
            listLoading: false, //
            tabData: [], //
            formLabelWidth: "120px", // dialog
            dialogVisible: false,
            dialogWidth: null,
            channelData: {},
            channelId: null,
            chartLine: null,
            chartLineData: {
                title: {
                    text: "vip",
                    subtext: ")  VIP",
                    x: "center"
                },
                tooltip: {
                    trigger: "item",
                    formatter: "{a} <br/>{b} : {c} ({d}%)"
                },
                legend: {
                    orient: "horizontal",
                    x: "center",
                    y: "bottom",
                    data: [
                        "",
                        "",
                        "",
                        "",
                        "",
                        "",
                        "",
                        "",
                    ]
                },
                toolbox: {
                    show: true,
                    feature: {
                        mark: { show: true },
                        dataView: { show: true, readOnly: false },
                        magicType: {
                            show: true,
                            type: ["pie", "funnel"],
                            option: {
                                funnel: {
                                    x: "25%",
                                    width: "50%",
                                    funnelAlign: "left",
                                    max: 1548
                                }
                            }
                        },
                        restore: { show: true },
                        saveAsImage: { show: true }
                    }
                },
                calculable: true,
                series: [
                    {
                        name: "",
                        type: "pie",
                        radius: "55%",
                        center: ["25%", "60%"],
                        data: [
                            { value: '', name: "" },
                            { value: '', name: "" },
                        ],
                    },
                    {
                        name: "vip",
                        type: "pie",
                        radius: "55%",
                        center: ["75%", "60%"],
                        data: [
                            { value: '', name: "" },
                            { value: '', name: "" },
                            { value: '', name: "" },
                            { value: '', name: "" },
                            { value: '', name: "" },
                            { value: '', name: "" },
                        ]
                    }
                ]
            }
        };
    },
    methods: {
        // 
        searchCondition() {
            var _this = this;
            var obj = {};
            obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0],0);
            obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1],0);
            obj.channel = this.channelId.join(",");
            obj.show_type = this.searchType;
            return obj;
        },
        // 
        getTableData() {
            var _this = this;
            _this.listLoading = true;
            var url = "/NewMoney/getRechargeCountData";
            var params = _this.searchCondition();
            
            // get(params, url)
            allget(params, url)
                .then(res => {
                    _this.listLoading = false;
                    if (res.data.ret) {
                        _this.tabData = res.data.list;
                        // 
                        for(var i = 0; i<res.data.sex.length; i++){
                            if(res.data.sex[i].sex == 2) {
                                _this.chartLineData.series[0].data[1].value = res.data.sex[i].num;
                            }else if(res.data.sex[i].sex == 1) {
                                _this.chartLineData.series[0].data[0].value = res.data.sex[i].num;
                            }
                        }
                        // 
                        for(var i = 0; i<res.data.vip.length; i++) {
                            if(res.data.vip[i].vip_month == 1) {
                                _this.chartLineData.series[1].data[0].value = res.data.vip[i].total;// 
                            }else if(res.data.vip[i].vip_month == 3) {
                                _this.chartLineData.series[1].data[1].value = res.data.vip[i].total;// 
                            }else if(res.data.vip[i].vip_month == 6) {
                                _this.chartLineData.series[1].data[2].value = res.data.vip[i].total;// 
                            }else if(res.data.vip[i].vip_month == 12) {
                                _this.chartLineData.series[1].data[3].value = res.data.vip[i].total;// 
                            }else if(res.data.vip[i].vip_month == 13) {
                                _this.chartLineData.series[1].data[4].value = res.data.vip[i].total;// 
                            }else if(res.data.vip[i].vip_month == 14) {
                                _this.chartLineData.series[1].data[5].value = res.data.vip[i].total;// 
                            }
                        }
                    } else {
                        // ret==0
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(function(error) {
                    console.log(error);
                });
        },
        show() {
            this.$nextTick(function() {
                this.chartLine = echarts.init(
                    document.querySelector(".chartLine")
                );
                document.querySelector(".chartLine").style.border =
                    "1px solid #4488BB";
                this.chartLine.setOption(this.chartLineData);
            });
        },
        chartLineShow() {
            this.dialogVisible = true;
        }
    },
    mounted() {
        var _this = this;
        this.$nextTick(function() {
            _this.tableHeight = searchHeight;
            _this.getTableData();
        });
        _this.dialogWidth = lookWidth * 0.8 + "px"; //dialog80%
        var id = store.state.user.channelid.split(",");
        for (var i = 0; i < id.length; i++) {
            this.channelData[id[i]] = store.state.user.channelname.split(",")[i];
        }
    }
};
</script>

<style lang="css" scoped>
.infomation {
    width: 100%;
    height: 100%;
}
</style>



