<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!-- / -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <!-- <el-form-item>
                    <div class="block">
                        <span class="registerTime"></span>
                        <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                        </el-date-picker>
                    </div>
                </el-form-item> -->
                <!-- <el-form-item class="search-span" style="float:right;">
                    <el-button id="searchBtn" type="primary" @click="getData(0)"></el-button>
                </el-form-item> -->
                <el-form-item>
                    <el-button class="dissolve" type="primary" @click="dialogFormVisible=true"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="dismiss_time" label=""></el-table-column>
                <el-table-column prop="family_id" label="ID"></el-table-column>
                <el-table-column prop="family_name" label=""></el-table-column>
                <el-table-column prop="owner_uid" label="UID"></el-table-column>
                <!-- <el-table-column prop="agent_join_money" label=""></el-table-column> -->
                <el-table-column prop="level" label=""></el-table-column>
                <el-table-column prop="reason" label=""></el-table-column>
                <el-table-column prop="operate_user" label=""></el-table-column>
            </el-table>
            <el-dialog title="" :visible.sync="dialogFormVisible">
                <el-form :model="form">
                    <el-form-item label="ID" :label-width="formLabelWidth">
                        <el-input v-model="form.family_id" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="form.reason" auto-complete="off"></el-input>
                    </el-form-item>
                </el-form>
                
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button type="primary" @click="dissolve()"> </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-col :span="24" class="toolbar">
                <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
                </el-pagination>
            </el-col>
        </template>
    </section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from "axios";
import store from "../../../vuex/store";
export default {
    data() {
        return {
            tableHeight: null, // table
            // formOne: {
            //     startDate: [new Date() - 1 * 24 * 60 * 60 * 1000, new Date()] // ,180
            // },
            page: 0,
            listData: [],
            totalpage: null,
            formLabelWidth: "120px",
            listLoading: false,
            operate_user: null,
            form: {
                family_id: null,
                reason: null
            },
            dialogFormVisible: false,
        };
    },
    methods: {
        //
        handleCurrentChange(val) {
            //0   -1
            this.page = val - 1;
            this.getData();
        },
        // 
        getData(type) {
            var _this = this;
            if (type == 0) {
                _this.page = 0;
            }
            let url = "/NewFamily/getDismissFamilyRecord";
            let param = {
                page: this.page,
                // date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                // date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0)
            };
            allget(param, url)
                .then(res => {
                    this.listData = res.data.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        // 
        dissolve() {
            var _this = this;
            let url = "/NewFamily/dismissFamily";
            let param = {
                family_id: this.form.family_id,
                reason: this.form.reason,
                operate_user: this.operate_user
            };
            allget(param, url)
                .then(res => {
                    if(res.data.ret){
                        _this.dialogFormVisible = false;
                        baseConfig.successTipMsg(_this, res.data.msg);
                    }else{
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = searchPageHeight;
        _this.getData();
        this.operate_user = store.state.user.name;
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
.dissolve {
    margin-left: 20px;
}
</style>



