<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item>
					<span>uid</span>
					<el-input style="width:200px;" placeholder="uid" v-model="uid" clearable>
					</el-input>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-input style="width:200px;" placeholder="" v-model="operate_user" clearable>
					</el-input>
				</el-form-item>
				<el-form-item style="margin-left: 50px;">
					<el-button id="addAuthor" type="primary" @click="dialogFormVisible = true"></el-button>
				</el-form-item>
				<el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="uid" label="UID"></el-table-column>
				<el-table-column prop="register_time" label=""></el-table-column>
				<el-table-column prop="addtime" label=""></el-table-column>
				<el-table-column prop="nickname" label=""></el-table-column>
				<el-table-column prop="lasttime" label=""></el-table-column>
				<el-table-column prop="accumulate_time" label="" sortable></el-table-column>
				<el-table-column prop="total_chat_ticket" label=""></el-table-column>
				<el-table-column prop="total_money" label=""></el-table-column>
				<el-table-column prop="follow_num" label=""></el-table-column>
				<el-table-column prop="fans_num" label=""></el-table-column>
				<el-table-column prop="rand_call_num" label=""></el-table-column>
				<el-table-column prop="operate_user" label=""></el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" :page-size="20" :total=1000 style="float:right; ">
				</el-pagination>
			</el-col>
		</template>
		<el-dialog title="" :visible.sync="dialogFormVisible" center>
			<el-form>
				<el-form-item label="uid" :label-width="formLabelWidth">
					<el-input v-model="addUid" placeholder="uid" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false"> </el-button>
				<el-button type="primary" @click="dialogFormVisible = false,addNewAuthor()"> </el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
import { allget } from "../../../api/api";
import store from '../../../vuex/store.js'
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 90 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            uid: null,
            operate_user: null,
            dialogFormVisible: false,
            addUid: null
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/Anchor/getAnchorOwnData";
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                uid: this.uid,
                operate_user: this.operate_user
            };
            allget(param, url)
                .then(res => {
                    this.listData = res.data.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },
		addNewAuthor() {
			var _this = this;
			let url = '/Anchor/registerAnchorByUid';
			let param = {
				uid: this.addUid,
				operate_user: store.state.user.name,
			}
			allget(param,url)
			.then(res=>{
				if(res.data.ret){
					baseConfig.successTipMsg(_this,res.data.msg)
				}else{
					baseConfig.errorTipMsg(_this,res.data.msg);
				}
			})
			.catch(err=>{
				baseConfig.errorTipMsg(_this,res.msg)
			})
		}
    },
    mounted() {
        var _this = this;
        _this.tableHeight = searchPageHeight;
        _this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



