<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select multiple collapse-tags style="margin-left: 20px;" v-model="formOne.channel" placeholder="">
						<el-option v-for="item in formOne.options" :key="item.id" :label="item.annotation" :value="item.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select v-model="formOne.sex">
						<el-option label="" value=""></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<el-button type="primary" @click="chartLineShow"></el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="date" label="" width="200" sortable ></el-table-column>
				<el-table-column prop="regPlayer" label="" min-width="150" sortable ></el-table-column>
				<el-table-column label="" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.rate==0">--</p>
							<p v-else>{{(scope.row.rate*100).toFixed(2)+'%'}}</p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="2" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.rate2==0">--</p>
							<p v-else>{{(scope.row.rate2*100).toFixed(2)+'%'}}</p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="3" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.rate3==0">--</p>
							<p v-else>{{(scope.row.rate3*100).toFixed(2)+'%'}}</p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="7" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.rate7==0">--</p>
							<p v-else>{{(scope.row.rate7*100).toFixed(2)+'%'}}</p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="15" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.rate15==0">--</p>
							<p v-else>{{(scope.row.rate15*100).toFixed(2)+'%'}}</p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="30" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.rate30==0">--</p>
							<p v-else>{{(scope.row.rate30*100).toFixed(2)+'%'}}</p>
						</div>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<chartLine></chartLine>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
		</template>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
import chartLine from '../../rootGlobal/chartLine.vue'; // 
export default {
	data() {
		return {
			tableHeight: null, // table
			// 
			formOne: {
				choiceDate: [new Date()-7*24*60*60*1000, new Date()], // ,180
				channel: [],
				options: [],
				sex: '',
			},
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			formLabelWidth: '120px', // dialog
			chartLineData: {//
				legend: [],
				xAxis: [],
				series: [[], [], [], [], [], []],
			}, 
		};
	},
	components: {
        chartLine,
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0);
			obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0);
			if(_this.formOne.channel.length) {
				for(var i=0; i<_this.formOne.channel.length; i++) {
					if(i==0) {
						obj.channel += _this.formOne.channel[i];
					} else {
						obj.channel += ','+_this.formOne.channel[i];
					}
				}
			} else {
				obj.channel = '';
			}
			obj.sex = _this.formOne.sex;
			return obj;
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Base/getRetPlayer';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
					_this.listLoading = false;
					if(res.data.ret) {
						// 
						var betweenData = res.data.data;
						_this.totalpage = betweenData.length;
						_this.tabData = betweenData;
						// 
						_this.chartLineData.legend = ['', '2', '3', '7', '15', '30'];
						_this.chartLineData.unit = '%';
						_this.chartLineData.name = '';
						_this.chartLineData.series = [[], [], [], [], [], []];
						_this.chartLineData.xAxis = [];
						for(var j=0; j<betweenData.length; j++) {
							_this.chartLineData.xAxis.push(res.data.data[j].date);
							_this.chartLineData.series[0].push((res.data.data[j].rate*100).toFixed(2));
							_this.chartLineData.series[1].push((res.data.data[j].rate2*100).toFixed(2));
							_this.chartLineData.series[2].push((res.data.data[j].rate3*100).toFixed(2));
							_this.chartLineData.series[3].push((res.data.data[j].rate7*100).toFixed(2));
							_this.chartLineData.series[4].push((res.data.data[j].rate15*100).toFixed(2));
							_this.chartLineData.series[5].push((res.data.data[j].rate30*100).toFixed(2));
						}
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					console.log(error);
				})
			}
		},
		// 
		getChannel() {
			var _this = this;
			_this.listLoading = true;
			var url = '/GlobalSet/getChannel';
			allget('', url).then(res => {
				_this.listLoading = false;
				if(res.data.ret) {
					_this.formOne.options = res.data.data;
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg);
				}
			}).catch(function(error){
				console.log(error);
			})
		},
		// 
		chartLineShow() {
			var _this = this;
			Event.$emit('show-chart-line', {
				data: _this.chartLineData,
			});
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getChannel();
			_this.getTableData();
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



