<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item label="">
					<el-input v-model="find" auto-complete="off"></el-input>
				</el-form-item>
                <el-form-item>
					<el-button type="primary" @click="formTwo.dialogVisible=true;"></el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="create_time" label="" width="150" sortable ></el-table-column>
				<el-table-column prop="sort" label="" width="100"></el-table-column>
				<el-table-column prop="valid_time" label="" width="150"></el-table-column>
				<el-table-column label="" width="400">
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<img :src="scope.row.img_url" alt="" style="width: 300px; height: 200px;">
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="title" label="" width="100"></el-table-column>
				<el-table-column prop="activity_url" label="" min-width="100"></el-table-column>
				<el-table-column label="" width="150">
					<template slot-scope="scope">
						<el-button type="primary" @click.native.prevent="changeOneUserData(scope.$index, tabData)" size="small"></el-button>
						<el-button type="primary" @click.native.prevent="deleteOneUserData(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
			<!-- --dialog -->
			<el-dialog title="" :visible.sync="formOne.dialogVisible">
				<el-form :model="formOne">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input disabled v-model="formOne.id" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.title" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.desc" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.sort" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<div class="block">
							<el-date-picker v-model="formOne.valid_time" type="date" placeholder=""></el-date-picker>
						</div>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<input class="filepic fileinput" @change="uploading($event, 1)" type="file" style="display: block;">
				<img :src="formOne.pic_src" style="width: 300px; height: 200px; margin-top: 20px;"/>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.activity_url" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="editorSure(0)"> </el-button>
					<el-button type="primary" @click="editorSure(1)"> </el-button>
				</div>
			</el-dialog>
			<!-- --dialog -->
			<el-dialog title="" :visible.sync="formTwo.dialogVisible">
				<el-form :model="formTwo">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.title" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.desc" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.sort" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<div class="block">
							<el-date-picker v-model="formTwo.valid_time" type="date" placeholder=""></el-date-picker>
						</div>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<input class="filepic fileinput" @change="uploading($event, 0)" type="file" style="display: block;">
				<img :src="formTwo.pic_src" style="width: 300px; height: 200px; margin-top: 20px;"/>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.activity_url" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="addSure(0)"> </el-button>
					<el-button type="primary" @click="addSure(1)"> </el-button>
				</div>
			</el-dialog>
		</template>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			formOne: { // 
				dialogVisible: false,
				id: '',
				title: '',
				desc: '',
				sort: '',
				valid_time: '',
				pic: '',
				pic_src: '',
				activity_url: '',
			},
			formTwo: { // 
				dialogVisible: false,			
				operation_name: '',
				title: '',
				desc: '',
				sort: '',
				valid_time: '',
				pic: '', 
				pic_src: '', // src
				activity_url: '',
			},
			listLoading: false, //
			find: '', //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.find = _this.find;
			for(var key in obj) { // 
				if(obj[key]=='') {
					//baseConfig.warningTipMsg(_this, '');
					return null;
				}
			}
			return obj; // return
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/GlobalSet/findActivityMessage';
			var params = _this.searchCondition();
			if(params==null) {
				params = '';
			} else {
				// 
			}
			// get(params, url)
			allget(params, url).then(res => {
				// 
				_this.listLoading = false;
				if(res.data.ret) {
					// 
					_this.totalpage = res.data.data.length;
					_this.tabData = res.data.data;
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}

			}).catch(function(error){
				console.log(error);
			})
		},
		// type(0->1->)
        uploading(event, type) {
			var _this = this;
			if(type==0) { // 
				_this.formTwo.pic = event.target.files[0];//
			var windowURL = window.URL || window.webkitURL;
		//url
				_this.formTwo.pic_src = windowURL.createObjectURL(event.target.files[0]);
			} else if(type==1) { // 
				_this.formOne.pic = event.target.files[0];//
			var windowURL = window.URL || window.webkitURL;
		//url
				_this.formOne.pic_src = windowURL.createObjectURL(event.target.files[0]);
			}
    	}, 
		// 
		changeOneUserData(index, rows){
			var _this = this;
			index = index + (_this.page-1)*20; // index
			_this.formOne.id = rows[index].id;
			_this.formOne.title = rows[index].title;
			_this.formOne.desc = rows[index].desc;
			_this.formOne.sort = rows[index].sort;
			_this.formOne.valid_time = rows[index].valid_time;
			_this.formOne.pic_src = rows[index].img_url;
			_this.formOne.activity_url = rows[index].activity_url;
			_this.formOne.dialogVisible = true; // dialog
		},
		// 
		editorSure(type) {
			var _this = this;
			if(type==0) {
				_this.formOne.dialogVisible = false;
			} else if(type==1) {
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('id', _this.formOne.id);
				formData.append('title', _this.formOne.title);
				formData.append('desc', _this.formOne.desc);
				formData.append('sort', _this.formOne.sort);
				formData.append('valid_time', baseConfig.changeDateTime(_this.formOne.valid_time, 0));
				formData.append('pic', _this.formOne.pic);
				formData.append('activity_url', _this.formOne.activity_url);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/editActivityMessage', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formOne.dialogVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			}
		},
		// 
		addSure(type) {
			var _this = this;
			if(type==0) {
				_this.formTwo.dialogVisible = false;
			} else if(type==1) {
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('operation_name', _this.formTwo.operation_name);
				formData.append('title', _this.formTwo.title);
				formData.append('desc', _this.formTwo.desc);
				formData.append('sort', _this.formTwo.sort);
				formData.append('valid_time', baseConfig.changeDateTime(_this.formTwo.valid_time, 0));
				formData.append('pic', _this.formTwo.pic);
				formData.append('activity_url', _this.formTwo.activity_url);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/sendActivityMessage', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formTwo.dialogVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			}
		},
		// 
		deleteOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			// 
			_this.listLoading = true;
			var url = '/GlobalSet/delActivityMessage';
			var params = {
				id: id,
			};
			// get
			allget(params, url).then(res => {
				// 
				_this.listLoading = false;
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, ''); // 
					_this.getTableData();
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}

			}).catch(function(error) {
				console.log(error);
			})
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight; // table
			_this.getTableData(); // table
			_this.formTwo.operation_name = store.state.user.name; // 			
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 190px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



