<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!-- / -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <div class="block">
                        <span class="registerTime"></span>
                        <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item style="margin-left: 100px;">
                    <span></span>
                    <el-select v-model="channelId" multiple filterable collapse-tags style="margin-left: 20px;" placeholder="">
                        <el-option v-for="(item, key) of channelData" :key="item" :label="item" :value="key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item class="search-span" style="float:right;">
                    <el-button id="searchBtn" type="primary" @click="getData()"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="time" label=""></el-table-column>
                <el-table-column prop="annotation" label=""></el-table-column>
                <el-table-column prop="device" label=""></el-table-column>
                <el-table-column prop="register" label=""></el-table-column>
                <!-- <el-table-column prop="channel" label=""></el-table-column> -->
                <el-table-column prop="active" label=""></el-table-column>
                <el-table-column prop="ACCU" label=""></el-table-column>
                <el-table-column prop="consume_user" label=""></el-table-column>
                <el-table-column prop="total_fee" label=""></el-table-column>
                <el-table-column prop="first_consume_user" label=""></el-table-column>
                <el-table-column prop="first_consume_amount" label=""></el-table-column>
                <el-table-column prop="register_rate" label=""></el-table-column>
                <el-table-column prop="register_contrast" label=""></el-table-column>
                <el-table-column prop="register_channel_contrast" label=""></el-table-column>
                <el-table-column prop="PCU" label=""></el-table-column>
                <el-table-column prop="ACU" label=""></el-table-column>
                <el-table-column prop="consume_rate" label=""></el-table-column>
                <el-table-column prop="first_consume_rate" label=""></el-table-column>
                <el-table-column prop="ARPPU" label="ARPPU"></el-table-column>
                <el-table-column prop="ARPU" label="ARPU"></el-table-column>
            </el-table>
            <!--  -->
            <!-- <el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" :page-size="20" :total=1000 style="float:right; ">
				</el-pagination>
			</el-col> -->
        </template>
    </section>
</template>

<script>
import { allget } from "../../../api/api";
import store from "../../../vuex/store";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            channelData: {},
            channelId: null,
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/Base/comprehensiveDataByChannel";
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                channel: this.channelId.join(',')
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        this.listData = res.data.total_data;
                        this.listData = this.listData.concat(res.data.channel_data);
                    } else {
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(this, err.data.msg);
                });
        }
    },
    mounted() {
        this.tableHeight = searchHeight;
        this.getData();
        var id = store.state.user.channelid.split(",");
        var name = store.state.user.channelname.split(","); 
        for(var i = 0; i<id.length; i++){
            this.channelData[id[i]] = name[i];
        }
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



