<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="date" label=""></el-table-column>
                <el-table-column prop="num" label=""></el-table-column>
                <el-table-column prop="heart_num" label=""></el-table-column>
                <el-table-column prop="chat_gold" label=""></el-table-column>
                <el-table-column prop="gift_two_num" label=""></el-table-column>
                <el-table-column prop="gift_one_num" label=""></el-table-column>
            </el-table>
        </template>
    </section>
</template>

<script>
import { allget } from "../../api/api.js";
import axios from "axios";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 100 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/NewActivity/getBlackLoverDataInfo";
            let param = {
            };
            allget(param, url)
                .then(res => {
                    if(res.data.ret){
                        this.listData = res.data.data;
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
    },
    mounted() {
        this.tableHeight = searchPageHeight;
        this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



