<template>
    <!--  -->
    <!-- dom -->
    <section>
        <el-tabs type="border-card" v-model="activeName">
            <el-tab-pane label="" name="first">
                <!--  -->
                <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                    <el-form :inline="true">
                        <el-form-item>
                            <div class="block">
                                <span class="registerTime"></span>
                                <el-date-picker v-model="formOne.startDate1" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                                </el-date-picker>
                            </div>
                        </el-form-item>
                        <el-form-item>
                            <span>UID</span>
                            <el-input style="width:100px;" placeholder="" v-model="room_id" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item>
                            <span>UID</span>
                            <el-input style="width:100px;" placeholder="" v-model="owner_uid" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item style="float:right;margin-right: 100px;">
                            <el-button type="primary" @click="getTbData"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <!---->
				<template>
					<el-table ref="tableHeight" :data="listData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="start_time" label=""></el-table-column>
						<el-table-column prop="room_id" label="ID"></el-table-column>
						<el-table-column prop="room_name"  label=""></el-table-column>
						<el-table-column prop="family_name" label=""></el-table-column>
						<el-table-column prop="owner_uid" label="UID"></el-table-column>
						<el-table-column prop="owner_nickname" label=""></el-table-column>
						<el-table-column prop="room_background_pic" label="">
                            <template slot-scope="scope">
                                <el-popover trigger="hover" placement="left">
                                    <img :src="scope.row.room_background_pic" alt="" style="width: 300px; height: auto;">
                                    <div slot="reference" class="name-wrapper">
                                        <img :src="scope.row.room_background_pic" alt="" style="width: 100px; height: auto;">
                                    </div>
                                </el-popover>
                            </template>
                        </el-table-column>
						<el-table-column prop="room_status" :formatter="judgeRoom" label=""></el-table-column>
						<el-table-column prop="use_time" label=""></el-table-column>
						<el-table-column prop="total_use_time" label=""></el-table-column>
						<el-table-column prop="this_money" label=""></el-table-column>
						<el-table-column prop="total_money" label=""></el-table-column>
						<el-table-column prop="this_man" label=""></el-table-column>
						<el-table-column prop="total_man" label=""></el-table-column>
						<el-table-column prop="total_num" label=""></el-table-column>
						<el-table-column prop="all_total_num" label=""></el-table-column>
						<el-table-column prop="room_status" label="" width="130px">
                            <template slot-scope="scope">
                                <el-button size="small" type="danger" @click="deleteBgImg(scope.$index, scope.row)"></el-button>
                                <div v-if="scope.row.room_status==0">
                                    <el-button size="small" type="danger"  style="margin-top: 2px;" @click="closeRoomModal(scope.$index, scope.row)"></el-button>
                                </div>
                                <div v-else-if="scope.row.room_status==1">
                                    <el-button size="small" type="primary" style="margin-top: 2px;" @click="openRoomModal(scope.$index, scope.row)"></el-button>
                                </div>
                                <el-button size="small" type="primary" style="margin-top: 2px;" @click="editRoom(scope.$index, scope.row)">ID</el-button>
                            </template>
                        </el-table-column>
					</el-table>
                    <!--  -->
                    <el-col :span="24" class="toolbar">
                        <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
                        </el-pagination>
                    </el-col>
                    <!--  -->
                    <el-dialog title="" :visible.sync="formInfo.dialogShow" center :before-close="handleClose">
                        <el-form :model="formInfo">
                            <div><span>--</span>{{roomName}}</div>
                            <el-form-item label="" width="120px">
                                <el-input v-model="formInfo.oppenReason" auto-complete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <span slot="footer" class="dialog-footer">
                            <el-button @click="formInfo.dialogShow = false"> </el-button>
                            <el-button type="primary" @click="sureOpenRoom"> </el-button>
                        </span>
                    </el-dialog>
                    <!--  -->
                    <el-dialog title="" :visible.sync="closeFormInfo.dialogShow" center :before-close="handleClose">
                        <el-form :model="closeFormInfo">
                            <div><span>--</span>{{roomName}}</div>
                            <el-radio v-model="radio" label="0"></el-radio>
                            <el-radio v-model="radio" label="1"></el-radio>
                            <el-form-item label="" width="120px">
                                <el-input v-model="closeFormInfo.oppenReason" auto-complete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <span slot="footer" class="dialog-footer">
                            <el-button @click="closeFormInfo.dialogShow = false"> </el-button>
                            <el-button type="primary" @click="sureCloseRoom"> </el-button>
                        </span>
                    </el-dialog>
                    <!--  -->
                    <el-dialog title="" :visible.sync="editFormInfo.dialogShow" center>
                        <el-form :model="editFormInfo">
                            <div><span>--</span>{{editFormInfo.editRoomName}}</div>
                            <el-form-item label="id" width="120px">
                                <el-input v-model="editFormInfo.editRoomId" style="width: 300px;" auto-complete="off"></el-input>
                            </el-form-item>
                        </el-form>
                        <span slot="footer" class="dialog-footer">
                            <el-button @click="editFormInfo.dialogShow = false"> </el-button>
                            <el-button type="primary" @click="sureEditRoom"> </el-button>
                        </span>
                    </el-dialog>
				</template>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <!--  -->
                <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                    <el-form :inline="true">
                        <el-form-item>
                            <div class="block">
                                <span class="registerTime"></span>
                                <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                                </el-date-picker>
                            </div>
                        </el-form-item>
                        <el-form-item>
                            <span>UID</span>
                            <el-input style="width:100px;" placeholder="" v-model="room_id1" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item>
                            <span>UID</span>
                            <el-input style="width:100px;" placeholder="" v-model="owner_uid1" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item style="float:right;margin-right: 100px;">
                            <el-button type="primary" @click="getTbData1"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <!---->
				<template>
					<el-table ref="tableHeight" :data="listData1" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column prop="operate_time" label=""></el-table-column>
						<el-table-column prop="room_id" label="ID"></el-table-column>
						<el-table-column prop="room_name" label=""></el-table-column>
						<el-table-column prop="family_name" label=""></el-table-column>
						<el-table-column prop="owner_uid" label="UID"></el-table-column>
						<el-table-column prop="nickname" label=""></el-table-column>
						<el-table-column prop="status" :formatter="judgeState" label=""></el-table-column>
						<el-table-column prop="reason" label=""></el-table-column>
						<el-table-column prop="operate_user" label=""></el-table-column>
					</el-table>
                    <!--  -->
                    <el-col :span="24" class="toolbar">
                        <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange1" :page-size="20" :total=1000 :current-page="page1+1" style="float:right; ">
                        </el-pagination>
                    </el-col>
				</template>
            </el-tab-pane>
        </el-tabs>
    </section>
</template>
<script>
import {allget} from '../../../api/api';
import store from '../../../vuex/store.js';
export default {
    data() {
        return {
            sex: "",
            listData: [],
            listData1: [],
            listLoading: false,
            tableHeight: null,
            activeName: "first",
            formOne: {
				startDate: [new Date() - 30 * 24 * 60 * 60 * 1000, new Date()], // ,180
				startDate1: [new Date() - 13 * 24 * 60 * 60 * 1000, new Date()], // ,180
            },
            room_id: null,
            owner_uid: null,
            room_id1: null,
            owner_uid1: null,
            totalpage:null,
            totalpage1:null,
            page: 0,
            page1: 0,
            tableHeight: null, // table
            formInfo: {
                dialogShow: false,
                oppenReason: '', // 
            },
            closeFormInfo: {
                dialogShow: false,
                oppenReason: '', // 
            },
            editFormInfo: {
                dialogShow: false,
                editRoomId: '', // id
                editRoomName: '',// name 
                yun_xin_room_id: '',// id
            },
            roomName: null,// 
            yun_xin_room_id: null,//id
            type: null,// 
            operate_user: null,// 
            radio: '0',
        };
    },
    methods: {
        //
        handleCurrentChange(val) {
            //0   -1
            this.page = val-1;
            this.getTbData();
        },
         //
        handleCurrentChange1(val) {
            //0   -1
            this.page1 = val-1;
            this.getTbData1();
        },
        getTbData() {
            var _this = this;
            let url = '/Family/getLiveBroadCastInfo';
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate1[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate1[1], 0),
                room_id: this.room_id,
                owner_uid: this.owner_uid,
                page: this.page,
            }
            allget(param, url).then(res => {
                if(res.data.ret){
                    this.listData = res.data.data;
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);
                }
            }).catch(err => {
                console.log(err)
            })
        },
        getTbData1(){
            var _this = this;
            let url = '/Family/getLiveBroadCastKickRecord';
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                room_id: this.room_id1,
                owner_uid: this.owner_uid1,
                page: this.page1,
            }
            this.uid1==null||this.uid1==""?delete param.uid1:param.uid1=this.uid1;
            allget(param, url).then(res => {
                if(res.data.ret){
                    this.listData1 = res.data.data;
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);
                }
            }).catch(err => {
                console.log(err)
            })
        },
        // 
        judgeRoom(row){
            if(row.room_status==0){
                return "";
            }else if(row.room_status==1){
                return "";
            }
        },
        // 
        judgeState(row){
            if(row.status==0){
                return "";
            }else if(row.status==1){
                return "";
            }else if(row.status==2){
                return "";
            }
        },
        // 
        deleteBgImg(index,row) {
            var _this = this;
            console.log(row)
            let url = '/Family/delRoomLiveBroadCastBackGround';
            let param = {
                yun_xin_room_id: row.yun_xin_room_id
            }
            allget(param, url).then(res => {
                console.log(res)
                if(res.data.ret){
                    baseConfig.successTipMsg(this, res.data.msg);
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);    
                }
            })
        },
        // before-close
        handleClose(done) {
            this.$confirm('')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        },
        // 
        openRoomModal(index, row){
            this.formInfo.dialogShow = true;
            this.roomName = row.room_name;
            this.yun_xin_room_id = row.yun_xin_room_id;
        },
        // 
        sureOpenRoom(){
            var _this = this;
            let url = '/Family/closeOrOpenLiveBroadCastRoomId';
            let param = {
                yun_xin_room_id: this.yun_xin_room_id,
                type: 2,// 
                reason: this.formInfo.oppenReason,
                operate_user: this.operate_user
            }
            console.log(param);
            allget(param, url).then(res => {
                if(res.data.ret){
                    baseConfig.successTipMsg(this, res.data.msg);
                    _this.formInfo.dialogShow = false;
                    _this.getTbData();
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);  
                }
            })
        },
        // 
        closeRoomModal(index, row){
            this.closeFormInfo.dialogShow = true;
            this.roomName = row.room_name;
            this.yun_xin_room_id = row.yun_xin_room_id;
        },
        // 
        sureCloseRoom() {
            var _this = this;
            let url = "/Family/closeOrOpenLiveBroadCastRoomId";
            let param = {
                yun_xin_room_id: this.yun_xin_room_id,
                type: this.radio,// 
                reason: this.closeFormInfo.oppenReason,
                operate_user: this.operate_user
            }
            console.log(param)
            allget(param, url).then(res => {
                if(res.data.ret){
                    baseConfig.successTipMsg(this, res.data.msg);
                    _this.closeFormInfo.dialogShow = false;
                    _this.getTbData();
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);  
                }
            })
        },
        // 
        editRoom(index, row){
            this.editFormInfo.dialogShow = true;
            this.editFormInfo.editRoomName = row.room_name;
            this.editFormInfo.editRoomId = row.room_id;
            this.editFormInfo.yun_xin_room_id = row.yun_xin_room_id;
        },
        // 
        sureEditRoom(){
            var _this = this;
            let url = '/Family/editLiveBroadCastRoomId';
            let param = {
                yun_xin_room_id: _this.editFormInfo.yun_xin_room_id,
                room_id: this.editFormInfo.editRoomId,
            }
            allget(param, url).then(res => {
                if(res.data.ret){
                    baseConfig.successTipMsg(this, res.data.msg);
                    _this.editFormInfo.dialogShow = false;
                    _this.getTbData();
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);  
                }
            })
        }
     },
    mounted() {
        this.tableHeight = tabSearchPageHeight;
        this.getTbData();        
        this.getTbData1();
        this.operate_user = store.state.user.name;//     
    }
};
</script>
<style lang="css" scoped>

</style>




