<template>
    <!--  -->
    <!-- dom -->
	<section>
        <!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
                <el-form-item class="search-span">
                    <el-button type="primary" @click="openRecordingRecommend"></el-button>
                </el-form-item>
                <el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="time" label=""></el-table-column>
				<el-table-column prop="share_sort"  label=""></el-table-column>
				<el-table-column prop="id" label="" ></el-table-column>
                <el-table-column label="" width="300">
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<audio controls="controls" :src="scope.row.voice_url"></audio>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="uid" label="ID"></el-table-column>
				<el-table-column prop="content"  label=""></el-table-column>
				<el-table-column prop="share_listen" label=""></el-table-column>
				<el-table-column label="" min-width="120">
					<template slot-scope="scope">
                        <el-col :span="12"><el-button size="small" type="danger" @click="cancleRecommend(scope.$index, scope.row)"></el-button></el-col>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
				</el-pagination>
			</el-col>
            <el-dialog title="" class="recordPopup" :visible.sync="dialogFormVisible" center style="text-align:center;">
				<el-form :model="formInfo">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" id="withdraw_money" v-model="withdrawMoneyValue" style="width:240px;" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" id="withdraw_day" v-model="withdrawDayValue" style="width:240px;" placeholder=""></el-input>
					</el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
						<el-input auto-complete="off" id="numpeople" v-model="numpeopleValue" style="width:240px;" placeholder=""></el-input>
					</el-form-item>
				</el-form>
                <span slot="footer" id="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button type="primary" @click="addShareHotVoice()"> </el-button>
                </span>
			</el-dialog>
		</template>
    </section>
</template>

<script>
	import { allget } from '../../../api/api';
	import axios from 'axios';
    export default {
        data() {
            return {
				tableHeight: null, // table
                formOne: {
                    startDate: [new Date()-14*24*60*60*1000, new Date()], // ,180
				},
				page: 0,
				listData: [],
				totalpage:null,
				formLabelWidth: '100px',
                listLoading: false,
                formInfo: {

                },
                dialogFormVisible: false,
				withdrawMoneyValue: "",
				withdrawDayValue: "",
				numpeopleValue: ""
            }
        },
		methods: {
			//
			handleCurrentChange(val) {
				//0   -1
				this.page = val-1;
				this.getData();
			},
			// 
			getData() {
				let _this = this;
				let url = '/Voice/getShareHotVoiceData';
				let param ={
					date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
					date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
				}
				allget(param, url).then(res => {
					this.listData = res.data.data;
				}).catch(err => {
					console.log(err)
				})
            },
			// 
			addShareHotVoice() {
				let _this = this;
				let url = '/Voice/addShareHotVoice';
				let param ={
					id: _this.withdrawMoneyValue,
					share_sort: withdrawDayValue,
					cheat_listen: numpeopleValue
				}
				_this.listLoading = true;
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
			},
            // 
            cancleRecommend() {
                let _this = this;
				const id = row.id;
				const url = '/Voice/cancelShareHotVoiceData';
				const params = {
					id: id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
            },
            // 
            cancleDialogFormVisible() {
                this.dialogFormVisible = false;
            },
            // 
            openRecordingRecommend() {
                this.dialogFormVisible = true;
            }
		},
		mounted() {
			var _this = this;
			_this.tableHeight = searchPageHeight;
			_this.getData();
		}
    }

</script>

<style lang="css" scoped>
    #searchBtn{
        margin-right: 50px;
    }
	.recordPopup{
		 text-align: center;
		 width: 1000px;
		 margin: 0 auto;
	}
	#dialog-footer{
		text-align: center;
		margin: 0 auto;
	}
	.el-dialog__footer{
		text-align: center;
	}
</style>



