<template>
    <!--  -->
    <!-- dom -->
    <section>
        <el-tabs type="border-card" v-model="activeName">
            <el-tab-pane label="" name="first">
                <!--  -->
                <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                    <el-form :inline="true">
                        <el-form-item>
                            <div class="block">
                                <span class="registerTime"></span>
                                <el-date-picker v-model="formOne.startDate1" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                                </el-date-picker>
                            </div>
                        </el-form-item>
                        <el-form-item>
                            <span>uid</span>
                            <el-input style="width:100px;" placeholder="" v-model="uid" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item style="float:right;margin-right: 100px;">
                            <el-button type="primary" @click="getTbData(0)"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <!---->
				<template>
					<el-table ref="tableHeight" :data="listData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column type="index" label="" center></el-table-column>
						<el-table-column prop="uid" label="UID"></el-table-column>
						<el-table-column prop="phone"  label=""></el-table-column>
						<el-table-column prop="nickname" label=""></el-table-column>
						<el-table-column prop="total_spread_num" label=""></el-table-column>
						<el-table-column prop="one_num" label=""></el-table-column>
						<el-table-column prop="two_num" label=""></el-table-column>
						<el-table-column prop="three_num" label=""></el-table-column>
						<el-table-column prop="turn_agent_num" label=""></el-table-column>
						<el-table-column prop="total_agent_money" label=""></el-table-column>
					</el-table>
                    <!--  -->
                    <el-col :span="24" class="toolbar">
                        <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
                        </el-pagination>
                    </el-col>
				</template>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <!--  -->
                <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                    <el-form :inline="true">
                        <el-form-item>
                            <div class="block">
                                <span class="registerTime"></span>
                                <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                                </el-date-picker>
                            </div>
                        </el-form-item>
                        <el-form-item>
                            <span>uid</span>
                            <el-input style="width:100px;" placeholder="" v-model="uid1" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item style="float:right;margin-right: 100px;">
                            <el-button type="primary" @click="getTbData1(0)"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <!---->
				<template>
					<el-table ref="tableHeight" :data="listData1" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column type="index" label=""></el-table-column>
						<el-table-column prop="uid" label="UID"></el-table-column>
						<el-table-column prop="username"  label=""></el-table-column>
						<el-table-column prop="nickname" label=""></el-table-column>
						<el-table-column prop="spread_num" label=""></el-table-column>
						<el-table-column prop="turn_agent_num" label=""></el-table-column>
					</el-table>
                    <!--  -->
                    <el-col :span="24" class="toolbar">
                        <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange1" :page-size="20" :total=1000 :current-page="page1+1" style="float:right; ">
                        </el-pagination>
                    </el-col>
				</template>
            </el-tab-pane>
        </el-tabs>
    </section>
</template>
<script>
import {allget} from '../../../api/api';
export default {
    data() {
        return {
            sex: "",
            listData: [],
            listData1: [],
            listLoading: false,
            tableHeight: null,
            activeName: "first",
            formOne: {
				startDate: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()], // ,180
				startDate1: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()], // ,180
            },
            totalpage:null,
            totalpage1:null,
            page: 0,
            page1: 0,
            tableHeight: null, // table
            uid: null,
            uid1: null,
        };
    },
    methods: {
        //
        handleCurrentChange(val) {
            //0   -1
            this.page = val-1;
            this.getTbData();
        },
         //
        handleCurrentChange1(val) {
            //0   -1
            this.page1 = val-1;
            this.getTbData1();
        },
        getTbData(arg) {
            if(arg==0){
                this.page = 0;
            }
            var _this = this;
            let url = '/Agent/getAgentRankList';
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate1[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate1[1], 0),
                uid: this.uid,
                page: this.page,
                type: 1,// 
            }
            allget(param, url).then(res => {
                if(res.data.ret){
                    this.listData = res.data.data;
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);
                }
            }).catch(err => {
                console.log(err)
            })
        },
        getTbData1(arg){
            if(arg==0){
                this.page1 = 0;
            }
            var _this = this;
            let url = '/Agent/getAgentRankList';
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                uid: this.uid1,
                page: this.page1,
                type: 0,// 
            }
            this.uid1==null||this.uid1==""?delete param.uid1:param.uid1=this.uid1;
            allget(param, url).then(res => {
                if(res.data.ret){
                    this.listData1 = res.data.data;
                }else{
                    baseConfig.errorTipMsg(this, res.data.msg);
                }
            }).catch(err => {
                console.log(err)
            })
        },
     },
    mounted() {
        this.tableHeight = tabSearchPageHeight;
        this.getTbData();        
        this.getTbData1();
    }
};
</script>
<style lang="css" scoped>

</style>




