<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
					</div>
				</el-form-item>
                <el-form-item>
                    <span></span>
                    <el-select v-model="channelId" multiple filterable collapse-tags style="margin-left: 20px;" placeholder="">
                        <el-option v-for="(item, key) of channelData" :key="item" :label="item" :value="key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item style="margin-left: 100px;">
					<el-button type="primary" @click="chartLineShow"></el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
                <el-form-item style="margin-left: 100px;">
                    <div class="infomation"  @click="infoDialogVisible=true;">
                        <i class="el-icon-question" style="color: #999;"></i>
                    </div>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="tabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="every_day" label="" width="90" sortable ></el-table-column>
				<el-table-column prop="second_1_3" label="1-3" min-width="60"></el-table-column>
				<el-table-column prop="second_4_10" label="4-10" min-width="60"></el-table-column>
				<el-table-column prop="second_11_30" label="11-30" min-width="60"></el-table-column>
				<el-table-column prop="second_31_59" label="31-59" min-width="60"></el-table-column>
				<el-table-column prop="min_1_3" label="1-3" min-width="60"></el-table-column>
				<el-table-column prop="min_3_10" label="3-10" min-width="60"></el-table-column>
				<el-table-column prop="min_10_30" label="10-30" min-width="60"></el-table-column>
				<el-table-column prop="min_30_60" label="30-60" min-width="60"></el-table-column>
				<el-table-column prop="hour_1_2" label="1-2" min-width="60"></el-table-column>
				<el-table-column prop="hour_2_5" label="2-5" min-width="60"></el-table-column>
				<el-table-column prop="hour_5_10" label="5-10" min-width="60"></el-table-column>
				<el-table-column prop="hour_10" label="10" min-width="60"></el-table-column>
			</el-table>
			<!--  -->
			<el-dialog title="" :width="dialogWidth"  :visible.sync="dialogVisible" @open="show">
                <div class="chartLine"  style="width: 100%;height: 600px;"></div>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :width="dialogWidth"  :visible.sync="infoDialogVisible" >
                <p>030()</p>
                <p>030()</p>
            </el-dialog>
		</template>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
import echarts from 'echarts';
export default {
	data() {
		return {
			tableHeight: null, // table
			// 
			formOne: {
				choiceDate: [new Date()-7*24*60*60*1000, new Date()], // ,180
			},
			listLoading: false, //
			tabData: [], 
			formLabelWidth: '120px', // dialog
            dialogVisible: false,
            infoDialogVisible: false,
            dialogWidth: null,
            channelData: {},
            channelId: null,
            chartLine: null,
            chartLineData: {
                title: {
                        text: ''
                    },
                tooltip : {
                    trigger: 'axis'
                },
                toolbox: {
                    show : true,
                    feature : {
                        mark : {show: true},
                        dataView : {show: true, readOnly: false},
                        magicType: {show: true, type: ['line', 'bar']},
                        restore : {show: true},
                        saveAsImage : {show: true}
                    }
                },
                calculable : true,
                legend: {
                    data:['ARPPU','ARPU','','']
                },
                xAxis: [
                    {
                        type: 'category',
                        data: []
                    }
                ],
                yAxis: [
                    {
                        type : 'value',
                        name : 'ARPU',
                        axisLabel : {
                            formatter: '{value} '
                        }
                    },
                    {
                        type : 'value',
                        name : '',
                        axisLabel : {
                            formatter: '{value} %'
                        }
                    },
                ],
                series: [
                    {
                        name:'ARPPU',
                        type:'bar',
                        data:[]
                    },
                    {
                        name:'ARPU',
                        type:'bar',
                        data:[]
                    },
                    {
                        name:'',
                        type:'line',
                        yAxisIndex: 1,
                        data:[]
                    },
                    {
                        name:'',
                        type:'line',
                        yAxisIndex: 1,
                        data:[]
                    }
                ]
            }
		};
	},
	methods: {
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
            obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0);
            obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0);
            obj.channel = this.channelId.join(',')
            // obj.date_s = "2018-03-19";
            // obj.date_e = "2018-03-19";
			return obj;
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Base/onlineDistribute';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
                    _this.listLoading = false;
					if(res.data.ret) {
                        _this.tabData = res.data.data;
                        // 
                        // 
                        _this.chartLineData.xAxis[0].data = [];
                        _this.chartLineData.series[0].data = [];
                        _this.chartLineData.series[1].data = [];
                        _this.chartLineData.series[2].data = [];
                        _this.chartLineData.series[3].data = [];
                        for(var i = res.data.data.length-1; i >= 0; i--){
                            _this.chartLineData.xAxis[0].data.push(_this.tabData[i].time); // 
                            _this.chartLineData.series[0].data.push(_this.tabData[i].consume_ARPPU); // ARPPU
                            _this.chartLineData.series[1].data.push(_this.tabData[i].active_ARPPU); // ARPU
                            _this.chartLineData.series[2].data.push(_this.tabData[i].consume_rate); // 
                            _this.chartLineData.series[3].data.push(_this.tabData[i].cumulative_consume_rate); // 
                        }
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					console.log(error);
				})
			}
        },
        show() {
            this.$nextTick(function() {
                this.chartLine = echarts.init(document.querySelector('.chartLine'));
                document.querySelector('.chartLine').style.border = "1px solid #4488BB";
                this.chartLine.setOption(this.chartLineData);
            })
        },
        chartLineShow() {
            this.dialogVisible = true;
        }
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getTableData();
        })
        _this.dialogWidth = lookWidth*0.8+'px'; //dialog80%
        var id = store.state.user.channelid.split(",");
        for(var i = 0; i<id.length; i++){
            this.channelData[id[i]] = store.state.user.channelname.split(",")[i];
        }
	}
};
</script>

<style lang="css" scoped>
   .infomation{
       width: 100%;
       height: 100%;
   }
</style>



