<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select v-model="formOne.type">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select v-model="formOne.status">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<el-button type="primary" @click="dialogFormVisible=true;"></el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="tabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="operate_time" label="" width="80" sortable ></el-table-column>
				<el-table-column prop="start_time" label="" width="80" sortable ></el-table-column>
				<el-table-column prop="end_time" label="" width="80" sortable ></el-table-column>
				<el-table-column prop="content" label="" min-width="300" sortable ></el-table-column>
				<el-table-column label="" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.link_type==0"></p>
							<p v-else-if="scope.row.link_type==1"></p>
							<p v-else-if="scope.row.link_type==2">H5</p>
							<p v-else-if="scope.row.link_type==3"></p>
							<p v-else-if="scope.row.link_type==4"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="ios_link" label="IOS" width="200" sortable ></el-table-column>				
				<el-table-column prop="android_link" label="" width="200" sortable ></el-table-column>				
				<el-table-column label="" width="100">
					<template slot-scope="scope">
						<el-button v-if="scope.row.status=='1'" plain size="small" @click.native.prevent="tipEnd()"></el-button>
						<el-button v-else-if="scope.row.status=='0'" type="primary" @click.native.prevent="endSure(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" :current-page="page+1" style="float:right;"></el-pagination>
			</el-col>
		</template>
		<!-- --dialog -->
		<el-dialog title="" :visible.sync="dialogFormVisible">
			<el-form :model="formTwo">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input type="textarea" :rows="2" :maxlength="50" v-model="formTwo.content" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<p style="font-weight: bold;">{messageDisplay}</p>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="formTwo.link_type">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="H5" value="2"></el-option>
						<el-option label="" value="3"></el-option>
						<el-option label="" value="4"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="IOS" :label-width="formLabelWidth">
					<el-input v-model="formTwo.ios_link" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formTwo.android_link" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formTwo.display" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formTwo.choiceDate" type="datetimerange" range-separator="  " start-placeholder="" end-placeholder=""></el-date-picker>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addSure(0)"> </el-button>
				<el-button type="primary" @click="addSure(1)"> </el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			operate_user: '', // 
			// 
			formOne: {
				choiceDate: [new Date()-180*24*60*60*1000, new Date()], // ,180
				type: '0', 
				status: '0',
			},
			formTwo: {
				choiceDate: [new Date(), new Date()],
				content: '',
				link_type: '0',
				ios_link: '',
				android_link: '',
				display: '',
			},
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 0, //
			dialogFormVisible: false, // dialog
			formLabelWidth: '130px', // dialog
		};
	},
	methods: {
		// 
		handleCurrentChange(val) {
			var _this = this;
			// val
			_this.page = val-1;
			_this.getTableData();
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.type = _this.formOne.type; // 
			obj.status = _this.formOne.status; // 
			obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0);
			obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0);
			// 
			for(var key in obj) {
				if(obj[key]=='') {
					baseConfig.warningTipMsg(_this, '');
					return null;
				}
			}
			obj.page = _this.page; // page==0
			return obj; // return
		},
		// (val0->1->)
		addSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.dialogFormVisible = false; // dialog
				// console.log('');
			} else if(val==1) {
				// 
				// console.log('');
				_this.listLoading = true;
				// 
				let formData = new FormData();
				formData.append('content', encodeURI(_this.formTwo.content));
				formData.append('link_type', _this.formTwo.link_type);
				formData.append('ios_link', _this.formTwo.ios_link);
				formData.append('android_link', _this.formTwo.android_link);
				formData.append('display', _this.formTwo.display);
				formData.append('start_time', baseConfig.changeDateTime(_this.formTwo.choiceDate[0], 1));
				formData.append('end_time', baseConfig.changeDateTime(_this.formTwo.choiceDate[1], 1));
				formData.append('operate_user', _this.operate_user);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};
				if(formData.get('content')!=''&&formData.get('start_time')!=''&&formData.get('end_time')!='') {
					axios.post(baseConfig.server+baseConfig.requestUrl+'/Marquee/sendOperationMarquee', formData, config)
					.then((res) => {
						_this.listLoading = false;	
						_this.dialogFormVisible = false;								
						if(res.data.ret) {	
							baseConfig.successTipMsg(_this, '');
							_this.getTableData();
						} else {
							baseConfig.errorTipMsg(_this, res.data.msg);						
						}
					}).catch((error) => {
						console.log(error);
					});
				} else {
					_this.listLoading = false; 
					baseConfig.warningTipMsg(_this, '');
				}
			} 
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Marquee/getOperationMarquee';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
					_this.listLoading = false;
					if(res.data.ret) {
						// 
						var thisDate = baseConfig.changeDateTime(new Date(), 1);
						for(var i=0; i<res.data.data.length; i++) { // 
							res.data.data[i].content = decodeURI(res.data.data[i].content);
							// 
							if(res.data.data[i].status==0&&res.data.data[i].end_time<thisDate) {
								res.data.data[i].status = 1;
							} else {
								// 
							}
							// 
							res.data.data[i].content = res.data.data[i].content.replace(/<font  color="#809ebd">/g, '');
							res.data.data[i].content = res.data.data[i].content.replace(/<font color="#809ebd">/g, '');
							res.data.data[i].content = res.data.data[i].content.replace(/<\/font>/g, '');
							res.data.data[i].content = res.data.data[i].content.replace(/<font  color="#ffffff">/g, '');
						}
						_this.totalpage = res.data.data.length;
						_this.tabData = res.data.data;
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					console.log(error);
				})
			}
		},
		// 
		tipEnd() {
			var _this = this;
			baseConfig.warningTipMsg(_this, '');						
		},
		// 
		endSure(index, rows) {
			var _this = this;
			var id = rows[index].id;
			var url = '/Marquee/killOperationMarquee';
			var params = {
				id: id,
			};
			allget(params, url).then(res => { // get(params, url)
				// 
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, '');
					_this.getTableData();
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
				}
			}).catch(function(error){
				baseConfig.errorTipMsg(_this, error);
			})
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getTableData();
			_this.operate_user = store.state.user.name; // 
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



