<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select v-model="formOne.send_status">
						<el-option label="" value=""></el-option>
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<el-button type="primary" @click="formCondition.dialogFormVisible=true;"></el-button>
					<el-button type="primary" @click="formUid.dialogFormVisible=true;">UID</el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="time" label="" width="150" sortable ></el-table-column>
				<el-table-column prop="send_time" label="" width="150" sortable ></el-table-column>
				<el-table-column prop="uid_list" label="" width="150" sortable ></el-table-column>
				<el-table-column label="" width="80" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.address==0"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="" width="80" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.condition_sex==0"></p>
							<p v-else-if="scope.row.condition_sex==1"></p>
							<p v-else-if="scope.row.condition_sex==2"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="content" label="" min-width="150" sortable ></el-table-column>
				<el-table-column prop="operation_name" label="" width="150" sortable ></el-table-column>
				<el-table-column label="" width="150" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.send_status==0"></p>
							<p v-else-if="scope.row.send_status==1"></p>
							<p v-else-if="scope.row.send_status==2"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="" width="100" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.is_timing==0"></p>
							<p v-else-if="scope.row.is_timing==1"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="" width="100">
					<template slot-scope="scope">
						<el-button v-if="scope.row.send_status==0" type="primary" @click.native.prevent="cacelOneUserData(scope.$index, tabData)" size="small"></el-button>
						<p v-else-if="scope.row.send_status==1||scope.row.send_status==2"></p>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
		</template>
		<!-- --dialog -->
		<el-dialog title="" :visible.sync="formCondition.dialogFormVisible">
			<el-form :model="formCondition">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formCondition.condition_sex" label="0"></el-radio>
					<el-radio v-model="formCondition.condition_sex" label="1"></el-radio>
					<el-radio v-model="formCondition.condition_sex" label="2"></el-radio>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formCondition.registerTime" type="daterange" range-separator="  " start-placeholder="" end-placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formCondition.loginTime" type="daterange" range-separator="  " start-placeholder="" end-placeholder=""></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="formCondition.address">
						<el-option label="" value="0"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formCondition.channel" label="0"></el-radio>
					<el-radio v-model="formCondition.channel" label="1">ios</el-radio>
					<el-radio v-model="formCondition.channel" label="2">android</el-radio>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formCondition.version_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="formCondition.identity">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="vip" value="2"></el-option>
						<el-option label="" value="3"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input type="textarea" :rows="2" :maxlength="50" v-model="formCondition.content" auto-complete="off"></el-input>
					<p style="font-weight: bold;">{messageDisplay}</p>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formCondition.display" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="formCondition.Link_type">
						<el-option label="" value="1"></el-option>
						<el-option label="H5" value="2"></el-option>
						<el-option label="" value="3"></el-option>
						<el-option label="" value="4"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="ios" :label-width="formLabelWidth">
					<el-input v-model="formCondition.ios_link" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formCondition.android_link" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formCondition.is_timing" label="0"></el-radio>
					<el-radio v-model="formCondition.is_timing" label="1"></el-radio>
				</el-form-item>
				<el-form-item v-if="formCondition.is_timing==1" label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formCondition.send_time" type="datetime" placeholder=""></el-date-picker>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="conditionSure(0)"> </el-button>
				<el-button type="primary" @click="conditionSure(1)"> </el-button>
			</div>
		</el-dialog>
		<!-- uid--dialog -->
		<el-dialog title="Uid" :visible.sync="formUid.dialogFormVisible">
			<el-form :model="formUid">
				<el-form-item label="Uid" :label-width="formLabelWidth">
					<el-input type="textarea" :rows="2" :maxlength="50" v-model="formUid.uid_list" auto-complete="off"></el-input>
					<p>Uid(:10000,10001,12201)</p>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input type="textarea" :rows="2" :maxlength="50" v-model="formUid.content" auto-complete="off"></el-input>
					<p style="font-weight: bold;">{messageDisplay}</p>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formUid.display" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="formUid.Link_type">
						<el-option label="" value="1"></el-option>
						<el-option label="H5" value="2"></el-option>
						<el-option label="" value="3"></el-option>
						<el-option label="" value="4"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="ios" :label-width="formLabelWidth">
					<el-input v-model="formUid.ios_link" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formUid.android_link" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-radio v-model="formUid.is_timing" label="0"></el-radio>
					<el-radio v-model="formUid.is_timing" label="1"></el-radio>
				</el-form-item>
				<el-form-item v-if="formUid.is_timing==1" label="" :label-width="formLabelWidth">
					<div class="block">
						<el-date-picker v-model="formUid.send_time" type="datetime" placeholder=""></el-date-picker>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="uidSure(0)"> </el-button>
				<el-button type="primary" @click="uidSure(1)"> </el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			// 
			formOne: {
				choiceDate: [new Date()-180*24*60*60*1000, new Date()], // ,180
				send_status: '',
			},
			formCondition: {
				dialogFormVisible: false, // dialog
				appoint_type: '0', //0
				// 
				condition_sex: '0', //
				registerTime: [new Date()-180*24*60*60*1000, new Date()], //
				loginTime: [new Date()-180*24*60*60*1000, new Date()], //
				addtime_start: '', //
				addtime_end: '', //
				lasttime_start: '', //
				lasttime_end: '', //
				address: '0', //
				channel: '0', //ios
				version_name: '', //
				identity: '0', //
				// 
				// *****
				uid_list: ' ', //uid
				// *****
				content_type: '0', //0,1
				content: '', //
				display: '', //
				Link_type: '1', //
				ios_link: '', //ios
				android_link: '', //
				is_timing: '0', //0->1->
				send_time: '', //
				operation_name: '', //
			},
			formUid: {
				dialogFormVisible: false, // dialog
				appoint_type: '1', //uid1
				// *****
				condition_sex: '0', //
				addtime_start: '', //
				addtime_end: '', //
				lasttime_start: '', //
				lasttime_end: '', //
				address: '0', //
				channel: '0', //ios
				version_name: '', //
				identity: '0', //
				// *****
				// 
				uid_list: ' ', //uid
				// 
				content_type: '0', //0,1
				content: '', //
				display: '', //
				Link_type: '1', //
				ios_link: '', //ios
				android_link: '', //
				is_timing: '0', //0->1->
				send_time: '', //
				operation_name: '', //
			},
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0); // 
			obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0); // 
			obj.send_status = _this.formOne.send_status; // 
			// 
			for(var key in obj) {
				if(key=='send_status') {
					// 
				} else {
					if(obj[key]=='') {
						baseConfig.warningTipMsg(_this, '');
						return null;
					}
				}
			}
			return obj; // return
		},
		// 
		conditionSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.formCondition.dialogFormVisible = false; // dialog
				// console.log('');
			} else if(val==1) {
				// 
				// console.log('');
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('condition_sex', _this.formCondition.condition_sex);
				formData.append('addtime_start', baseConfig.changeDateTime(_this.formCondition.registerTime[0], 0));
				formData.append('addtime_end', baseConfig.changeDateTime(_this.formCondition.registerTime[1], 0));
				formData.append('lasttime_start', baseConfig.changeDateTime(_this.formCondition.loginTime[0], 0));
				formData.append('lasttime_end', baseConfig.changeDateTime(_this.formCondition.loginTime[1], 0));
				formData.append('address', _this.formCondition.address);
				formData.append('appoint_type', _this.formCondition.appoint_type);
				formData.append('uid_list', _this.formCondition.uid_list);
				formData.append('content_type', _this.formCondition.content_type);
				formData.append('content', _this.formCondition.content);
				formData.append('display', _this.formCondition.display);
				formData.append('Link_type', _this.formCondition.Link_type);
				formData.append('ios_link', _this.formCondition.ios_link);
				formData.append('android_link', _this.formCondition.android_link);
				formData.append('operation_name', _this.formCondition.operation_name);
				formData.append('channel', _this.formCondition.channel);
				formData.append('version_name', _this.formCondition.version_name);
				formData.append('is_timing', _this.formCondition.is_timing);
				formData.append('send_time', baseConfig.changeDateTime(_this.formCondition.loginTime, 1));
				formData.append('identity', _this.formCondition.identity);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/sendSystemMessage', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formCondition.dialogFormVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			} 
		},
		// uid
		uidSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.formUid.dialogFormVisible = false; // dialog
				// console.log('');
			} else if(val==1) {
				// 
				// console.log('');
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('condition_sex', _this.formUid.condition_sex);
				formData.append('addtime_start', '');
				formData.append('addtime_end', '');
				formData.append('lasttime_start', '');
				formData.append('lasttime_end', '');
				formData.append('address', _this.formUid.address);
				formData.append('appoint_type', _this.formUid.appoint_type);
				formData.append('uid_list', _this.formUid.uid_list);
				formData.append('content_type', _this.formUid.content_type);
				formData.append('content', _this.formUid.content);
				formData.append('display', _this.formUid.display);
				formData.append('Link_type', _this.formUid.Link_type);
				formData.append('ios_link', _this.formUid.ios_link);
				formData.append('android_link', _this.formUid.android_link);
				formData.append('operation_name', _this.formUid.operation_name);
				formData.append('channel', _this.formUid.channel);
				formData.append('version_name', _this.formUid.version_name);
				formData.append('is_timing', _this.formUid.is_timing);
				formData.append('send_time', baseConfig.changeDateTime(_this.formUid.loginTime, 1));
				formData.append('identity', _this.formUid.identity);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/sendSystemMessage', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formUid.dialogFormVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			} 
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/GlobalSet/findSystemMessageLog';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
					_this.listLoading = false;
					if(res.data.ret) {
						// 
						_this.totalpage = res.data.data.length;
						_this.tabData = res.data.data;
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}

				}).catch(function(error){
					console.log(error);
				})
			}
		},
		// 
		cacelOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			// 
			_this.listLoading = true;
			let formData = new FormData();
			formData.append('id', id);
			let config = {
				headers: {
					'Content-Type': 'multipart/form-data'
				}
			};		
			axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/delSystemMessage', formData, config)
			.then((res) => {
				_this.listLoading = false;	
				if(res.data.ret) {	
					baseConfig.successTipMsg(_this, '');
					_this.getTableData();
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg);						
				}
			}).catch((error) => {
				console.log(error);
			});
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getTableData();
			_this.formCondition.operation_name = store.state.user.name; // 
			_this.formUid.operation_name = store.state.user.name; // 
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



