<template>
    <!--  -->
    <!-- dom -->
	<section>
        <!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
                <el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="time" label=""></el-table-column>
				<el-table-column prop="complain"  label=""></el-table-column>
				<el-table-column prop="id" label="" ></el-table-column>
				<el-table-column prop="uid" label="" ></el-table-column>
                <el-table-column label="" width="300">
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<audio controls="controls" :src="scope.row.voice_url"></audio>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="content"  label=""></el-table-column>
				<el-table-column label="" min-width="120">
					<template slot-scope="scope">
                        <div v-if="scope.row.status==0">
                            <el-col :span="8"><el-button size="warning" type="info"></el-button></el-col>
                        </div>
                        <div v-else-if="scope.row.status==1">
                            <el-col :span="8"><el-button size="primary" type="danger" @click="dealVoiceOffData(scope.$index, scope.row)"></el-button></el-col>
                        </div>
                        <div v-else>
                            <el-col :span="8"><el-button size="small" type="danger" @click="deleteVoiceComplain(scope.$index, scope.row)"></el-button></el-col>
                        </div>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
				</el-pagination>
			</el-col>
		</template>
    </section>
</template>

<script>
	import { allget } from '../../../api/api';
	import axios from 'axios';
    export default {
        data() {
            return {
				tableHeight: null, // table
                formOne: {
                    startDate: [new Date()-14*24*60*60*1000, new Date()], // ,180
				},
				page: 0,
				listData: [],
				totalpage:null,
				formLabelWidth: '100px',
                listLoading: false,
               
            }
        },
		methods: {
			//
			handleCurrentChange(val) {
				//0   -1
				this.page = val-1;
				this.getData();
			},
			// 
			getData() {
				let _this = this;
				let url = '/Voice/getVoiceOffData';
				let param ={
					date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
					date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
				}
				allget(param, url).then(res => {
					this.listData = res.data.data;
				}).catch(err => {
					console.log(err)
				})
            },
            // 
            dealVoiceOffData() {
                let _this = this;
                const id = row.id;
                const url = '/Voice/dealVoiceOffData';
                const params = {
                    id: id,
                };
                _this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
            },
            // 
            deleteVoiceComplain() {
                let _this = this;
                const id = row.id;
                const url = '/Voice/deleteVoiceComplain';
                const params = {
                    id: id,
                };
                _this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
            }
		},
		mounted() {
			var _this = this;
			_this.tableHeight = searchPageHeight;
			_this.getData();
		}
    }

</script>

<style lang="css" scoped>
    
</style>



