<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<span></span>
					<el-select v-model="formOne.position">
						<el-option label="" value=""></el-option>
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
						<el-option label="" value="3"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<el-button type="primary" @click="dialogFormVisible=true;"></el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="sort" label="" width="100" sortable ></el-table-column>
				<el-table-column prop="content" label="" min-width="400" sortable ></el-table-column>
				<el-table-column label="" width="400" sortable >
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<p v-if="scope.row.position==0"></p>
							<p v-else-if="scope.row.position==1"></p>
							<p v-else-if="scope.row.position==2"></p>
							<p v-else-if="scope.row.position==3"></p>
						</div>
					</template>
				</el-table-column>
				<el-table-column label="" width="200">
					<template slot-scope="scope">
						<el-button type="primary" @click.native.prevent="deleteOneUserData(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
		</template>
		<!-- --dialog -->
		<el-dialog title="" :visible.sync="dialogFormVisible">
			<el-form :model="formTwo">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formTwo.content" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-select v-model="formTwo.position">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
						<el-option label="" value="3"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formTwo.sort" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addSure(0)"> </el-button>
				<el-button type="primary" @click="addSure(1)"> </el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			// 
			formOne: {
				position: '', //form
			},
			formTwo: {
				content: '',
				position: '0',
				sort: '0',
			},
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			dialogFormVisible: false, // dialog
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.position = _this.formOne.position; // 
			// 
			for(var key in obj) {
				if(key=='position') {
					// 
				} else {
					if(obj[key]=='') {
						baseConfig.warningTipMsg(_this, '');
						return null;
					}
				}
			}
			return obj; // return
		},
		// (val0->1->)
		addSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.dialogFormVisible = false; // dialog
				// console.log('');
			} else if(val==1) {
				// 
				// console.log('');
				_this.listLoading = true;
				// 
				let formData = new FormData();
				formData.append('content', encodeURI(_this.formTwo.content));
				formData.append('position', _this.formTwo.position);
				formData.append('sort', _this.formTwo.sort);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};
				if(formData.get('content')!=''&&formData.get('position')!=''&&formData.get('sort')!='') {
					axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/addScreen', formData, config)
					.then((res) => {
						_this.listLoading = false;	
						_this.dialogFormVisible = false;								
						if(res.data.ret) {	
							baseConfig.successTipMsg(_this, '');
							_this.getTableData();
						} else {
							baseConfig.errorTipMsg(_this, res.data.msg);						
						}
					}).catch((error) => {
						console.log(error);
					});
				} else {
					_this.listLoading = false; 
					baseConfig.warningTipMsg(_this, '');
				}
			} 
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/GlobalSet/findScreen';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
					_this.listLoading = false;
					if(res.data.ret) {
						// 
						for(var i=0; i<res.data.data.length; i++) { // 1
							res.data.data[i].content = decodeURI(res.data.data[i].content);
							if(_this.formTwo.sort<=res.data.data[i].sort) {
								_this.formTwo.sort = res.data.data[i].sort-0+1;
								// console.log(_this.formTwo.sort);
							}
						}
						_this.totalpage = res.data.data.length;
						_this.tabData = res.data.data;
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					console.log(error);
				})
			}
		},
		// 
		deleteOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			// 
			_this.listLoading = true;
			let formData = new FormData();
			formData.append('id', id);
			let config = {
				headers: {
					'Content-Type': 'multipart/form-data'
				}
			};
			if(formData.get('id')!='') {
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/delScreen', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			} else {
				_this.listLoading = false; 
				baseConfig.warningTipMsg(_this, 'id');
			}
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getTableData();
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



