<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!-- / -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <div class="block">
                        <span class="registerTime"></span>
                        <el-date-picker v-model="formOne.startDate" type="date">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item>
                    <span>uid</span>
                    <el-input style="width:200px;" placeholder="uid" v-model="uid" clearable>
                    </el-input>
                </el-form-item>

                <el-form-item class="search-span" style="float:right;">
                    <el-button id="searchBtn" type="primary" @click="getData(0)"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="addtime" label=""></el-table-column>
                <el-table-column prop="uid" label="ID"></el-table-column>
                <el-table-column prop="anchor_level" :formatter="judgeAnchor" label=""></el-table-column>
                <el-table-column prop="day_charm" label=""></el-table-column>
                <el-table-column prop="week_charm" label="7"></el-table-column>
                <el-table-column prop="month_charm" label="30" sortable></el-table-column>
                <el-table-column prop="charm_score" label=""></el-table-column>
            </el-table>
            <!--  -->
            <el-col :span="24" class="toolbar">
                <el-pagination layout="total,prev, pager, next,jumper" :page-size="20"  @current-change="handleCurrentChange" :current-page="page+1" :total=1000 style="float:right; ">
                </el-pagination>
            </el-col>
        </template>
    </section>
</template>

<script>
import { allget } from "../../../api/api";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date()] // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            uid: null,
            page: 0,
            totalpage: '',
        };
    },
    methods: {
        //
        handleCurrentChange(val) {
            //0   -1
            this.page = val-1;
            this.getData();
        },
        // 
        getData(type) {
            var _this = this;
            if(type==0){
                _this.page = 0;
            }
            let url = "/User/getFemaleCharmList";
            let param = {
                page: this.page,
                date: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                uid: this.uid
            };
            allget(param, url)
                .then(res => {
                    this.listData = res.data.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        // 
        judgeAnchor(row) {
            return row.anchor_level == 0 ? "" : "";
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = searchPageHeight;
        _this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



