<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
                <el-form-item>
					<el-button type="primary" @click="formTwo.dialogVisible=true;"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="id" label="ID" width="80" sortable ></el-table-column>
				<el-table-column prop="g_key" label="" width="150"></el-table-column>
				<el-table-column prop="value" label="" min-width="400"></el-table-column>
				<el-table-column prop="desc" label="" width="250"></el-table-column>
				<el-table-column label="" width="150">
					<template slot-scope="scope">
						<el-button type="primary" @click.native.prevent="changeOneUserData(scope.$index, tabData)" size="small"></el-button>
						<el-button type="primary" @click.native.prevent="deleteOneUserData(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
			<!-- --dialog -->
			<el-dialog title="" :visible.sync="formOne.dialogVisible">
				<el-form :model="formOne">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input disabled v-model="formOne.id" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.g_key" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item v-if="formOne.id!=37" label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.value" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item v-if="formOne.id==37" label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.values.home_h5_img" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item v-if="formOne.id==37" label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.values.home_h5_jump_url" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item v-if="formOne.id==37" label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.values.home_h5_jump_type" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item v-if="formOne.id==37" label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.values.home_h5_jump_title" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item v-if="formOne.id==37" label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.values.home_h5_share_content" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item v-if="formOne.id==37" label="" :label-width="formLabelWidth">
						<el-input v-model="formOne.values.home_h5_is_show" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input type="textarea" autosize v-model="formOne.desc" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="editorSure(0)"> </el-button>
					<el-button type="primary" @click="editorSure(1)"> </el-button>
				</div>
			</el-dialog>
			<!-- --dialog -->
			<el-dialog title="" :visible.sync="formTwo.dialogVisible">
				<el-form :model="formTwo">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.g_key" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.value" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input type="textarea" autosize v-model="formTwo.desc" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="addSure(0)"> </el-button>
					<el-button type="primary" @click="addSure(1)"> </el-button>
				</div>
			</el-dialog>
		</template>
	</section>
</template>

<script>
/* js */
import Event from '../../public_js/event.js';
import { allget } from '../../api/api';
import store from '../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			formOne: { // 
				dialogVisible: false,
				id: '',
				g_key: '',
				value: '',
				desc: '',
				values: {
					home_h5_img: '',
					home_h5_jump_url: '',
					home_h5_jump_type: '',
					home_h5_jump_title: '',
					home_h5_share_content: '',
					home_h5_is_show: '',
				},
			},
			formTwo: { // 
				dialogVisible: false,			
				g_key: '',
				value: '',
				desc: '',
			},
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/GlobalSet/getGlobalSet';
			// get(params, url)
			allget('', url).then(res => {
				// 
				_this.listLoading = false;
				if(res.data.ret) {
					// 
					_this.totalpage = res.data.data.length;
					_this.tabData = res.data.data;
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}
			}).catch(function(error){
				console.log(error);
			})
		},
		// 
		changeOneUserData(index, rows){
			var _this = this;
			index = index + (_this.page-1)*20; // index
			_this.formOne.id = rows[index].id;
			_this.formOne.g_key = rows[index].g_key;
			_this.formOne.desc = rows[index].desc;
			if(_this.formOne.id==37) {
				var aa = JSON.parse(rows[index].value);
				_this.formOne.values.home_h5_img = aa.home_h5_img;
				_this.formOne.values.home_h5_jump_url = aa.home_h5_jump_url;
				_this.formOne.values.home_h5_jump_type = aa.home_h5_jump_type;
				_this.formOne.values.home_h5_jump_title = aa.home_h5_jump_title;
				_this.formOne.values.home_h5_share_content = aa.home_h5_share_content;
				_this.formOne.values.home_h5_is_show = aa.home_h5_is_show;
			} else {
				_this.formOne.value = rows[index].value;
				_this.formOne.values.home_h5_img = '';
				_this.formOne.values.home_h5_jump_url = '';
				_this.formOne.values.home_h5_jump_type = '';
				_this.formOne.values.home_h5_jump_title = '';
				_this.formOne.values.home_h5_share_content = '';
				_this.formOne.values.home_h5_is_show = '';
			}
			_this.formOne.dialogVisible = true; // dialog
		},
		// 
		editorSure(type) {
			var _this = this;
			if(type==0) {
				_this.formOne.dialogVisible = false;
			} else if(type==1) {
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('id', _this.formOne.id);
				formData.append('g_key', _this.formOne.g_key);
				formData.append('desc', _this.formOne.desc);
				if(_this.formOne.id==37) {
					_this.formOne.value = JSON.stringify(_this.formOne.values);
				} else {}
				formData.append('value', _this.formOne.value);				
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/editGlobalSet', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formOne.dialogVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			}
		},
		// 
		addSure(type) {
			var _this = this;
			if(type==0) {
				_this.formTwo.dialogVisible = false;
			} else if(type==1) {
				_this.listLoading = true;
				let formData = new FormData();
				formData.append('g_key', _this.formTwo.g_key);
				formData.append('value', _this.formTwo.value);
				formData.append('desc', _this.formTwo.desc);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};		
				axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/addGlobalSet', formData, config)
				.then((res) => {
					_this.listLoading = false;	
					_this.formTwo.dialogVisible = false;									
					if(res.data.ret) {	
						baseConfig.successTipMsg(_this, '');
						_this.getTableData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);						
					}
				}).catch((error) => {
					console.log(error);
				});
			}
		},
		// 
		deleteOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20;
			var id = rows[index].id;
			_this.listLoading = true;
			let formData = new FormData();
			formData.append('id', id);
			let config = {
				headers: {
					'Content-Type': 'multipart/form-data'
				}
			};		
			axios.post(baseConfig.server+baseConfig.requestUrl+'/GlobalSet/delGlobalSet', formData, config)
			.then((res) => {
				_this.listLoading = false;
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, '');
					_this.getTableData();
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg);
				}
			}).catch((error) => {
				console.log(error);
			});
		},
	},
	mounted() {
		var _this = this;
		_this.$nextTick(function() {
			_this.tableHeight = searchPageHeight; // table
			_this.getTableData(); // table
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 190px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



