                                    <el-button v-if="scope.row.turn_agent_num" type="primary" @click="dialogTableVisible = true, getDetailData(4, scope.row.uid, scope.row.date)" size="mini" style="margin-left: 10px;"></el-button>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="total_turn_agent_num" label=""></el-table-column>
                        <el-table-column prop="spread_ticket" label=""></el-table-column>
                        <el-table-column prop="total_spread_ticket" label=""></el-table-column>
                    </el-table>
                    <!-- Table -->
                    <el-dialog title="" :visible.sync="dialogTableVisible" center>
                        <el-table :data="detialData">
                            <el-table-column property="addtime" label=""></el-table-column>
                            <el-table-column property="uid" label="UID"></el-table-column>
                            <el-table-column property="phone" label=""></el-table-column>
                            <el-table-column property="nickname" label=""></el-table-column>
                            <el-table-column property="add_time" label=""></el-table-column>
                        </el-table>
