                    <!--  -->
                    <el-col :span="24" class="toolbar">
                        <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
                        </el-pagination>
                    </el-col>
                </template>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <!--  -->
                <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
                    <el-form :inline="true">
                        <el-form-item>
                            <div class="block">
                                <span class="registerTime"></span>
                                <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                                </el-date-picker>
                            </div>
                        </el-form-item>
                        <el-form-item>
                            <span>uid</span>
                            <el-input style="width:100px;" placeholder="" v-model="uid1" clearable>
                            </el-input>
                        </el-form-item>
                        <el-form-item style="float:right;margin-right: 100px;">
                            <el-button type="primary" @click="getTbData1(0)"></el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
                <!---->
                <template>
                    <el-table ref="tableHeight" :data="listData1" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
                        <el-table-column prop="date" label=""></el-table-column>
                        <el-table-column prop="uid" label="UID"></el-table-column>
                        <el-table-column prop="username" label=""></el-table-column>
                        <el-table-column prop="nickname" label=""></el-table-column>
                        <el-table-column prop="wx_invite_uid" label="UID"></el-table-column>
                        <el-table-column prop="total_spread_num" label=""></el-table-column>
                        <el-table-column prop="one_num" label="">
                            <template slot-scope="scope">
                                <div slot="reference" class="name-wrapper">
                                    <span>{{scope.row.one_num}}</span>
                                    <el-button v-if="scope.row.one_num" type="primary" @click="dialogTableVisible1 = true, getDetailData(1, scope.row.uid, scope.row.date)" size="mini" style="margin-left: 10px;"></el-button>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="total_one_num" label=""></el-table-column>
                        <el-table-column prop="two_num" label="">
                            <template slot-scope="scope">
                                <div slot="reference" class="name-wrapper">
                                    <span>{{scope.row.two_num}}</span>
                                    <el-button v-if="scope.row.two_num" type="primary" @click="dialogTableVisible1 = true, getDetailData(2, scope.row.uid, scope.row.date)" size="mini" style="margin-left: 10px;"></el-button>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="total_two_num" label=""></el-table-column>
                        <el-table-column prop="three_num" label="">
                            <template slot-scope="scope">
                                <div slot="reference" class="name-wrapper">
                                    <span>{{scope.row.three_num}}</span>
                                    <el-button v-if="scope.row.three_num" type="primary" @click="dialogTableVisible1 = true, getDetailData(3, scope.row.uid, scope.row.date)" size="mini" style="margin-left: 10px;"></el-button>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="total_three_num" label=""></el-table-column>
                        <el-table-column prop="turn_agent_num" label="">
                            <template slot-scope="scope">
                                <div slot="reference" class="name-wrapper">
                                    <span>{{scope.row.turn_agent_num}}</span>
                                    <el-button v-if="scope.row.turn_agent_num" type="primary" @click="dialogTableVisible1 = true, getDetailData(4, scope.row.uid, scope.row.date)" size="mini" style="margin-left: 10px;"></el-button>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="total_turn_agent_num" label=""></el-table-column>
                        <el-table-column prop="agent_back" label=""></el-table-column>
                        <el-table-column prop="total_agent_back" label=""></el-table-column>
                        <el-table-column prop="agent_back" label=""></el-table-column>
                        <el-table-column prop="total_cost_back" label=""></el-table-column>
                        <el-table-column prop="all_back" label=""></el-table-column>
                        <el-table-column prop="all_total_back" label=""></el-table-column>
                    </el-table>
                    <!-- Table -->
                    <el-dialog title="" :visible.sync="dialogTableVisible1" center>
                        <el-table :data="detialData">
                            <el-table-column property="addtime" label=""></el-table-column>
                            <el-table-column property="uid" label="UID"></el-table-column>
                            <el-table-column property="phone" label=""></el-table-column>
                            <el-table-column property="nickname" label=""></el-table-column>
                            <el-table-column property="add_time" label=""></el-table-column>
                        </el-table>
                    </el-dialog>
                    <!--  -->
                    <el-col :span="24" class="toolbar">
                        <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange1" :page-size="20" :total=1000 :current-page="page1+1" style="float:right; ">
                        </el-pagination>
                    </el-col>
                </template>
            </el-tab-pane>
            
        </el-tabs>
    </section>
</template>
<script>
import { allget } from "../../../api/api";
import store from "../../../vuex/store.js";
import axios from "axios";
export default {
    data() {
        return {
            sex: "",
            listData: [],
            listData1: [],
            listLoading: false,
            tableHeight: null,
            activeName: "first",
            formOne: {
                startDate: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()], // ,180
                startDate1: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            totalpage: null,
            totalpage1: null,
            page: 0,
            page1: 0,
            tableHeight: null, // table
            uid: null,
            uid1: null,
            dialogTableVisible: false,
            dialogTableVisible1: false,
            detialData: [],
        };
    },
    methods: {
        //
        handleCurrentChange(val) {
            //0   -1
            this.page = val - 1;
            this.getTbData();
        },
        //
        handleCurrentChange1(val) {
            //0   -1
            this.page1 = val - 1;
            this.getTbData1();
        },
        getTbData(type) {
            var _this = this;
            let url = "/Agent/getAgentDataList";
            if(type==0){
                this.page = 0;
            }
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate1[0],0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate1[1],0),
                type: 0, //
                page: this.page,
                uid: this.uid,
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        this.listData = res.data.data;
                    } else {
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(this, res.data.msg);
                });
        },
        getTbData1(type) {
            var _this = this;
            if(type==0){
                this.page = 0;
            }
            let url = "/Agent/getAgentDataList";
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                type: 1, // 
                page: this.page1,
                uid: this.uid1,
            };
            this.uid1 == null || this.uid1 == ""
                ? delete param.uid1
                : (param.uid1 = this.uid1);
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        this.listData1 = res.data.data;
                    } else {
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(this, res.data.msg);
                });

            // 
            // url = 'https://manage.dianliaoapp.com/ydlManage/server/index.php/Agent/getAgentDataList?date_s=2018-04-08&date_e=2018-04-10&type=1&page=0';
            // axios.get(url,param).then(res=>{
            //     if (res.data.ret) {
            //         this.listData1 = res.data.data;
            //     } else {
            //         baseConfig.errorTipMsg(this, res.data.msg);
            //     }
            // }).catch(err=>{
            //     baseConfig.errorTipMsg(this, res.data.msg);
            // })

        },
        //   type ( 1 2 3 4 5)
        getDetailData(type, uid, date) {
            var _this = this;
            let url = '/Agent/getAgentManList';
            let param = {
                type: type,
                uid: uid,
                date: date,
            }
            allget(param, url)
                .then(res => {
                    if(res.data.ret){
                        _this.detialData = res.data.data;
                    }else{
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
        }
    },
    mounted() {
        this.tableHeight = tabSearchPageHeight;
        this.getTbData();
        this.getTbData1();
    }
};
</script>
<style lang="css" scoped>

</style>




