<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="id" label="ID" width="100" sortable ></el-table-column>
				<el-table-column prop="key" label="" width="200"></el-table-column>
				<el-table-column prop="value" label="" width="200"></el-table-column>
				<el-table-column prop="desc" label="" width="200"></el-table-column>
				<el-table-column label="" min-width="300">
					<template slot-scope="scope">
						<el-button type="primary" @click.native.prevent="changeOneUserData(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
			<!-- --dialog -->
			<el-dialog title="ID" :visible.sync="dialogFormVisible">
				<el-form :model="formTwo">
					<el-form-item label="ID" :label-width="formLabelWidth">
						<el-input v-model="formTwo.id" auto-complete="off" disabled></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.key" auto-complete="off" disabled></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.value" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.desc" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="editorSure(0)"> </el-button>
					<el-button type="primary" @click="editorSure(1)"> </el-button>
				</div>
			</el-dialog>
		</template>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			formTwo: {
				id: '', // id
				key: '', // key
				value: '', // value
				desc: '', // desc
				rows: null, // table
				index: null, // table
			},
			formThree: {
				file: '', // Excel
			},
			excelBoxActivity: false, // Excel
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			dialogFormVisible: false, // dialog
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/User/getAdCondition';
			// get(params, url)
			allget('', url).then(res => {
				// 
				_this.listLoading = false;
				if(res.data.ret) {
					// 
					_this.totalpage = res.data.data.length;
					_this.tabData = res.data.data;
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}
			}).catch(function(error){
				console.log(error);
			})
		},
		// 
		changeOneUserData(index, rows){
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			_this.formTwo.index = index;
			_this.formTwo.id = id;
			_this.formTwo.key = rows[index].key;
			_this.formTwo.value = rows[index].value;
			_this.formTwo.desc = rows[index].desc;
			_this.dialogFormVisible = true; // dialog
		},
		// (val0->1->)
		editorSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.dialogFormVisible = false; // dialog
				console.log('');
			} else if(val==1) {
				// 
				console.log('');
				_this.listLoading = true;
				// 
				var url = '/User/editAdCondition';
				var params = {
					id: _this.formTwo.id,
					key: _this.formTwo.key,
					value: _this.formTwo.value,
					// id
					desc: _this.formTwo.desc,
				};
				// valuedesc
				if(params.value!='' && params.desc!=''){
					_this.dialogFormVisible = false; // dialog
					allget(params, url).then(res => {
						console.log(res);
						if(res.data.ret) {
							// 
							_this.listLoading = false;
							baseConfig.successTipMsg(_this, '');
							_this.getTableData();
						} else {
							baseConfig.errorTipMsg(_this, res.data.msg);
						}
					}).catch(function(error){
						console.log(error);
					})
				} else {
					_this.listLoading = false; 
					baseConfig.warningTipMsg(_this, '');
				}
			} 
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = pageHeight; // table
			_this.getTableData(); // table
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 190px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



