<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- Excel -->
		<div class="excelBox" v-show="excelBoxActivity">
			<p>ExcelExcel</p>
			<div class="excelInput">
				<p>Excel:</p>
				<input @change="uploading($event)" type="file" accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" />
			</div>
			<div class="select">
				<p>:</p>
				<div>
					<el-select v-model="formThree.threeSexValue" placeholder="" style="width: 100%;">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</div>
				
			</div>
			<div class="btns">
				<!--  -->
	    <button @click="excelBoxActivity=false;"></button>
		<button @click="submit()"></button>
			</div>
		</div>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<span></span>
					<el-select v-model="formOne.sexValue">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<el-button type="primary" @click="dialogFormVisible=true;"></el-button>
					<el-button type="primary" @click="excelBoxActivity=true;"></el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column type="index" width="100" ></el-table-column>
				<el-table-column prop="value" label="" width="800" sortable ></el-table-column>
				<el-table-column label="" min-width="200">
					<template slot-scope="scope">
						<el-button type="primary" @click.native.prevent="deleteOneUserData(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
		</template>
		<!-- --dialog -->
		<el-dialog title="" :visible.sync="dialogFormVisible">
			<el-form :model="formTwo">
				<el-form-item label="" :label-width="formLabelWidth">
					<el-input v-model="formTwo.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" :label-width="formLabelWidth">
				<el-select v-model="formTwo.region" placeholder="" style="width: 100%;">
					<el-option label="" value="1"></el-option>
					<el-option label="" value="2"></el-option>
				</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addSure(0)"> </el-button>
				<el-button type="primary" @click="addSure(1)"> </el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			// 
			formOne: {
				sexValue: '1', //form
			},
			formTwo: {
				name: '', // inputvalue
				region: '1', // selectvalue
			},
			formThree: {
				file: '', // Excel
				threeSexValue: '1', // 
			},
			excelBoxActivity: false, // Excel
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			dialogFormVisible: false, // dialog
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.sex = _this.formOne.sexValue; // 
			// 
			for(var key in obj) {
				if(obj[key]=='') {
					baseConfig.warningTipMsg(_this, '');
					return null;
				}
			}
			return obj; // return
		},
		// (val0->1->)
		addSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.dialogFormVisible = false; // dialog
				console.log('');
			} else if(val==1) {
				// 
				console.log('');
				_this.listLoading = true;
				// 
				var url = '/User/addRegisterSayHelloRedis';
				var params = {
					content: encodeURI(_this.formTwo.name),
					sex: _this.formTwo.region,
				};
				// 
				if(params.content!='' && (params.sex=='1' || params.sex=='2')){
					_this.dialogFormVisible = false; // dialog
					allget(params, url).then(res => {
						console.log(res);
						if(res.data.ret) {
							// 
							_this.listLoading = false;
							baseConfig.successTipMsg(_this, '');
							_this.getTableData();
						} else {
							baseConfig.errorTipMsg(_this, res.data.msg);
						}
					}).catch(function(error){
						console.log(error);
					})
				} else {
					_this.listLoading = false; 
					baseConfig.warningTipMsg(_this, '');
				}
			} 
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/User/getRegisterSayHelloRedis';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
					_this.listLoading = false;
					if(res.data.ret) {
						// 
						_this.totalpage = res.data.data.length;
						var arrObjData = [];
						for(var i=0; i<res.data.data.length; i++) {
							var obj = {};
							obj.value = decodeURI(res.data.data[i]);
							arrObjData.push(obj);
						} 
						console.log(arrObjData);
						_this.tabData = arrObjData;
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}

				}).catch(function(error){
					console.log(error);
				})
			}
		},
		// 
		deleteOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var content = rows[index].value; // value	
			console.log(content);
			// 
			_this.listLoading = true;
			var url = '/User/delRegisterSayHelloRedis';
			var params = {
				content: encodeURI(content),
				sex: _this.formOne.sexValue,
			};
			// get
			allget(params, url).then(res => {
				// 
				_this.listLoading = false;
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, ''); // 
					_this.getTableData();
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}

			}).catch(function(error) {
				console.log(error);
			})
		},
		// 
		uploading(event) {
			var _this = this;
    	var windowURL = window.URL || window.webkitURL;
    _this.formThree.file = event.target.files[0];
    	},
		// Excel 
    	submit() {
            var _this = this;
			let formData = new FormData();
        	formData.append('file', _this.formThree.file); // 
        	formData.append('sex', _this.formThree.threeSexValue); // 
        	let config = {
    headers: {
    'Content-Type': 'multipart/form-data' //fromdata
    }
    };
    axios.post(baseConfig.server+baseConfig.requestUrl+'/User/addRegisterSayHelloByExcel', formData, config)
            .then( (res) => {
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, ''); // 
					_this.getTableData();
					// 
					_this.excelBoxActivity = false; // Excel
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}
        }).catch((error) =>{
                console.log(error);
        });
    	},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getTableData();
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



