<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!-- / -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <div class="block">
                        <span class="registerTime"></span>
                        <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item>
                    <span></span>
                    <el-select v-model="channelId" multiple filterable collapse-tags style="width: 150px;" placeholder="">
                        <el-option v-for="(item, key) of channelData" :key="item" :label="item" :value="key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <span>UID</span>
                    <el-input style="width:100px;" placeholder="" v-model="find" clearable>
                    </el-input>
                </el-form-item>
                <el-form-item>
                    <span></span>
                    <el-autocomplete class="inline-input" v-model="nickname" :fetch-suggestions="querySearch" placeholder=""></el-autocomplete>
                </el-form-item>
                <el-form-item class="search-span" style="float:right;">
                    <el-button id="searchBtn" type="primary" @click="getData()"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="time" label=""></el-table-column>
                <el-table-column prop="uid" label="UID"></el-table-column>
                <el-table-column prop="nickname" label=""></el-table-column>
                <el-table-column prop="channel" label=""></el-table-column>
                <el-table-column prop="day" label=""></el-table-column>
                <el-table-column prop="free_time" label=""></el-table-column>
                <el-table-column prop="reason" label=""></el-table-column>
                <el-table-column prop="operate_user" label=""></el-table-column>
                <el-table-column label="" min-width="120">
                    <template slot-scope="scope">
                        <div v-if="scope.row.status==0&&scope.row.type==1"></div>
                        <div v-else-if="scope.row.status==1&&scope.row.type==1">
                            <el-button style="margin-left: 5px;" plain size="mini" type="primary" @click="getter(scope.$index, scope.row)"></el-button>
                        </div>
                        <div v-else-if="scope.row.type==2"></div>
                        <div v-else-if="scope.row.type==3"></div>
                    </template>
                </el-table-column>
            </el-table>
        </template>
        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="reasonform">
                <el-form-item label="" placeholder="" :label-width="formLabelWidth">
                    <el-input v-model="reasonform.value" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="closeTitle()"> </el-button>
            </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialogtitle" width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogtitle = false"> </el-button>
                <el-button type="primary" @click="dialogtitle = false"> </el-button>
            </span>
        </el-dialog>
    </section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from "axios";
import store from "../../../vuex/store";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 100 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            channelData: {},
            channelId: null,
            find: null,
            restaurants: [],
            nickname: null,
            dialogFormVisible: false,
            dialogtitle: false,
            operate_user: null,
            uid: null,
            reasonform: {
                value:""
            },
            nicknameObj: [],
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/NewUser/kickHistory";
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                operate_user: this.operate_user,
                find: this.find,
                channel: this.channelId.join(","),
                nickname: this.nickname,
            };
            allget(param, url)
                .then(res => {
                    if(res.data.ret){
                        this.listData = res.data.data;
                        for(var i = 0;i < res.data.data.length; i++){
                            var timeObj = {}; 
                            timeObj.value = res.data.data[i].nickname;
                            _this.nicknameObj.push(timeObj);
                        }
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //  
        getter(index, row) {
            this.dialogFormVisible = true;
            this.uid = row.uid;
        },
        // 
        closeTitle() {
            var _this = this;
            let url = "/NewUser/freeUser";
            let param = {
                uid: this.uid,
                operate_user: this.operate_user,
                reason: this.reasonform.value
            };
            if (param.reason == "" || param.reason == null) {
                _this.dialogtitle = true;
                return;
            }
            _this.dialogFormVisible = false;
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                        _this.getData();
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        },
        // 
        querySearch(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            //  callback 
            cb(results);
        },
        createFilter(queryString) {
            return (restaurant) => {
                return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        loadAll(){
            return this.nicknameObj;
            
        },
    },
    mounted() {
        this.tableHeight = searchHeight;
        var id = store.state.user.channelid.split(",");
        var name = store.state.user.channelname.split(",");
        for (var i = 0; i < id.length; i++) {
            this.channelData[id[i]] = name[i];
        }
        this.getData();
        this.operate_user = store.state.user.name;
        this.restaurants = this.loadAll();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



