<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="formOne">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.choiceDate" type="daterange" range-separator="  " placeholder=""></el-date-picker>
					</div>
				</el-form-item>
                <el-form-item style="margin-left: 100px;">
					<span></span>
					<el-select style="width: 150px;" v-model="searchType">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
                    <span></span>
                    <el-select v-model="channelId" multiple filterable collapse-tags style="margin-left: 20px;" placeholder="">
                        <el-option v-for="(item, key) of channelData" :key="item" :label="item" :value="key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item style="margin-left: 100px;">
					<el-button type="primary" @click="chartLineShow"></el-button>
					<el-button type="primary" @click="getTableData"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="tabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="day" label="/" width="90" sortable ></el-table-column>
				<el-table-column prop="total" label="" min-width="60"></el-table-column>
				<el-table-column prop="chat_gold" label="" min-width="60"></el-table-column>
				<el-table-column prop="vip" label="VIP" min-width="60"></el-table-column>
				<el-table-column prop="family" label="" min-width="60"></el-table-column>
				<el-table-column prop="agent"  label="" min-width="60"></el-table-column>
				<el-table-column prop="enchashment" label="" min-width="60"></el-table-column>
			</el-table>
			<!--  -->
			<el-dialog title="" :width="dialogWidth"  :visible.sync="dialogVisible" @open="show">
                <div class="chartLine"  style="width: 100%;height: 600px;"></div>
            </el-dialog>
		</template>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
import echarts from 'echarts';
export default {
	data() {
		return {
			tableHeight: null, // table
			// 
			formOne: {
				choiceDate: [new Date()-15*24*60*60*1000, new Date()], // ,180
            },
            searchType: "1",
			listLoading: false, //
			tabData: [], //
			formLabelWidth: '120px', // dialog
            dialogVisible: false,
            dialogWidth: null,
            channelData: {},
            channelId: null,
            chartLine: null,
            chartLineData: {
                title: {
                        text: ''
                    },
                tooltip : {
                    trigger: 'axis'
                },
                toolbox: {
                    show : true,
                    feature : {
                        mark : {show: true},
                        dataView : {show: true, readOnly: false},
                        magicType: {show: true, type: ['line', 'bar']},
                        restore : {show: true},
                        saveAsImage : {show: true}
                    }
                },
                calculable : true,
                legend: {
                    data:['']
                },
                xAxis: [
                    {
                        type: 'category',
                        data: []
                    }
                ],
                yAxis: [
                    {
                        type : 'value',
                        name : '',
                        axisLabel : {
                            formatter: '{value} '
                        }
                    }
                ],
                series: [
                    {
                        name:'',
                        type:'line',
                        data:[]
                    }
                ]
            },
		};
	},
	methods: {
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
            obj.date_s = baseConfig.changeDateTime(_this.formOne.choiceDate[0], 0);
            obj.date_e = baseConfig.changeDateTime(_this.formOne.choiceDate[1], 0);
            obj.channel = this.channelId.join(',');
            obj.date_type = this.searchType;
			return obj;
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Money/getFinancialDaily';
			var params = _this.searchCondition();
			// null
			if(params==null) {
				// ,
				_this.listLoading = false;
			} else {
				// get(params, url)
				allget(params, url).then(res => {
					// 
                    _this.listLoading = false;
					if(res.data.ret) {
                        console.log(res.data.total)
                        res.data.total.day = "";
                        res.data.data.unshift(res.data.total);
                        // 
                        for(var i = 0; i < res.data.data.length; i++) {
                            res.data.data[i].total = (+res.data.data[i].total).toFixed(0);
                            res.data.data[i].chat_gold = (+res.data.data[i].chat_gold).toFixed(0);
                            res.data.data[i].vip = (+res.data.data[i].vip).toFixed(0);
                            res.data.data[i].family = (+res.data.data[i].family).toFixed(0);
                            res.data.data[i].agent = (+res.data.data[i].agent).toFixed(0);
                            res.data.data[i].enchashment = (+res.data.data[i].enchashment).toFixed(0);
                        }
                         _this.tabData = res.data.data;
                        
                        // 
                        // 
                        _this.chartLineData.xAxis[0].data = [];
                        _this.chartLineData.series[0].data = [];
                        for(var i = res.data.data.length-1; i > 0; i--){
                            _this.chartLineData.xAxis[0].data.push(_this.tabData[i].day); // 
                            _this.chartLineData.series[0].data.push(_this.tabData[i].total); // 
                        }
					} else {
						// ret==0
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					console.log(error);
				})
			}
        },
        show() {
            this.$nextTick(function() {
                this.chartLine = echarts.init(document.querySelector('.chartLine'));
                document.querySelector('.chartLine').style.border = "1px solid #4488BB";
                this.chartLine.setOption(this.chartLineData);
            })
        },
        chartLineShow() {
            this.dialogVisible = true;
        }
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight;
			_this.getTableData();
        })
        _this.dialogWidth = lookWidth*0.8+'px'; //dialog80%
        var id = store.state.user.channelid.split(",");
        for(var i = 0; i<id.length; i++){
            this.channelData[id[i]] = store.state.user.channelname.split(",")[i];
        }
	}
};
</script>

<style lang="css" scoped>
   .infomation{
       width: 100%;
       height: 100%;
   }
</style>



