<template>
    <!--  -->
    <!-- dom -->
	<section>
        <!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
                <el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData(0)"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="date" label=""></el-table-column>
				<el-table-column prop="normal_agent"  label=""></el-table-column>
				<el-table-column prop="agent_join_num" label=""></el-table-column>
				<el-table-column prop="spread_num" label=""></el-table-column>
				<el-table-column prop="agent_join_money" label=""></el-table-column>
				<el-table-column prop="spread_money" label=""></el-table-column>
				<el-table-column prop="total_spread_money" label=""></el-table-column>
				<el-table-column prop="spread_cost" label=""></el-table-column>
				<el-table-column prop="cost_pay_back" label="" sortable></el-table-column>
				<el-table-column prop="agent_pay_back" label=""></el-table-column>
				<el-table-column prop="total_pay_back" label=""></el-table-column>
				<el-table-column prop="all_total_pay_back" label=""></el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
				</el-pagination>
			</el-col>
		</template>
    </section>
</template>

<script>
	import { allget } from '../../../api/api';
	import axios from 'axios';
    export default {
        data() {
            return {
				tableHeight: null, // table
                formOne: {
                    startDate: [new Date()-1*24*60*60*1000, new Date()], // ,180
				},
				page: 0,
				listData: [],
				totalpage:null,
				formLabelWidth: '120px',
				listLoading: false,
            }
        },
		methods: {
			//
			handleCurrentChange(val) {
				//0   -1
				this.page = val-1;
				this.getData();
			},
			// 
			getData(type) {
				var _this = this;
				if(type==0){
					_this.page = 0;
				}
				let url = '/Agent/getAgentTotalView';
				let param ={
					page: this.page,
					date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
					date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
				}
				allget(param, url).then(res => {
					this.listData = res.data.data;
				}).catch(err => {
					console.log(err)
				})
			},
		},
		mounted() {
			var _this = this;
			_this.tableHeight = searchPageHeight;
			_this.getData();
		},
    }

</script>

<style lang="css" scoped>
    .search-span{
        float: right;
    }
    #searchBtn{
        margin-right: 50px;
    }
</style>



