<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="date" label=""></el-table-column>
				<el-table-column prop="num" label=""></el-table-column>
				<el-table-column prop="complain_uid" label=""></el-table-column>
				<el-table-column prop="reason" label=""></el-table-column>
				<el-table-column prop="voice_id" label=""></el-table-column>
				<el-table-column prop="uid" label=""></el-table-column>
				<el-table-column prop="warn_num" label="">
                    <template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<audio controls="controls" :src="scope.row.warn_num"></audio>
						</div>
					</template>
                </el-table-column>
				<el-table-column prop="desc" label=""></el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
                        <el-button size="mini" type="success" @click="ignore(scope.$index, scope.row)"></el-button>
                        <el-button size="mini" type="warning" @click="deleteFun(scope.$index, scope.row)"></el-button>
					</template>
				</el-table-column>
			</el-table>
		</template>
		<el-dialog title="" :visible.sync="ignoreDialog" width="30%">
			<span></span>
			<span slot="footer" class="dialog-footer">
				<el-button @click="ignoreDialog = false"> </el-button>
				<el-button type="primary" @click="ignoreDialog = false,sureIgnore()"> </el-button>
			</span>
		</el-dialog>
        <el-dialog title="" :visible.sync="deleteDialog" width="30%">
			<span></span>
			<span slot="footer" class="dialog-footer">
				<el-button @click="deleteDialog = false"> </el-button>
				<el-button type="primary" @click="deleteDialog = false,sureDeleteFun()"> </el-button>
			</span>
		</el-dialog>
	</section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from "axios";
import store from "../../../vuex/store";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 100 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            recordStatus: "",
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            formLabelWidth: "120px",
            operate_user: null,
            ignoreDialog: false,
            ignoreId: null,
            deleteDialog: false,
            deleteId: null,
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/NewVoice/getVoiceComplain";
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
            };
            allget(param, url)
                .then(res => {
                    if(res.data.ret){
                        this.listData = res.data.data;
                    }else{
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        },
        // 
        ignore(index, row) {
            this.ignoreId = row.voice_id;
            this.ignoreDialog = true;
            
        },
        sureIgnore() {
            var _this = this;
            let url = "/Voice/ignoreVoiceComplain";
            let param = {
                id: this.ignoreId
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                        _this.getData();
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        },
        // 
        deleteFun(index, row) {
            this.deleteId = row.voice_id;
            this.deleteDialog = true;
        },
        sureDeleteFun() {
            var _this = this;
            let url = "/Voice/deleteVoiceComplain";
            let param = {
                id: this.deleteId
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                        _this.getData();
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = searchPageHeight;
        _this.getData();
        _this.operate_user = store.state.user.name;
    },
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



