<template>
    <!--  -->
    <!-- dom -->
	<section>
        <!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item style="margin-left: 50px;">
					<span></span>
					<el-select style="width: 100px;" v-model="recordStatus">
						<el-option label="" value=""></el-option>
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<span></span>
					<el-select style="width: 100px;" v-model="recordStyle">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<span>uid</span>
					<el-input style="width:100px;" placeholder="" v-model="uid" clearable>
                    </el-input>
				</el-form-item>
                <el-form-item>
					<span>id</span>
					<el-input style="width: 100px;" placeholder="" v-model="audio_uid" clearable>
                    </el-input>
				</el-form-item>
                <el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData(0)"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="time" label="" width="180"></el-table-column>
				<el-table-column prop="id"  label="" width="100"></el-table-column>
				<el-table-column label="" width="300">
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<audio controls="controls" :src="scope.row.voice_url"></audio>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="release_num" label=""  min-width="50" ></el-table-column>
				<el-table-column prop="adoption_num" label="" min-width="50" ></el-table-column>
				<el-table-column prop="recommend" label="" min-width="50" ></el-table-column>
				<el-table-column prop="duration" label="" min-width="50" ></el-table-column>
				<el-table-column prop="uid" label="uid"></el-table-column>
				<el-table-column prop="content" label=""></el-table-column>
				<el-table-column prop="price" :formatter="price" label="" min-width="50" sortable></el-table-column>
				<el-table-column prop="listen" label="" width="50"></el-table-column>
				<el-table-column prop="praise" label="" width="50"></el-table-column>
				<el-table-column prop="relay" label="" width="50"></el-table-column>
				<el-table-column prop="award" label="" width="50"></el-table-column>
				<el-table-column prop="share" label="" width="50"></el-table-column>
				<el-table-column prop="complain" label="" width="50"></el-table-column>
				<el-table-column label="" min-width="220">
					<template slot-scope="scope">
						<div v-if="scope.row.is_up_list==1">
							<el-col :span="8"><el-button size="mini" type="primary" @click="cacelRecommend(scope.$index, scope.row)"></el-button></el-col>
							<el-col :span="3"><el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button></el-col>
						</div>
						<div v-if="scope.row.is_up_list==2">
							<el-col :span="5"><el-button size="mini" type="primary" @click="cacelRecommend(scope.$index, scope.row)"></el-button></el-col>
							<el-col :span="5"><el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button></el-col>
						</div>
						<div v-if="scope.row.is_up_list==0">
							<el-col :span="5"><el-button size="mini" type="primary" @click="recommend(scope.$index, scope.row)"></el-button></el-col>
							<el-col :span="5"><el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button></el-col>
						</div>
						<div v-if="scope.row.is_review==0">
							<el-col :span="5"><el-button size="mini" type="success" @click="Auditing(scope.$index, scope.row)"></el-button></el-col>
							<el-col :span="8"><el-button style="margin-right:10px;" size="mini" type="info" @click="showInfo(scope.$index, scope.row)"></el-button></el-col>
						</div>
						<div v-if="scope.row.is_review==1">
							<el-col :span="5"><el-button size="mini" plain  @click="tipInfo()"></el-button></el-col>
						</div>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
				</el-pagination>
			</el-col>
			<el-dialog title="" :visible.sync="formInfo.dialogShow">
				<el-form :model="formInfo">
					<el-form-item label="Uid" :label-width="formLabelWidth">
						<el-input v-model="formInfo.req_uid" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="Uid" :label-width="formLabelWidth">
						<el-input v-model="formInfo.res_uid" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
			</el-dialog>
		</template>
    </section>
</template>

<script>
	import { allget } from '../../../api/api';
	import axios from 'axios';
    export default {
        data() {
            return {
				tableHeight: null, // table
                formOne: {
                    startDate: [new Date()-2*24*60*60*1000, new Date()], // ,180
                    // endDate: [new Date()-180*24*60*60*1000, new Date()], // ,180
				},
				formInfo: {
					dialogShow: false,
					req_uid: '',
					res_uid: '',
				},
				value6: '',
				value10: 'text',
				recordStatus: '',
				recordStyle: '',
				page: 0,
				listData: [],
				uid: "",
				audio_uid: '',
				totalpage:null,
				formLabelWidth: '120px',
				listLoading: false,
            }
        },
		methods: {
			//
			handleCurrentChange(val) {
				//0   -1
				this.page = val-1;
				this.getData();
			},
			// 
			getData(type) {
				var _this = this;
				if(type==0){
					_this.page = 0;
				}
				let url = '/Voice/getVoiceListInfo';
				let param ={
					page: this.page,
					date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
					date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
					is_up_list: this.recordStatus,
					price: this.recordStyle,
					uid: this.uid,
					id: this.audio_uid
				}
				allget(param, url).then(res => {
					this.listData = res.data.data;
				}).catch(err => {
					console.log(err)
				})
			},
			// 
			tipInfo() {
				var _this = this;
				baseConfig.warningTipMsg(_this, '');
			},
			// 
			price(row){
				return row.price > 0 ? "" : "";
			},
			// 
			cacelRecommend(index, row) {
				var _this = this;
				var id = row.id;
				var url = '/Voice/cancelRecommendVoiceToHot';
				var params = {
					id: id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, err.data.msg);
				})
			},
			// 
			recommend(index, row) {
				var _this = this;
				var id = row.id;
				var url = '/Voice/recommendVoiceToHot';
				var params = {
					id: id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
			},
			// 
			handleDelete(index, row) {
				var _this = this;
				var id = row.id;
				var url = '/Voice/deleteVoice';
				var params = {
					id: id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
			},
			// 
			showInfo(index, row) {
				var _this = this;
				var call_record_id = row.call_record_id;
				var url = '/Voice/getVoiceOther';
				var params = {
					id: call_record_id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						_this.formInfo.req_uid = res.data.data.uid_req;
						_this.formInfo.res_uid = res.data.data.uid_res;
						_this.formInfo.dialogShow = true;
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
			},
			// 
			Auditing(index, row) {
				var _this = this;
				var id = row.id;
				var url = '/Voice/reviewVoice';
				var params = {
					id: id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
			},
		},
		mounted() {
			var _this = this;
			_this.tableHeight = searchPageHeight;
			_this.getData();
		},
		updated() {
			var _this = this;
			//
			var audio = document.getElementsByTagName("audio");
			for(var i=0; i<audio.length; i++){
				audio[i].addEventListener("play", function() {
					for(var j=0; j<audio.length; j++) {
						if(audio[j]!=this) { //this
							audio[j].pause();
						}
					}
				});
			}
		}
    }

</script>

<style lang="css" scoped>
    .search-span{
        float: right;
    }
    #searchBtn{
        margin-right: 50px;
    }
</style>



