<template>
    <!-- () -->
    <!-- dom -->
    <section>
        <!-- / -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <div class="block">
                        <span class="registerTime"></span>
                        <el-date-picker v-model="formOne.startDate" style="width:250px;" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item>
                    <span>UID</span>
                    <el-input style="width:100px;" @clear="clearInput" placeholder="UID" v-model="find" clearable>
                    </el-input>
                </el-form-item>
                <el-form-item>
                    <span></span>
                    <el-input style="width:145px;" @clear="clearInput" placeholder="" v-model="identity_card" clearable>
                    </el-input>
                </el-form-item>
                <el-form-item>
                    <span></span>
                    <el-select style="width: 100px;" v-model="status">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                        <el-option label="" value="3"></el-option>
                        <el-option label="" value="4"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <span></span>
                    <el-input style="width:140px;" @clear="clearInput" placeholder="" v-model="pay_account" clearable>
                    </el-input>
                </el-form-item>
                <el-form-item class="search-span" style="float:right;">
                    <el-button id="searchBtn" type="primary" @click="getData()"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="req_time" label=""></el-table-column>
                <el-table-column prop="uid" label="UID"></el-table-column>
                <el-table-column prop="nickname" label=""></el-table-column>
                <el-table-column prop="sex" :formatter="judegSex" label=""></el-table-column>
                <el-table-column prop="aut_icon" label="">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="left">
                            <img :src="scope.row.aut_icon" alt="" style="width:300px;height:400px;">
                            <div slot="reference" class="name-wrapper">
                                <img :src="scope.row.aut_icon" alt="" style="width:100px;height:100px;">
                            </div>
                        </el-popover>
                    </template>
                </el-table-column>
                <el-table-column prop="identity_card_icon" label="">
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="right">
                            <img :src="scope.row.identity_card_icon" alt="" style="width:300px;height:400px;">
                            <div slot="reference" class="name-wrapper">
                                <img :src="scope.row.identity_card_icon" alt="" style="width:100px;height:100px;">
                            </div>
                        </el-popover>
                    </template>
                </el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="identity_card" label=""></el-table-column>
                <el-table-column prop="pay_account" label=""></el-table-column>
                <el-table-column prop="status" label="">
                    <template slot-scope="scope">
                        <div v-if="scope.row.status==1"></div>
                        <div v-else-if="scope.row.status==2"></div>
                        <div v-else-if="scope.row.status==3"></div>
                        <div v-else-if="scope.row.status==3"></div>
                    </template>
                </el-table-column>
                <el-table-column prop="fail_reason" label=""></el-table-column>
                <el-table-column prop="auth_time" label=""></el-table-column>
                <el-table-column prop="auth_user" label=""></el-table-column>
                <el-table-column label="" min-width="120">
                    <template slot-scope="scope">
                        <el-row v-if="scope.row.status==1">
                            <el-col :span="8">
                                <el-button size="mini" type="success" @click="pass(scope.$index, scope.row)"></el-button>
                            </el-col>
                            <el-col :span="8">
                                <el-button size="mini" type="danger" @click="refuseBtn(scope.$index, scope.row)"></el-button>
                            </el-col>
                            <el-col :span="8">
                                <el-button style="margin-right: 20px;" size="mini" type="info" @click="revamp(scope.$index, scope.row)"></el-button>
                            </el-col>
                        </el-row>
                        <el-row :gutter="20" v-else-if="scope.row.status==2">
                            <el-col :span="12">
                                <el-button size="mini" type="success" @click="clearDate(scope.$index, scope.row)"></el-button>
                            </el-col>
                            <el-col :span="12">
                                <el-button size="mini" type="info" @click="revamp(scope.$index, scope.row)"></el-button>
                            </el-col>
                        </el-row>
                        <div v-else-if="scope.row.status==3"></div>
                        <div v-else-if="scope.row.status==4"></div>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-dialog title="" :visible.sync="passDialogVisible" width="30%">
                <span></span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="passDialogVisible = false"> </el-button>
                    <el-button type="primary" @click="passDialogVisible = false, surePass()"> </el-button>
                </span>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="refuse.DialogVisible">
                <el-form :model="refuse">

                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input v-model="refuse.reason" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="refuse.DialogVisible = false, refuse.reason=''"> </el-button>
                    <el-button type="primary" @click="sureRefuse()"> </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="refuse.titleDialogVisible" width="30%">
                <span></span>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="refuse.titleDialogVisible = false"> </el-button>
                </span>
            </el-dialog>
            <!--  -->
            <el-dialog title="" style="width:800px; left:30%;" :visible.sync="info.dialogVisible">
                <el-form :model="info">
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input style="width: 200px;" v-model="info.name"  auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input style="width: 200px;" v-model="info.identity_card"  auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <el-input style="width: 200px;" v-model="info.pay_account"  auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <template slot-scope="scope">
                            <el-popover trigger="hover" placement="left">
                                <img :src="info.aut_icon" alt="" style="width:300px;height:300px;">
                                <div slot="reference" class="name-wrapper">
                                    <img :src="info.aut_icon" alt="" style="width:100px;height:100px;">
                                </div>
                            </el-popover>
                        </template>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth">
                        <template slot-scope="scope">
                            <el-popover trigger="hover" placement="left">
                                <img :src="info.identity_card_icon" alt="" style="width:300px;height:300px;">
                                <div slot="reference" class="name-wrapper">
                                    <img :src="info.identity_card_icon" alt="" style="width:100px;height:100px;">
                                </div>
                            </el-popover>
                        </template>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="info.dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="sureRevamp()"></el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog title="" :visible.sync="clear.dialogVisible" width="30%">
                <span></span>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="clear.dialogVisible = false"> </el-button>
                    <el-button type="primary" @click="sureClear()"> </el-button>
                </span>
            </el-dialog>
        </template>
    </section>
</template>

<script>
import { allget } from "../../../api/api";
import store from "../../../vuex/store";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 100 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            listData: [
                {
                    uid: "11181",
                    aut_icon:
                        "http://img.dianliaoapp.com/DEBUG/11181/picwall/1515464456345.png",
                    status: "1",
                    req_time: "2018-01-09 10:20:56",
                    identity_card_icon: "http://img.dianliaoapp.com/DEBUG/11181/picwall/1515464456345.png",
                    identity_card: "456",
                    name: "cyz",
                    pay_account: "32145678",
                    surplus_num: "0",
                    type: "0",
                    fail_reason: null,
                    auth_time: null,
                    auth_user: null,
                    nickname: "",
                    sex: "1"
                },
                {
                    uid: "11181",
                    aut_icon:
                        "http://img.dianliaoapp.com/DEBUG/11181/picwall/1515464456345.png",
                    status: "2",
                    req_time: "2018-01-09 10:20:56",
                    identity_card_icon: null,
                    identity_card: null,
                    name: null,
                    pay_account: null,
                    surplus_num: "0",
                    type: "0",
                    fail_reason: null,
                    auth_time: null,
                    auth_user: null,
                    nickname: "",
                    sex: "1"
                },
                {
                    uid: "11181",
                    aut_icon:
                        "http://img.dianliaoapp.com/DEBUG/11181/picwall/1515464456345.png",
                    status: "3",
                    req_time: "2018-01-09 10:20:56",
                    identity_card_icon: null,
                    identity_card: null,
                    name: null,
                    pay_account: null,
                    surplus_num: "0",
                    type: "0",
                    fail_reason: null,
                    auth_time: null,
                    auth_user: null,
                    nickname: "",
                    sex: "1"
                },
                {
                    uid: "11181",
                    aut_icon:
                        "http://img.dianliaoapp.com/DEBUG/11181/picwall/1515464456345.png",
                    status: "4",
                    req_time: "2018-01-09 10:20:56",
                    identity_card_icon: null,
                    identity_card: null,
                    name: null,
                    pay_account: null,
                    surplus_num: "0",
                    type: "0",
                    fail_reason: null,
                    auth_time: null,
                    auth_user: null,
                    nickname: "",
                    sex: "1"
                }
            ],
            totalpage: null,
            formLabelWidth: "120px",
            listLoading: false,
            pay_account: null,
            identity_card: null,
            status: null,
            find: null,
            operate_user: null,
            passDialogVisible: false,
            passUid: null, //pass UID
            refuse: {
                DialogVisible: false,
                titleDialogVisible: false,
                reason: null,
                uid: null
            },
            info: {
                dialogVisible: false,
                name: null,
                identity_card: null,
                pay_account: null,
                aut_icon: null,
                identity_card_icon: null,
            },
            clear: {
                dialogVisible: false,
                uid: null,
            }
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
            let url = "/NewUser/getRealNameRecord";
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                find: this.find,
                pay_account: this.pay_account,
                identity_card: this.identity_card,
                status: this.status
            };
            allget(param, url)
                .then(res => {
                    // this.listData = res.data.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        // 
        judegSex(row, column) {
            return row.sex == 1 ? "" : "";
        },
        clearInput() {
            this.getData();
        },
        // 
        pass(index, row) {
            this.passDialogVisible = true;
            this.passUid = row.uid;
        },
        surePass() {
            let url = "/NewUser/passRealName";
            let param = {
                uid: this.passUid,
                auth_user: this.operate_user
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                        _this.getData();
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        },
        // 
        refuseBtn(index, row) {
            this.refuse.DialogVisible = true;
            this.refuse.uid = row.uid;
        },
        sureRefuse() {
            let url = "/NewUser/failRealName";
            let param = {
                uid: this.refuse.uid,
                auth_user: this.operate_user,
                fail_reason: this.refuse.reason
            };
            if (param.fail_reason == null || param.fail_reason == "") {
                this.refuse.titleDialogVisible = true;
                return;
            }
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                        _this.refuse.reason = "";
                        _this.getData();
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        },
        // 
        revamp(index, row) {
            this.info.dialogVisible = true;
            this.info.name = row.name;
            this.info.identity_card = row.identity_card;
            this.info.pay_account = row.pay_account;
            this.info.aut_icon = row.aut_icon;
            this.info.identity_card_icon = row.identity_card_icon;
        },
        sureRevamp() {
            var _this = this;
            let url = "/NewUser/editRealName";
            let param = {
                uid: this.info.uid,
                identity_card_icon: this.info.identity_card_icon,
                identity_card: this.info.identity_card,
                name: this.info.name,
                pay_account: this.info.pay_account,
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                        _this.info.dialogVisible = false;
                        _this.getData();
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        },
        // 
        clearDate(index, row) {
            this.clear.dialogVisible = true;
            this.clear.uid = row.uid;
        },
        sureClear() {
            var _this = this;
            let url = "/NewUser/clearRealName";
            let param = {
                uid: this.clear.uid,
                auth_user: this.operate_user,
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(_this, res.data.msg);
                        _this.info.dialogVisible = false;
                        _this.getData();
                    } else {
                        baseConfig.errorTipMsg(_this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(_this, err.data.msg);
                });
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = searchHeight;
        _this.getData();
        _this.operate_user = store.state.user.name;
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



