<template>
	<!-- ->tabletable -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-tabs v-model="activeName2" type="border-card" @tab-click="handleClick">
			<el-tab-pane label="" name="first" :style="{ height: tabHeight+ 'px' }">
				<el-form :label-position="labelPosition" class="demo-ruleForm" label-width="120px" :model="formLabelAlign" style="padding-left: 30px;">
					<el-form-item label="" style="padding-top: 30px;">
						<el-input v-model="formLabelAlign.name"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="formLabelAlign.icon"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input type="textarea" v-model="formLabelAlign.desc"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-select v-model="formLabelAlign.type" placeholder="">
							<el-option label="" value="0"></el-option>
							<el-option label="" value="1"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="">
						<el-select v-model="formLabelAlign.reward_type">
							<el-option label="" value="0"></el-option>
							<el-option label="" value="1"></el-option>
							<el-option label="" value="2"></el-option>
							<el-option label="" value="3"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="formLabelAlign.reward"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-select v-model="choice_url" :change="choiceUrlValue(choice_url)">
							<el-option label="" value="0"></el-option>
							<el-option label="H5" value="1"></el-option>
							<el-option label="" value="2"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="H5" v-if="formLabelAlign.jump_h5_show">
						<el-input v-model="formLabelAlign.jump_url"></el-input>
					</el-form-item>
					<el-form-item label="IOS" v-if="formLabelAlign.jump_app_show">
						<el-select v-model="formLabelAlign.ios_link">
							<el-option label="" value="VOIHomeVC"></el-option>
							<el-option label="" value="VOIFindUserVC"></el-option>
							<el-option label="" value="VOIEditUserInfoVC"></el-option>
							<el-option label="" value="VOIRushChatVC"></el-option>
							<el-option label="" value="VOIBegChatVC"></el-option>
							<el-option label="" value="VOIMineVC"></el-option>
							<el-option label="" value="VOIMyRewardVC"></el-option>
							<el-option label="" value="VOIShareView"></el-option>
							<el-option label="" value="VOIVipCenterVC"></el-option>
							<el-option label="" value="VOIConfigChargeVC"></el-option>
							<el-option label="-" value="VOIMarkVC"></el-option>
							<el-option label="" value="VOIFamilyListVC"></el-option>
							<el-option label="-" value="VOIRankingVC_Wealth"></el-option>
							<el-option label="-" value="VOIRankingVC_Charm"></el-option>
							<el-option label="" value="VOIRadarVC_OneKeyChat"></el-option>
							<el-option label="" value="VOIRadarVC_EavesdropLearnChat"></el-option>
							<el-option label="" value="VOIChatCoinVC"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" v-if="formLabelAlign.jump_app_show">
						<el-select v-model="formLabelAlign.android_link">
							<el-option label="" value="FirstMainActivity"></el-option>
							<el-option label="" value="SearchUserActivity"></el-option>
							<el-option label="" value="ModifyUserInfoActivity"></el-option>
							<el-option label="" value="GrabOrderFragment"></el-option>
							<el-option label="" value="SendOrderFragment"></el-option>
							<el-option label="" value="FirstMyFragment"></el-option>
							<el-option label="" value="MyAwardActivity"></el-option>
							<el-option label="" value="ShareDialog"></el-option>
							<el-option label="" value="VipCenterActivateActivity"></el-option>
							<el-option label="" value="CallingPriceActivity"></el-option>
							<el-option label="-" value="SelectTopChannelActivity"></el-option>
							<el-option label="" value="FamilyLobbyActivity"></el-option>
							<el-option label="-" value="WealthRankFragment"></el-option>
							<el-option label="-" value="CharmRankFragment"></el-option>
							<el-option label="" value="ConversationMatchActivity"></el-option>
							<el-option label="" value="EavesdropMatchActivity"></el-option>
							<el-option label="" value="TaskActivity"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="">
						<el-input v-model="formLabelAlign.sort"></el-input>
					</el-form-item>
				</el-form>
				<el-button type="primary" style="margin-left: 50px; margin-top: 30px;" @click="sendToTask()"></el-button>
			</el-tab-pane>
			<el-tab-pane label="" name="second" :style="{ height:  tabSearchHeight+'px' }">
				<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
					<el-form :inline="true" :model="formOne">
						<el-form-item>
							<span></span>
							<el-select v-model="formOne.status">
								<el-option label="" value="1"></el-option>
								<el-option label="" value="0"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="getTableData"></el-button>
						</el-form-item>
					</el-form>
				</el-col>
				<!---->
				<template>
					<el-table ref="tableHeightAddTab" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
						<el-table-column type="index" width="50" ></el-table-column>
						<el-table-column prop="id" label="ID" width="50"></el-table-column>
						<el-table-column prop="name" label="" width="100"></el-table-column>
						<el-table-column label="" width="100">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="scope.row.type=='0'" :type="scope.row.type"></p>
									<p v-if="scope.row.type=='1'" :type="scope.row.type"></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="desc" label="" width="100"></el-table-column>
						<el-table-column label="" width="150">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<img :src="scope.row.icon" alt="" style="width:100px;height:100px;">
								</div>
							</template>
						</el-table-column>
						<el-table-column label="" width="80">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="scope.row.reward_type=='0'" :type="scope.row.reward_type"></p>
									<p v-if="scope.row.reward_type=='1'" :type="scope.row.reward_type"></p>
									<p v-if="scope.row.reward_type=='2'" :type="scope.row.reward_type"></p>
									<p v-if="scope.row.reward_type=='3'" :type="scope.row.reward_type"></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="reward" label="" width="80"></el-table-column>
						<el-table-column label="" width="100">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="(scope.row.jump_url==null&&scope.row.ios_link==null&&scope.row.android_link==null)||(scope.row.jump_url==''&&scope.row.ios_link==''&&scope.row.android_link=='')"></p>
									<p v-else-if="scope.row.jump_url!=null&&scope.row.jump_url!=''">H5</p>
									<p v-else-if="scope.row.ios_link!=null"></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column label="" width="100">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="(scope.row.jump_url==null&&scope.row.ios_link==null&&scope.row.android_link==null)||(scope.row.jump_url==''&&scope.row.ios_link==''&&scope.row.android_link=='')"></p>
									<p v-else-if="scope.row.jump_url!=null&&scope.row.jump_url!=''">{{scope.row.jump_url}}</p>
									<p v-else-if="scope.row.android_link=='FirstMainActivity'" androidlink="FirstMainActivity" ioslink="VOIHomeVC"></p>
									<p v-else-if="scope.row.android_link=='SearchUserActivity'" androidlink="SearchUserActivity" ioslink="VOIFindUserVC"></p>
									<p v-else-if="scope.row.android_link=='ModifyUserInfoActivity'" androidlink="ModifyUserInfoActivity" ioslink="VOIEditUserInfoVC"></p>
									<p v-else-if="scope.row.android_link=='GrabOrderFragment'" androidlink="GrabOrderFragment" ioslink="VOIRushChatVC"></p>
									<p v-else-if="scope.row.android_link=='SendOrderFragment'" androidlink="SendOrderFragment" ioslink="VOIBegChatVC"></p>
									<p v-else-if="scope.row.android_link=='FirstMyFragment'" androidlink="FirstMyFragment" ioslink="VOIMineVC"></p>
									<p v-else-if="scope.row.android_link=='MyAwardActivity'" androidlink="MyAwardActivity" ioslink="VOIMyRewardVC"></p>
									<p v-else-if="scope.row.android_link=='ShareDialog'" androidlink="ShareDialog" ioslink="VOIShareView"></p>
									<p v-else-if="scope.row.android_link=='VipCenterActivateActivity'" androidlink="VipCenterActivateActivity" ioslink="VOIVipCenterVC"></p>
									<p v-else-if="scope.row.android_link=='CallingPriceActivity'" androidlink="CallingPriceActivity" ioslink="VOIConfigChargeVC"></p>
									<p v-else-if="scope.row.android_link=='SelectTopChannelActivity'" androidlink="SelectTopChannelActivity" ioslink="VOIMarkVC">-</p>
									<p v-else-if="scope.row.android_link=='FamilyLobbyActivity'" androidlink="FamilyLobbyActivity" ioslink="VOIFamilyListVC"></p>
									<p v-else-if="scope.row.android_link=='WealthRankFragment'" androidlink="WealthRankFragment" ioslink="VOIRankingVC_Wealth">-</p>
									<p v-else-if="scope.row.android_link=='CharmRankFragment'" androidlink="CharmRankFragment" ioslink="VOIRankingVC_Charm">-</p>
									<p v-else-if="scope.row.android_link=='ConversationMatchActivity'" androidlink="ConversationMatchActivity" ioslink="VOIRadarVC_OneKeyChat"></p>
									<p v-else-if="scope.row.android_link=='EavesdropMatchActivity'" androidlink="EavesdropMatchActivity" ioslink="VOIRadarVC_EavesdropLearnChat"></p>
									<p v-else-if="scope.row.android_link=='TaskActivity'" androidlink="TaskActivity" ioslink="VOIChatCoinVC"></p>
								</div>
							</template>
						</el-table-column>
						<el-table-column prop="sort" label="" width="100" sortable ></el-table-column>	
						<el-table-column label="" width="80">
							<template slot-scope="scope">
								<div slot="reference" class="name-wrapper">
									<p v-if="scope.row.status=='0'" :type="scope.row.status"></p>
									<p v-if="scope.row.status=='1'" :type="scope.row.status"></p>
								</div>
							</template>
						</el-table-column>					
						<el-table-column label="" min-width="100">
							<template slot-scope="scope">
								<el-button type="primary" @click.native.prevent="changeOneUserData(scope.$index, tabData)" size="small"></el-button>								
								<el-button v-if="scope.row.status=='0'" type="primary" @click.native.prevent="grounding(scope.$index, tabData)" size="small"></el-button>
								<el-button v-if="scope.row.status=='1'" plain @click.native.prevent="undercarriage(scope.$index, tabData)" size="small"></el-button>
							</template>
						</el-table-column>
					</el-table>
					<!---->
					<el-col :span="24" class="toolbar">
						<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
					</el-col>
				</template>
			</el-tab-pane>
		</el-tabs>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tabHeight: null, // tab
			tableHeight: null, // table
			tabSearchHeight: null, //tab-plane
			// 
			formOne: {
				status: '1', //form
			},
			formTwo: {
				uid: '', // inputvalueuid
			},
			labelPosition: 'left', // 
			// form
			formLabelAlign: {
				id: '',
				name: '',
				icon: '',
				type: '',
				android_link: '',
				ios_link: '',
				desc: '',
				reward_type: '',
				reward: '',
				jump_url: '',
				jump_h5_show: false, //H5
				jump_app_show: false, //
				sort: '',
			},
			choice_url: '', //form
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			activeName2: 'second', // tab(firstsecond)
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		searchCondition() {
			var _this = this;
			var obj = {};
			obj.status = _this.formOne.status; // 
			for(var key in obj) { // 
				if(obj[key]=='') {
					baseConfig.warningTipMsg(_this, '');
					return null;
				}
			}
			return obj; // return
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Marquee/getRevenueFloatingWindowData';
			var params = _this.searchCondition();
			if(params==null) { // null
				_this.listLoading = false; // ,
			} else {
				allget(params, url).then(res => { // get(params, url)
					// 
					_this.listLoading = false;
					if(res.data.ret) {
						_this.totalpage = res.data.data.length; // 
						_this.tabData = res.data.data;
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
					}
				}).catch(function(error){
					console.log(error);
				})
			}
		},
		// 
		changeOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			_this.activeName2 = 'first'; // 
			_this.formLabelAlign = rows[index]; // 
			_this.formLabelAlign = {
				id: rows[index].id,
				name: rows[index].name,
				icon: rows[index].icon,
				type: rows[index].type,
				android_link: rows[index].android_link,
				ios_link: rows[index].ios_link,
				desc: rows[index].desc,
				reward_type: rows[index].reward_type,
				reward: rows[index].reward,
				jump_url: rows[index].jump_url,
				jump_h5_show: false, //H5
				jump_app_show: false, //
				sort: rows[index].sort,
			};
			// 
			if((_this.formLabelAlign.jump_url==null&&_this.formLabelAlign.ios_link==null&&_this.formLabelAlign.android_link==null)||(_this.formLabelAlign.jump_url==''&&_this.formLabelAlign.ios_link==''&&_this.formLabelAlign.android_link=='')) {
				_this.choice_url = '0'; // 
			} else if(_this.formLabelAlign.jump_url!=null&&_this.formLabelAlign.jump_url!='') {
				_this.choice_url = '1'; // h5
			} else {
				_this.choice_url = '2'; // 
			}
		},
		// 
		grounding(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			var url = 'Marquee/upDownRevenueFloatingWindowData';
			var params = {
				id: id,
				status: 1, //
			};
			allget(params, url).then(res => { // get(params, url)
				// 
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, '');
					_this.tabData[index].status = 1;
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
				}
			}).catch(function(error){
				console.log(error);
			})
		},
		// 
		undercarriage(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			var url = 'Marquee/upDownRevenueFloatingWindowData';
			var params = {
				id: id,
				status: 0, //
			};
			allget(params, url).then(res => { // get(params, url)
				// 
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, '');
					_this.tabData[index].status = 0;					
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
				}
			}).catch(function(error){
				console.log(error);
			})
		},
		// tabtabid...
		handleClick(tab, event) {
			var _this = this;
			if(tab.label=='') {
				_this.choice_url = '';
				_this.formLabelAlign = {
					id: '',
					name: '',
					icon: '',
					type: '',
					android_link: '',
					ios_link: '',
					desc: '',
					reward_type: '',
					reward: '',
					jump_url: '',
					jump_h5_show: false, //H5
					jump_app_show: false, //
					sort: '',
				};
			} 
		},
		// selectchoiceUrlValue
		choiceUrlValue(value) {
			var _this = this;
			if(value=='0') { // 0->1->H52->
				_this.formLabelAlign.android_link = '';
				_this.formLabelAlign.ios_link = '';
				_this.formLabelAlign.jump_url = '';
				_this.formLabelAlign.jump_h5_show = false;
				_this.formLabelAlign.jump_app_show = false;
			} else if(value=='1') {
				_this.formLabelAlign.android_link = '';
				_this.formLabelAlign.ios_link = '';
				_this.formLabelAlign.jump_h5_show = true;
				_this.formLabelAlign.jump_app_show = false;
			} else if(value=='2') {
				_this.formLabelAlign.jump_url = '';
				_this.formLabelAlign.jump_h5_show = false;
				_this.formLabelAlign.jump_app_show = true;				
			} else {
				//console.log(''); //
			}
		},
		// 
		sendToTask() {
			var _this = this;
			var params = baseConfig.deepCopy(_this.formLabelAlign);
			var url = '';
			delete params.jump_h5_show;
			delete params.jump_app_show;
			if(_this.formLabelAlign.id=='') { // 
				delete params.id;
				url = '/Marquee/addRevenueFloatingWindowData';				
			} else { // 
				url = '/Marquee/editRevenueFloatingWindowData';
			}
			_this.sendGoServer(url, params);
		},
		// 
		sendGoServer(url, params) {
			var _this = this;
			allget(params, url).then(res => { // get(params, url)
				// 
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, '');
					_this.getTableData();
					_this.activeName2 = 'second';
				} else {
					baseConfig.errorTipMsg(_this, res.data.msg); // ret==0
				}
			}).catch(function(error){
				console.log(error);
			})
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tabHeight = tabHeight;
			_this.tableHeight = tabSearchPageHeight;
			_this.tabSearchHeight = tabSearchHeight;
			_this.getTableData();
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 270px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
.demo-ruleForm{
	width: 500px;
}
.el-tab-pane{
	height: 800px;
}
</style>



