<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!-- / -->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <div class="block">
                        <span class="registerTime"></span>
                        <el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
                        </el-date-picker>
                    </div>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="searchType" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                        <el-option label="" value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item class="search-span">
                    <el-button id="searchBtn" type="primary" @click="getData(1)"></el-button>
                </el-form-item>
                <el-form-item style="margin-left: 200px;">
                    <el-button id="handBinding" type="primary" @click="form.dialogFormVisible = true "></el-button>
                    <el-button id="handBinding" type="primary" @click="logout.dialogVisible = true "></el-button>
                    <el-button id="handBinding" type="primary" @click="register.dialogVisible = true "></el-button>
                </el-form-item>

            </el-form>
        </el-col>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="operate_time" label=""></el-table-column>
                <el-table-column prop="type" :formatter="agentCode" label=""></el-table-column>
                <el-table-column prop="uid" label="UID"></el-table-column>
                <el-table-column prop="phone" label=""></el-table-column>
                <el-table-column prop="sex" :formatter="agentSex" label=""></el-table-column>
                <el-table-column prop="code" label=""></el-table-column>
                <el-table-column prop="before_phone" label=""></el-table-column>
                <el-table-column prop="operate_user" label=""></el-table-column>

            </el-table>
        </template>
        <el-dialog title="" :visible.sync="form.dialogFormVisible">
            <el-form :model="form">
                <p class="bindingTitle" style="color: red;"><br>1<br> 2
                </p>
                <el-form-item label="UID" :label-width="formLabelWidth">
                    <el-input v-model="form.uid" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="form.phone" placeholder="" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="form.dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="changeAccount()"> </el-button>
            </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="logout.dialogVisible">
            <el-form :model="logout">
                <p class="bindingTitle" style="color: red;"><br>1<br> 2
                </p>
                <el-form-item label="UID" :label-width="formLabelWidth">
                    <el-input v-model="logout.uid" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="logout.dialogVisible = false"> </el-button>
                <el-button type="primary" @click="logout.dialogVisible = false,logoutAccount()"> </el-button>
            </div>
        </el-dialog>
        <el-dialog title="" :visible.sync="register.dialogVisible">
            <el-form :model="register">
                <p class="bindingTitle" style="color: red;"><br>1/UID<br> 2/
                </p>
                <el-form-item label="UID" :label-width="formLabelWidth">
                    <el-input v-model="register.uid" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="register.sex" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="register.phone" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="register.is_code" placeholder="">
                        <el-option label="" value="0"></el-option>
                        <el-option label="" value="1"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item v-if="register.is_code==1" label="" :label-width="formLabelWidth">
                    <el-input v-model="register.code" placeholder="4" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="register.dialogVisible = false"> </el-button>
                <el-button type="primary" @click="registerAccount()"> </el-button>
            </div>
        </el-dialog>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from "axios";
import store from "../../../vuex/store.js";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 1 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            page: 0,
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            form: {
                dialogFormVisible: false,
                uid: null,
                phone: null
            },
            logout: {
                dialogVisible: false,
                uid: null,
                phone: null
            },
            operate_user: null,
            register: {
                dialogVisible: false,
                uid: null,
                sex: null,
                phone: null,
                is_code: null,
                code: null
            },
            page: 0,
            totalpage: null,
            searchType: null,
        };
    },
    methods: {
        //
        handleCurrentChange(val) {
            //0   -1
            this.page = val - 1;
            this.getData();
        },
        // 
        getData(type) {
            var _this = this;
            let url = "/NewUser/getAccountChangeRecord";
            if (this.formOne.startDate == null) {
                baseConfig.warningTipMsg(this, "");
                return;
            }
            if (type) {
                this.page = 0;
            }
            let param = {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                page: this.page,
                type: this.searchType
            };
            // 
            allget(param, url)
                .then(res => {
                    this.listData = res.data.data;
                })
                .catch(err => {
                    console.log(err);
                });
        },
        agentCode(row, column) {
            if (row.type == 1) {
                return "";
            } else if (row.type == 2) {
                return "";
            } else if (row.type == 3) {
                return "";
            }
        },
        agentSex(row, colum) {
            if (row.sex == 1) {
                return "";
            } else if (row.sex == 2) {
                return "";
            } else {
                return "";
            }
        },

        // 
        changeAccount() {
            var _this = this;
            let url = "/NewUser/uidChangePhone";
            let param = {
                uid: this.form.uid,
                phone: this.form.phone,
                operate_user: this.operate_user
            };
            if(!(/^1(3|4|5|7|8)\d{9}$/.test(param.phone))){ 
                baseConfig.warningTipMsg(this, '');
                return false; 
            } 
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                            form.dialogFormVisible = false;
                        baseConfig.successTipMsg(this, res.data.msg);
                        this.form.uid = "";
                        this.form.phone = "";
                        _this.getData();
                    } else {
                        baseConfig.warningTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        // 
        logoutAccount() {
            var _this = this;
            let url = "/NewUser/uidChangePhone";
            let param = {
                uid: this.logout.uid,
                phone: this.logout.uid,
                operate_user: this.operate_user
            };
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(this, res.data.msg);
                        this.logout.uid = "";
                        _this.getData();
                    } else {
                        baseConfig.warningTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        // 
        registerAccount() {
            var _this = this;
            let url = "/NewUser/registerGoodNumber";
            let param = {
                uid: this.register.uid,
                phone: this.register.phone,
                sex: this.register.sex,
                is_code: this.register.is_code,
                code: this.register.code,
                operate_user: this.operate_user
            };
            console.log(param);
            if (param.uid == null || param.uid == "") {
                baseConfig.warningTipMsg(this, "uid");
                return;
            }
            if (param.sex == null || param.sex == "") {
                baseConfig.warningTipMsg(this, "");
                return;
            }
            if (param.phone == null || param.phone == "") {
                baseConfig.warningTipMsg(this, "");
                return;
            }
            if(!(/^1(3|4|5|7|8)\d{9}$/.test(param.phone))){ 
                baseConfig.warningTipMsg(this, '');
                return; 
            }
            if (param.is_code == null || param.is_code == "") {
                baseConfig.warningTipMsg(this, "");
                return;
            }
            if (param.is_code == 1) {
                console.log(/^([0-9]{4})$/.test(param.code));
                if (!/^([0-9]{4})$/.test(param.code)) {
                    baseConfig.warningTipMsg(this, "4!");
                }
            } else {
                delete param.code;
            }

            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        baseConfig.successTipMsg(this, res.data.msg);
                        this.register.uid = "";
                        this.register.phone = "";
                        this.register.sex = "";
                        this.register.is_code = "";
                        this.register.code = "";
                        _this.getData();
                        _this.register.dialogVisible = false;
                    } else {
                        baseConfig.warningTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        }
    },
    mounted() {
        var _this = this;
        this.getData();
        _this.tableHeight = searchPageHeight;
        this.operate_user = store.state.user.name;
    }
};
</script>

<style lang="css" scoped>
#searchBtn {
    margin-right: 50px;
}
.bindingTitle {
    margin-left: 130px;
}
</style>



