<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="time" label=""></el-table-column>
				<el-table-column prop="totalrandcallaa" label="AA"></el-table-column>
				<el-table-column prop="totalnumaa" label="AA"></el-table-column>
				<el-table-column prop="totalrandtimeaa" label="AA">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.totalrandtimeaa)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="avgtimeaa" label="AA">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.avgtimeaa)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="totalrandcallab" label="AB"></el-table-column>
				<el-table-column prop="totalrandtimeab" label="AB">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.totalrandtimeab)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="totalnumab" label="AB"></el-table-column>
				<el-table-column prop="avgtimeab" label="AB">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.avgtimeab)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="atimes" label="A"></el-table-column>
				<el-table-column prop="woman_atimes" label="A"></el-table-column>
				<el-table-column prop="man_atimes" label="A"></el-table-column>
				<el-table-column prop="new_atimes" label="A"></el-table-column>
				<el-table-column prop="old_atimes" label="A"></el-table-column>
				<el-table-column prop="anums" label="A"></el-table-column>
				<el-table-column prop="snums" label=""></el-table-column>
				<el-table-column prop="rate" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{(scope.row.rate*100).toFixed(2)+"%"}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="gift" label=""></el-table-column>
				<el-table-column prop="one_num" label="0~1"></el-table-column>
				<el-table-column prop="three_num" label="1~3"></el-table-column>
				<el-table-column prop="five_num" label="3~5"></el-table-column>
				<el-table-column prop="ten_num" label="5~10"></el-table-column>
				<el-table-column prop="thirty_num" label="10~30"></el-table-column>
				<el-table-column prop="sixty_num" label="30~60"></el-table-column>
				<el-table-column prop="sixtymore_num" label="60"></el-table-column>
				<el-table-column prop="totalcall" label=""></el-table-column>
				<el-table-column prop="totaltime" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.totaltime)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="listen_valid_times" label=""></el-table-column>
				<el-table-column prop="listen_valid_long" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.listen_valid_long)}}</P>
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="avgtime" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <P>{{timeTransform(scope.row.avgtime)}}</P>
                        </div>
                    </template>
                </el-table-column>
			</el-table>
		</template>
	</section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from 'axios';

export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
				startDate: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()], // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
			let param = _this.condition();
            let url = "/Record/getRandCallData";
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                        this.listData = res.data.data;
                    } else {
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(this, "error");
                });
		},
		// 
		condition() {
            return {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
            };
        },
        // 
        timeTransform(oldValue){
            return baseConfig.changeTime(oldValue);
        }
    },
    filters: {
        numberTransform(value){
            return (value*100).toFixed(2);
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = pageHeight;
        _this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



