<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
                <el-form-item>
					<el-button type="primary" @click="dialogAddFormVisible=true;"></el-button>
					<el-button type="primary" @click="refreshPage($event)"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!---->
		<template>
			<el-table ref="tableHeight" :data="onePageTabData" border fit highlight-current-row v-loading="listLoading" style="width: 100%;" :height="tableHeight">
				<el-table-column prop="id" label="" width="100" sortable ></el-table-column>
				<el-table-column prop="annotation" label="" width="500"></el-table-column>
				<el-table-column label="" min-width="200">
					<template slot-scope="scope">
						<el-button type="primary" @click.native.prevent="changeOneUserData(scope.$index, tabData)" size="small"></el-button>
						<el-button type="primary" @click.native.prevent="deleteOneUserData(scope.$index, tabData)" size="small"></el-button>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
			</el-col>
			<!-- --dialog -->
			<el-dialog title="" :visible.sync="dialogFormVisible">
				<el-form :model="formTwo">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formTwo.annotation" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="editorSure(0)"> </el-button>
					<el-button type="primary" @click="editorSure(1)"> </el-button>
				</div>
			</el-dialog>
			<!-- --dialog -->
			<el-dialog title="" :visible.sync="dialogAddFormVisible">
				<el-form :model="formThree">
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formThree.id" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" :label-width="formLabelWidth">
						<el-input v-model="formThree.annotation" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click="addSure(0)"> </el-button>
					<el-button type="primary" @click="addSure(1)"> </el-button>
				</div>
			</el-dialog>
		</template>
	</section>
</template>

<script>
/* js */
import Event from './../../../public_js/event.js';
import { allget } from '../../../api/api';
import store from '../../../vuex/store';
import axios from 'axios';
export default {
	data() {
		return {
			tableHeight: null, // table
			formTwo: {
				id: '', // id
				annotation: '', // inputvalue
				rows: null, // table
				index: null, // table
			},
			formThree: { // 
				id: '', // 
				annotation: '', // 
			},
			excelBoxActivity: false, // Excel
			listLoading: false, //
			tabData: [], //
			totalpage: null, // 
			page: 1, //1
			star: '0', //
			end: '20', //
			dialogFormVisible: false, // dialog
			dialogAddFormVisible: false, // dialog
			formLabelWidth: '120px', // dialog
		};
	},
	computed:{
		// 20
		onePageTabData() {
			var _this = this;
			return _this.tabData.slice(_this.star, _this.end);
		},
	},
	methods: {
		// onePageTabData
		handleCurrentChange(val) {
			// val
			var _this = this;
			_this.page = val;
			_this.star = (_this.page-1)*20;
			_this.end = _this.star+20;
		},
		// 
		getTableData() {
			var _this = this ;
			_this.listLoading = true;
			var url = '/Channel/getChannel';
			// get(params, url)
			allget('', url).then(res => {
				// 
				_this.listLoading = false;
				if(res.data.ret) {
					// 
					_this.totalpage = res.data.data.length;
					_this.tabData = res.data.data;
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}

			}).catch(function(error){
				console.log(error);
			})
		},
		// 
		changeOneUserData(index, rows){
			var _this = this;
			index = index + (_this.page-1)*20; // index
			_this.formTwo.index = index;
			_this.formTwo.id = rows[index].id;
			_this.formTwo.annotation = rows[index].annotation;
			_this.dialogFormVisible = true; // dialog
		},
		// (val0->1->)
		editorSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.dialogFormVisible = false; // dialog
				console.log('');
			} else if(val==1) {
				// 
				console.log('');
				_this.listLoading = true;
				// 
				var url = '/Channel/modifyChannel';
				var params = {
					annotation: _this.formTwo.annotation,
					id: _this.formTwo.id,
				};
				var paramsTable = {
					id: _this.formTwo.id,
					annotation: _this.formTwo.annotation,
				};
				// 
				if(params.annotation!='' && params.id!=''){
					_this.dialogFormVisible = false; // dialog
					allget(params, url).then(res => {
						console.log(res);
						if(res.data.ret) {
							// 
							_this.listLoading = false;
							baseConfig.successTipMsg(_this, '');
							_this.getTableData();
						} else {
							baseConfig.errorTipMsg(_this, res.data.msg);
						}
					}).catch(function(error){
						console.log(error);
					})
				} else {
					_this.listLoading = false; 
					baseConfig.warningTipMsg(_this, '');
				}
			} 
		},
		// 
		addSure(val) {
			var _this = this;
			if(val==0) {
				// 
				_this.dialogFormVisible = false; // dialog
				console.log('');
			} else if(val==1) {
				// 
				console.log('');
				_this.listLoading = true;
				// 
				var url = '/Channel/addChannel';
				var params = {
					id: _this.formThree.id,
					annotation: _this.formThree.annotation,
				};
				// 
				if(params.annotation!=''&&params.id!=''){
					_this.dialogAddFormVisible = false; // dialog
					allget(params, url).then(res => {
						console.log(res);
						if(res.data.ret) {
							// 
							_this.listLoading = false;
							baseConfig.successTipMsg(_this, '');
							_this.getTableData();
						} else {
							baseConfig.errorTipMsg(_this, res.data.msg);
						}
					}).catch(function(error){
						console.log(error);
					})
				} else {
					_this.listLoading = false; 
					baseConfig.warningTipMsg(_this, '');
				}
			} 
		},
		// 
		deleteOneUserData(index, rows) {
			var _this = this;
			index = index + (_this.page-1)*20; // index
			var id = rows[index].id; // value	
			console.log(id);
			// 
			_this.listLoading = true;
			var url = '/Channel/deleteChannel';
			var params = {
				id: id,
			};
			// get
			allget(params, url).then(res => {
				// 
				_this.listLoading = false;
				if(res.data.ret) {
					baseConfig.successTipMsg(_this, ''); // 
					_this.getTableData();
				} else {
					// ret==0
					baseConfig.errorTipMsg(_this, res.data.msg);
				}

			}).catch(function(error) {
				console.log(error);
			})
		},
		// 
		refreshPage() {
			var _this = this;
			// 
			var strPath = location.href;
            var index = strPath.indexOf('#/');
            strPath = strPath.substring(index+1,strPath.length);
            // 
            _this.$router.replace({
                path: '/empty', 
                query: {from: strPath},
            });
		},
	},
	mounted() {
		var _this = this;
		this.$nextTick(function() {
			_this.tableHeight = searchPageHeight; // table
			_this.getTableData(); // table
		})
	}
};
</script>

<style lang="css" scoped>
/* css */
.excelBox{
	width: 500px; height: 190px; margin-left: -150px; background: #f1f7ff;
	position: absolute; left: 50%; top: 15%; z-index: 1000;
}
p{ margin: 0; }
.excelBox>p{
	width: 100%; height: 50px; line-height: 50px; font-weight: bold;
	background: #e3efff; text-align: center;
}
.excelBox .excelInput{
	width: 100%; height: 60px;
}
.excelBox .select{
	width: 100%; height: 80px;
}
.excelBox .excelInput p,
.excelBox .select p{
	width: 100%; height: 36px; text-indent: 20px; line-height: 36px;
}
.excelBox .excelInput input{
    width: 300px; display: block; margin: 0 auto;
}
.excelBox .select>div{
	width: 300px; display: block; margin: 0 auto;
}
.btns{
    width: 100%; height: 50px;
}
.btns button{
    width: 80px; height: 40px; text-align: center; line-height: 40px;
    border: none; border-radius: 5px;
    background-color: #78B2FF; margin-top: 20px; color: #fff;
}
.btns button:nth-of-type(1){
    margin-left: 150px; cursor: pointer;
}
.btns button:nth-of-type(2){
    margin-left: 50px; cursor: pointer;
}
</style>



