<template>
    <!--  -->
    <!-- dom -->
	<section>
        <!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
                <el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;"  :height="tableHeight">
				<el-table-column prop="date" label=""></el-table-column>
				<el-table-column prop="new_anchor"  label=""></el-table-column>
				<el-table-column prop="total_anchor" label=""></el-table-column>
				<el-table-column prop="active_anchor" label="" ></el-table-column>
				<el-table-column prop="day_chat_ticket" label=""></el-table-column>
				<el-table-column prop="day_money" label="()" sortable></el-table-column>
				<el-table-column prop="call_chat_ticket" label=""></el-table-column>
				<el-table-column prop="gift_chat_ticket" label=""></el-table-column>
				<el-table-column prop="eavesdrop_chat_ticket" label=""></el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" :page-size="20" :total=1000  style="float:right; ">
				</el-pagination>
			</el-col>
		</template>
    </section>
</template>

<script>
	import { allget } from '../../../api/api';
    export default {
        data() {
            return {
				tableHeight: null, // table
                formOne: {
                    startDate: [new Date()-7*24*60*60*1000, new Date()], // ,180
				},
				listData: [],
				formLabelWidth: '120px',
				listLoading: false,
            }
        },
		methods: {
			// 
			getData() {
				var _this = this;
				let url = '/Anchor/getAnchorDayTotalData';
				let param ={
					date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
					date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
				}
				allget(param, url).then(res => {
					this.listData = res.data.data;
				}).catch(err => {
					console.log(err)
				})
			},
		},
		mounted() {
			var _this = this;
			_this.tableHeight = searchPageHeight;
			_this.getData();
		},
    }

</script>

<style lang="css" scoped>
    .search-span{
        float: right;
    }
    #searchBtn{
        margin-right: 50px;
    }
</style>



