<template>
    <!--  -->
    <!-- dom -->
	<section>
        <!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
				<el-form-item style="margin-left: 50px;">
					<span></span>
					<el-select style="width: 100px;" v-model="recordStatus">
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="recommend_time" label="" width="180"></el-table-column>
				<el-table-column prop="id"  label="" width="100"></el-table-column>
				<el-table-column label="" width="300">
					<template slot-scope="scope">
						<div slot="reference" class="name-wrapper">
							<audio controls="controls" :src="scope.row.voice_url"></audio>
						</div>
					</template>
				</el-table-column>
				<el-table-column prop="duration" label=""  min-width="50" ></el-table-column>
				<el-table-column prop="uid" label="ID" min-width="50" ></el-table-column>
				<el-table-column prop="content" label=""></el-table-column>
				<el-table-column prop="price" :formatter="price" label="" min-width="50" sortable></el-table-column>
				<el-table-column prop="listen" label="" width="50"></el-table-column>
				<el-table-column prop="praise" label="" width="50"></el-table-column>
				<el-table-column prop="relay" label="" width="50"></el-table-column>
				<el-table-column prop="award" label="" width="50"></el-table-column>
				<el-table-column prop="share" label="" width="50"></el-table-column>
				<el-table-column prop="complain" label="" width="50"></el-table-column>
				<el-table-column prop="recommend" label="" width="50"></el-table-column>
				<el-table-column label="" min-width="120">
					<template slot-scope="scope">
                        <el-col :span="12"><el-button size="small" type="primary" @click="recommendAgent(scope.$index, scope.row)"></el-button></el-col>
                        <el-col :span="12"><el-button size="small" type="danger" @click="cancleAgent(scope.$index, scope.row)"></el-button></el-col>
					</template>
				</el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
				</el-pagination>
			</el-col>
		</template>
    </section>
</template>

<script>
	import { allget } from '../../../api/api';
	import axios from 'axios';
    export default {
        data() {
            return {
				tableHeight: null, // table
                formOne: {
                    startDate: [new Date()-100*24*60*60*1000, new Date()], // ,180
				},
				value6: '',
				value10: 'text',
				recordStatus: '',
				page: 0,
				listData: [],
				totalpage:null,
				formLabelWidth: '120px',
				listLoading: false,
            }
        },
		methods: {
			//
			handleCurrentChange(val) {
				//0   -1
				this.page = val-1;
				this.getData();
			},
			// 
			getData() {
				var _this = this;
				let url = '/Voice/getHotVoiceList';
				let param ={
					page: this.page,
					date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
					date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
					is_up_list: this.recordStatus,
					price: this.recordStyle,
					uid: this.uid,
					id: this.audio_uid
				}
				allget(param, url).then(res => {
					this.listData = res.data.data;
				}).catch(err => {
					console.log(err)
				})
			},
			// 
			tipInfo() {
				var _this = this;
				baseConfig.warningTipMsg(_this, '');
			},
			// 
			price(row){
				return row.price > 0 ? "" : "";
			},
			
            // 
			recommendAgent(index, row) {
				var _this = this;
				var id = row.id;
				var url = '/Voice/recommendVoiceToHot';
				var params = {
					id: id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
            },
			// 
			cancleAgent(index, row) {
				var _this = this;
				var id = row.id;
				var url = '/Voice/cancelRecommendVoiceToHot';
				var params = {
					id: id,
				};
				_this.listLoading = true;				
				allget(params, url).then(res => {
					_this.listLoading = false;
					if(res.data.ret) {
						baseConfig.successTipMsg(_this, '');						
						_this.getData();
					} else {
						baseConfig.errorTipMsg(_this, res.data.msg);
					}
				}).catch(function(error){
					baseConfig.errorTipMsg(_this, error);
				})
			},
		},
		mounted() {
			var _this = this;
			_this.tableHeight = searchPageHeight;
			_this.getData();
		},
		updated() {
			var _this = this;
			//
			var audio = document.getElementsByTagName("audio");
			for(var i=0; i<audio.length; i++){
				audio[i].addEventListener("play", function() {
					for(var j=0; j<audio.length; j++) {
						if(audio[j]!=this) { //this
							audio[j].pause();
						}
					}
				});
			}
		}
    }

</script>

<style lang="css" scoped>
    .search-span{
        float: right;
    }
    #searchBtn{
        margin-right: 50px;
    }
</style>



