<!--  -->
<template lang="html">
    <el-row>
        <!--  -->
        <div class="left_active" ref="leftnav">
            <el-menu :default-active="indexPath" class="el-menu-vertical-demo " @open="handleOpen" @close="handleClose" theme="dark" router>
                <!--  -->
                <!-- hidden: true; -->                                
                <el-menu-item v-for="(item, indexs) in dataView" :key="indexs" :index="item.path" v-show="!item.hidden">
                    <i :class="item.iconCls"></i>
                    <span slot="title">{{item.name}}</span>
                </el-menu-item>
            </el-menu>
        </div>
        <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in">
                <router-view></router-view>
            </transition>
        </el-col>
    </el-row>
</template>

<script>
import store from '../../vuex/store';
export default {
    data() {
        return {
            indexPath: '', //  
        };
    },
    computed: {
        dataView() {
            // store
            let thatDdta = store.getters.addRouters;
            let data =  thatDdta.filter(data => {
                if(data.name==''){
                    return  data;
                }
            }) 
            //console.log(data[0].children[0].children);
            return data[0].children[0].children;
        }
    },
    methods: {
        handleOpen(key, keyPath) {
            //console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            //console.log(key, keyPath);
        }
    },
    mounted() {
        var _this = this;
        this.$nextTick(function() {
            _this.$refs.leftnav.style.height = leftNavHeight +'px';
            // 
            // 
            var strPath = location.href;
            var index = strPath.indexOf('#/');
            strPath = strPath.substring(index+1,strPath.length);
            // console.log(strPath);
            _this.indexPath = strPath;
		})
    },
}
</script>

<style lang="css">

</style>



