<template>
    <!--  -->
    <!-- dom -->
	<section>
        <!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
                <el-form-item style="margin-left: 100px;">
					<span></span>
					<el-select style="width: 150px;" v-model="searchType">
						<el-option label="" value=""></el-option>
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData(0)"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="operate_time" label=""></el-table-column>
				<el-table-column prop="uid"  label="UID"></el-table-column>
				<el-table-column prop="change_uid_list" label="uid"></el-table-column>
				<el-table-column prop="type" label="" :formatter="judgeType"></el-table-column>
				<el-table-column prop="operate_user" label=""></el-table-column>
			</el-table>
			<!--  -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="total,prev, pager, next,jumper" @current-change="handleCurrentChange" :page-size="20" :total=1000 :current-page="page+1" style="float:right; ">
				</el-pagination>
			</el-col>
		</template>
    </section>
</template>

<script>
	import { allget } from '../../../api/api';
	import axios from 'axios';
    export default {
        data() {
            return {
				tableHeight: null, // table
                formOne: {
                    startDate: [new Date()-1*24*60*60*1000, new Date()], // ,180
				},
				page: 0,
				listData: [],
				totalpage:null,
				formLabelWidth: '120px',
                listLoading: false,
                searchType: null,
            }
        },
		methods: {
			//
			handleCurrentChange(val) {
				//0   -1
				this.page = val-1;
				this.getData();
			},
			// 
			getData(type) {
				var _this = this;
				if(type==0){
					_this.page = 0;
				}
				let url = '/Agent/getAgentChangeRecord';
				let param ={
					page: this.page,
					date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                    date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                    type: this.searchType,
				}
				allget(param, url).then(res => {
					this.listData = res.data.data;
				}).catch(err => {
					console.log(err)
				})
            },
            judgeType(row){
                return row.type==0 ? "" : "";
            }
		},
		mounted() {
			var _this = this;
			_this.tableHeight = searchPageHeight;
			_this.getData();
		},
    }

</script>

<style lang="css" scoped>
    .search-span{
        float: right;
    }
    #searchBtn{
        margin-right: 50px;
    }
</style>



