<template>
    <!--  -->
    <!-- dom -->
    <section>
        <!--  -->
        <template>
            <el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
                <el-table-column prop="heart_time" label=""></el-table-column>
                <el-table-column prop="cp_uid" label="ID"></el-table-column>
                <el-table-column prop="cp_nickname" label=""></el-table-column>
                <el-table-column prop="uid" label="ID"></el-table-column>
                <el-table-column prop="nickname" label=""></el-table-column>
                <el-table-column prop="chat_gold" label=""></el-table-column>
            </el-table>
        </template>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="total,prev,pager,next,jumper" @current-change="handleCurrentChange" :page-size="20" :total="totalpage" style="float:right;"></el-pagination>
        </el-col>
    </section>
</template>

<script>
import { allget } from "../../api/api";
import axios from "axios";
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
                startDate: [new Date() - 100 * 24 * 60 * 60 * 1000, new Date()] // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
			page: 0,
            totalpage:1000,
        };
    },
    methods: {
        //
        handleCurrentChange(val) {
            //0   -1
            this.page = val-1;
            this.getData();
        },
        // 
        getData() {
            var _this = this;
            let url = "/NewActivity/getBothBlackLoverDataInfo";
            let param = {
                page: this.page
            };
            allget(param, url)
                .then(res => {
                    if(res.data.ret){
                        this.listData = res.data.data;
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
    },
    mounted() {
        this.tableHeight = pageHeight;
        this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



