<template>
	<!--  -->
	<!-- dom -->
	<section>
		<!-- / -->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item>
					<div class="block">
						<span class="registerTime"></span>
						<el-date-picker v-model="formOne.startDate" type="daterange" range-separator="  " start-placeholder="" end-placeholder="">
						</el-date-picker>
					</div>
				</el-form-item>
                <el-form-item style="margin-left: 50px;">
					<span></span>
					<el-select style="width: 100px;" v-model="callType">
						<el-option label="" value=""></el-option>
						<el-option label="AA" value="1"></el-option>
						<el-option label="AB" value="5"></el-option>
						<el-option label="" value="3"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
                <el-form-item>
					<span>UID//</span>
					<el-input style="width:100px;" placeholder="" v-model="uid" clearable>
                    </el-input>
				</el-form-item>
				<el-form-item class="search-span" style="float:right;">
					<el-button id="searchBtn" type="primary" @click="getData()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<template>
			<el-table :data="listData" border fit highlight-current-row style="width: 100%;" :height="tableHeight">
				<el-table-column prop="chat_id" label="id"></el-table-column>
				<el-table-column prop="start_time" label=""></el-table-column>
				<el-table-column prop="chat_type" label="">
                    <template slot-scope="scope">
                        <div slot="reference" class="name-wrapper">
                            <p v-if="scope.row.chat_type==1||scope.row.chat_type==5||scope.row.chat_type==8"></p>
                            <p v-else-if="scope.row.chat_type==2"></p>
                            <p v-else-if="scope.row.chat_type==3"></p>									
                            <p v-else-if="scope.row.chat_type==6"></p>									
                        </div>
                    </template>
                </el-table-column>
				<el-table-column prop="uid_req" label=""></el-table-column>
				<el-table-column prop="uid_res" label=""></el-table-column>
				<el-table-column prop="gift" label=""></el-table-column>
				<el-table-column prop="listen" label=""></el-table-column>
				<el-table-column prop="num" label=""></el-table-column>
				<el-table-column prop="priority_base" label="">
                    <template slot-scope="scope">
                        <div v-if="scope.row.priority_base>0" style="text-align: center;margin: 0 auto;">
                            <el-button type="primary" round size="mini" @click="sm(scope.$index, scope.row)"></el-button>
                            <el-button type="info" round size="mini"></el-button>
                        </div>
                        <div v-else-if="scope.row.priority_base<=0" style="text-align: center;margin: 0 auto;">
                            <el-button type="primary" round size="mini" @click="sm(scope.$index, scope.row)"></el-button>
                            <el-button type="primary" round size="mini" style="margin-top: 4px;" @click="fir(scope.$index, scope.row)"></el-button>
                        </div>
                    </template>
                </el-table-column>
			</el-table>
		</template>
	</section>
</template>

<script>
import { allget } from "../../../api/api";
import axios from 'axios';
export default {
    data() {
        return {
            tableHeight: null, // table
            formOne: {
				startDate: [new Date() - 2 * 24 * 60 * 60 * 1000, new Date()], // ,180
            },
            listData: [],
            formLabelWidth: "120px",
            listLoading: false,
            callType: null,
            uid: null,
        };
    },
    methods: {
        // 
        getData() {
            var _this = this;
			let param = _this.condition();
            let url = "/Record/getPublicCall";
            this.uid==null||this.uid==""?delete param.uid:param.uid=this.uid;
            this.callType==null||this.callType==""?delete param.callType:param.callType=this.callType;
            allget(param, url)
                .then(res => {
                    if (res.data.ret) {
                            for(var i = 0;i<res.data.data.length;i++){
                                res.data.data[i].avg = baseConfig.changeTime(res.data.data[i].listen_long/res.data.data[i].listen_success_times);
                            }
                        this.listData = res.data.data;
                    } else {
                        baseConfig.errorTipMsg(this, res.data.msg);
                    }
                })
                .catch(err => {
                    baseConfig.errorTipMsg(this, "error"); 
                });
            // url = 'https://manage.dianliaoapp.com/ydlManage/server/index.php/Record/getPublicCall?';
            // axios.get(url,param).then(res=>{
            //     if (res.data.ret) {
            //         for(var i = 0;i<res.data.data.length;i++){
            //             res.data.data[i].avg = baseConfig.changeTime(res.data.data[i].listen_long/res.data.data[i].listen_success_times);
            //         }
            //         this.listData = res.data.data;
            //     }else{
            //         baseConfig.errorTipMsg(this, res.data.msg);
            //     }
            // }).catch(err=>{
            //     baseConfig.errorTipMsg(this, "error"); 
            // })
		},
		// 
		condition() {
            return {
                date_s: baseConfig.changeDateTime(this.formOne.startDate[0], 0),
                date_e: baseConfig.changeDateTime(this.formOne.startDate[1], 0),
                chat_type: this.callType,
                find: this.uid,
            };
        },
        // 
        timeTransform(oldValue){
            return baseConfig.changeTime(oldValue);
        },
        // 
        sm(index, row){
            var _this = this;
            var url = "/Record/setSecret";
            var param = {
                chat_id: row.chat_id,
                id: row.id
            }
            allget(param, url).then(res => {
                if(res.data.ret) {
                    baseConfig.successTipMsg(_this, '');
                    _this.getData();
                }else{
                    baseConfig.errorTipMsg(_this, res.data.msg);
                }
            }).catch(err => {
                baseConfig.errorTipMsg(_this, error);
            })
        },
        // 
        fir(index, row){
            var _this = this;
            var url = '/Record/firstListen';
            var param = {
                id: row.id,
            }
            allget(param, url).then(res => {
                if(res.data.ret) {
                    baseConfig.successTipMsg(_this, '');
                    _this.getData();
                }else{
                    baseConfig.errorTipMsg(_this, res.data.msg);
                }
            }).catch(err => {
                baseConfig.errorTipMsg(_this, error);
            })
        }
    },
    mounted() {
        var _this = this;
        _this.tableHeight = pageHeight;
        _this.getData();
    }
};
</script>

<style lang="css" scoped>
.search-span {
    float: right;
}
#searchBtn {
    margin-right: 50px;
}
</style>



